import { useState, VFC } from 'react'
import { useEventInfo } from 'components/ProfileDrawer/providers/EventInfoProvider'
import { useDirtyCheck } from 'components/ProfileDrawer/providers/ProfileSectionDirtyCheckProvider'
import { CombinedAvailabilityProvider } from './CombinedAvailabilityProvider'
import { CombinedAvailabilities } from './CombinedAvailabilities'
import { DateRangeFilter } from './DateRangeFilter'
import {
  DateFilterWrapper,
  CombinedAvailabilityWrapper,
} from './styled-components'

export const CombinedAvailability: VFC<{
  timeZoneId: string
  userId: string
}> = ({ timeZoneId, userId }) => {
  const { startDate: eventStartDate, endDate: eventEndDate } =
    useEventInfo().location ?? {}
  const [dateRange, setDateRange] = useState([eventStartDate!, eventEndDate!])

  const [filterRange, setFilterRange] = useState(dateRange)

  const { isDirty, setShowConfirmation } = useDirtyCheck()

  const onChange = (dateRange: string[]) => {
    if (isDirty) {
      setShowConfirmation(true)
      setFilterRange(dateRange)
    } else {
      setDateRange(dateRange)
    }
  }

  return (
    <>
      <DateFilterWrapper>
        <DateRangeFilter
          dateRange={dateRange}
          onChange={onChange}
          minDate={eventStartDate!}
          maxDate={eventEndDate!}
        />
      </DateFilterWrapper>
      <CombinedAvailabilityWrapper>
        <CombinedAvailabilityProvider
          timeZoneId={timeZoneId}
          userId={userId}
          dateRange={dateRange}
          filterRange={filterRange}
          setDateRange={setDateRange}
        >
          <CombinedAvailabilities
            timeZoneId={timeZoneId}
            dateRange={dateRange}
          />
        </CombinedAvailabilityProvider>
      </CombinedAvailabilityWrapper>
    </>
  )
}
