import { useState, VFC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import {
  Tabs as BaseTabs,
  Tab as BaseTab,
  TabTitle,
  getTabOverrides,
} from '@jifflenow/brightui/tabs-motion'
import { useMe } from 'providers/MeProvider'
import { components } from 'types/openapi'
import { tabOverrides, tabsOverrides } from './overrides'
import { MappingsPage } from './MappingsPage'
import { Tab, TabsProps } from './types'
import { MappingsSettingContainer, TabsRoot } from './styled-components'
import { DefaultMappings } from './DefaultMappingSettings'

type Role = components['schemas']['Role']

const messages = defineMessages({
  mappedTabLabel: {
    defaultMessage: 'Mapped \u2022 {count, number}',
    description: 'Tab title',
  },
  unMappedTabLabel: {
    defaultMessage: 'Unmapped \u2022 {count, number}',
    description: 'Tab title',
  },
  noCampaignsListed: {
    defaultMessage:
      'No engagement types to show as you are not mapped to any campaigns yet.',
    description: 'Empty Message',
  },
})

const adminRoles: Set<Role> = new Set(['CSM', 'MM', 'JMM'])

export const Tabs: VFC<TabsProps> = props => {
  const { formatMessage } = useIntl()
  const { role: companyRole, eventRole } = useMe().data.attributes
  const [activeTab, setActiveTab] = useState<Tab>('mapped')
  const [pageNumber, setPageNumber] = useState(1)
  const { mappedCount, unMappedCount } = props.state[activeTab]

  const handleTabChange = (tab: Tab) => {
    setActiveTab(tab)
    setPageNumber(1)
  }

  return (
    <TabsRoot>
      <BaseTabs
        overrides={tabsOverrides}
        activeKey={activeTab}
        onChange={({ activeKey }) => handleTabChange(activeKey as Tab)}
      >
        <BaseTab
          key="mapped"
          title={
            <TabTitle>
              {formatMessage(messages.mappedTabLabel, { count: mappedCount })}
            </TabTitle>
          }
          overrides={getTabOverrides(tabOverrides)}
        >
          <MappingsPage
            activeTab={activeTab}
            pageNumber={pageNumber}
            setPageNumber={setPageNumber}
            {...props}
          />
        </BaseTab>
        {(adminRoles.has(companyRole) ||
          (eventRole && adminRoles.has(eventRole))) && (
          <BaseTab
            key="unmapped"
            title={
              <TabTitle>
                {formatMessage(messages.unMappedTabLabel, {
                  count: unMappedCount,
                })}
              </TabTitle>
            }
            overrides={getTabOverrides(tabOverrides)}
          >
            <MappingsPage
              activeTab={activeTab}
              pageNumber={pageNumber}
              setPageNumber={setPageNumber}
              {...props}
            />
          </BaseTab>
        )}
      </BaseTabs>
      <MappingsSettingContainer>
        <DefaultMappings
          selectedLocation={props.selectedLocation}
          user={props.user}
        />
      </MappingsSettingContainer>
    </TabsRoot>
  )
}
