import { Dispatch, SetStateAction } from 'react'
import { defineMessages, MessageDescriptor } from 'react-intl'
import { DateTime } from 'luxon'
import {
  getEndDateOfDay,
  getFullMinutes,
  getISOTimeFromDate,
  getPlusDate,
} from './DateTimeConversions'

export const deleteAllSlots = <T>({
  availability,
  setAvailability,
  check,
}: {
  availability: T[]
  setAvailability: Dispatch<SetStateAction<T[]>>
  check: (availability: T) => boolean
}) => {
  setAvailability(
    availability.map(availability =>
      check(availability) ? { ...availability, timeRanges: [] } : availability
    )
  )
}

const messages = defineMessages({
  overlapError: {
    defaultMessage: 'Time overlap',
    description: 'Error message',
  },
  endTimeLessThanStartTime: {
    defaultMessage: 'Choose an end time later than start time.',
    description: 'Error message',
  },
})

type TimeRanges = { start: string; end: string }[]

export const addNewSlot = <T extends { timeRanges: TimeRanges }>({
  availability,
  setAvailability,
  timeRanges,
  check,
  getStartOfDay,
  getNextSlot,
}: {
  availability: T[]
  setAvailability: Dispatch<SetStateAction<T[]>>
  check: (availability: T) => boolean
  timeRanges: TimeRanges
  getStartOfDay: () => string
  getNextSlot: (prevEndTime: string) => string
}) => {
  const prevEndTime = timeRanges[timeRanges.length - 1]?.end
  const newStartDate = prevEndTime
    ? getNextSlot(prevEndTime)
    : getPlusDate(getStartOfDay(), { hour: 9 })

  const newEndDate =
    DateTime.fromISO(newStartDate).hour === 23
      ? getEndDateOfDay(newStartDate)
      : prevEndTime
      ? getPlusDate(newStartDate, { hour: 1 })
      : getPlusDate(getStartOfDay(), { hour: 17 })

  setAvailability(
    availability.map(availability =>
      check(availability)
        ? {
            ...availability,
            timeRanges: [
              ...availability.timeRanges,
              {
                start: getISOTimeFromDate(newStartDate),
                end: getISOTimeFromDate(newEndDate),
              },
            ],
          }
        : availability
    )
  )
}

export const checkForOverlaps = <T extends { timeRanges: TimeRanges }>({
  setAvailability,
  check,
  formatMessage,
}: {
  setAvailability: Dispatch<SetStateAction<T[]>>
  check: (availability: T) => boolean
  formatMessage: (MessageDescriptor: MessageDescriptor) => string
}) => {
  setAvailability(availability =>
    availability.map(availability =>
      check(availability)
        ? {
            ...availability,
            timeRanges: availability.timeRanges.some(({ start, end }) => {
              const endTimeToCompare =
                getFullMinutes(end) === 0
                  ? DateTime.fromISO(end).endOf('day').toISOTime()
                  : end

              return endTimeToCompare <= start
            })
              ? availability.timeRanges
              : availability.timeRanges.map((range, currentRangeIndex) => {
                  const { start, end } = range

                  const endTimeToCompare =
                    getFullMinutes(end) === 0
                      ? DateTime.fromISO(end).endOf('day').toISOTime()
                      : end

                  const otherRanges = availability.timeRanges.filter(
                    (_, index) => index !== currentRangeIndex
                  )

                  const hasOverlap = otherRanges.some(
                    ({ start: rangeStart, end: rangeEnd }) => {
                      const rangeEndToCompare =
                        getFullMinutes(rangeEnd) === 0
                          ? DateTime.fromISO(rangeEnd).endOf('day').toISOTime()
                          : rangeEnd

                      return (
                        rangeStart < endTimeToCompare &&
                        rangeEndToCompare > start
                      )
                    }
                  )

                  return {
                    start,
                    end,
                    ...(hasOverlap
                      ? { error: formatMessage(messages.overlapError) }
                      : null),
                  }
                }),
          }
        : availability
    )
  )
}

export const deleteSlot = <T extends { timeRanges: TimeRanges }>({
  availability,
  setAvailability,
  check,
  formatMessage,
  indexToDelete,
}: {
  availability: T[]
  setAvailability: Dispatch<SetStateAction<T[]>>
  check: (availability: T) => boolean
  indexToDelete: number
  formatMessage: (MessageDescriptor: MessageDescriptor) => string
}) => {
  setAvailability(
    availability.map(availability =>
      check(availability)
        ? {
            ...availability,
            timeRanges: availability.timeRanges.filter(
              (_, index) => index !== indexToDelete
            ),
          }
        : availability
    )
  )

  checkForOverlaps({ setAvailability, check, formatMessage })
}
