import { VFC, useState, useEffect, useRef } from 'react'
import { DateTime } from 'luxon'
import { defineMessages, useIntl } from 'react-intl'
import { ModalSpinner } from '@jifflenow/brightui/modal-spinner'
import {
  useUserAvailabilityException,
  postUserAvailabilityExceptions,
  usePortalV2Client,
} from 'rest'
import { showSuccessToast, showErrorToast } from 'utils/toast'
import { useBeforeUnload } from 'utils/useBeforeUnload'
import { useParams } from 'react-router-dom'
import { useEventInfo } from 'components/ProfileDrawer/providers/EventInfoProvider'
import { Footer } from '../Common/Footer'
import { diffInISODates } from '../DateTimeConversions'
import { ListLoader } from './Loader'
import { AddExceptionButton } from './AddExceptionButton'
import { ListExceptions } from './ListExceptions'
import {
  getExceptionsInTimeZone,
  getExceptionsInUTC,
} from './transformExceptionDates'
import { fromAPI, toAPI } from './data-adapters'
import { Exception, ExceptionsProps } from './types'
import { Root, NoException, Text } from './styled-components'

const messages = defineMessages({
  addException: {
    defaultMessage: 'Add Exception',
    description: 'Button text',
  },
  noException: {
    defaultMessage: 'Add dates that deviate from your standard working hours.',
    description: 'Button text',
  },
  success: {
    defaultMessage: 'User availability exceptions updated successfully',
    description: 'Button text',
  },
  genericError: {
    defaultMessage: 'Something is not right. Please retry.',
    description: 'Error message',
  },
})

export const Exceptions: VFC<ExceptionsProps> = ({
  timeZone,
  onSuccess,
  clearTempActiveKey,
}) => {
  const { formatMessage } = useIntl()
  const unload = useBeforeUnload()
  const client = usePortalV2Client()
  const [isDirty, setIsDirty] = useState(false)
  const [showLoader, setShowLoader] = useState(false)
  const [UTCExceptions, setUTCExceptions] = useState<Exception[] | null>(null)
  const [exceptions, setExceptions] = useState<Exception[] | null>(null)
  const [defaultExceptions, setDefaultExceptions] = useState<
    Exception[] | null
  >(null)
  const shouldUpdateUTCExceptions = useRef(true)

  const { startDate, endDate } = useEventInfo().location || {}

  const { userId: id } = useParams<{ userId: string }>()
  const { timeZoneId } = timeZone

  useEffect(() => {
    if (shouldUpdateUTCExceptions.current && exceptions && UTCExceptions) {
      !isDirty && setIsDirty(true)
      setUTCExceptions(getExceptionsInUTC(exceptions, timeZoneId))
    }
    shouldUpdateUTCExceptions.current = true
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [exceptions])

  useEffect(() => {
    shouldUpdateUTCExceptions.current = false

    const exceptions = [...(UTCExceptions ?? [])]
    const exceptionsInChosenTimezone = getExceptionsInTimeZone(
      exceptions,
      timeZoneId
    )

    const [, exceptionsFromPresentDate] = partitionExceptionDates(
      exceptionsInChosenTimezone
    )

    setExceptions(exceptionsFromPresentDate)

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [timeZoneId])

  const { isPending } = useUserAvailabilityException({
    id,
    startDate,
    endDate,
    onResolve: ({ data }) => {
      const exceptions = fromAPI(data)
      const exceptionsInChosenTimezone = getExceptionsInTimeZone(
        exceptions,
        timeZoneId
      )

      setUTCExceptions(
        getExceptionsInUTC(exceptionsInChosenTimezone, timeZoneId)
      )
      shouldUpdateUTCExceptions.current = false

      setExceptions(exceptionsInChosenTimezone)

      setDefaultExceptions(exceptionsInChosenTimezone)
    },
  })

  const partitionExceptionDates = (exceptions: Exception[]) => {
    const todayInChosenTZ = DateTime.local().setZone(timeZoneId).toISODate()

    return [
      exceptions.filter(
        ({ date }) => diffInISODates(todayInChosenTZ, date).days < 0
      ),
      exceptions.filter(
        ({ date }) => diffInISODates(todayInChosenTZ, date).days >= 0
      ),
    ]
  }

  const handleSave = async () => {
    setShowLoader(true)
    const payload = {
      upsert: toAPI(getExceptionsInUTC(exceptions ?? [], timeZoneId)),
    }
    try {
      await postUserAvailabilityExceptions(client, id, payload)
      setShowLoader(false)
      showSuccessToast(formatMessage(messages.success))
      setIsDirty(false)
      onSuccess()
      return undefined
    } catch (error) {
      setShowLoader(false)
      showErrorToast(formatMessage(messages.genericError))
      return false
    }
  }

  useEffect(() => {
    unload(isDirty)
  }, [isDirty, unload])

  const resetExceptions = () => {
    setExceptions(defaultExceptions)
    setIsDirty(false)
    shouldUpdateUTCExceptions.current = false
  }

  return (
    <>
      {showLoader && <ModalSpinner />}
      <Root>
        {isPending && <ListLoader />}
        {!isPending &&
          exceptions &&
          (exceptions.length ? (
            <ListExceptions
              timeZone={timeZone}
              exceptions={exceptions}
              setExceptions={setExceptions}
            />
          ) : (
            <NoException>
              <AddExceptionButton
                exceptions={exceptions}
                setExceptions={setExceptions}
                timeZone={timeZone}
              />
              <Text>{formatMessage(messages.noException)}</Text>
            </NoException>
          ))}
      </Root>
      <Footer
        isDirty={isDirty}
        clearTempActiveKey={clearTempActiveKey}
        onSave={handleSave}
        isDisabled={showLoader || !isDirty}
        pendoPrefix="profile/availability/exceptions"
        handleDiscard={resetExceptions}
      />
    </>
  )
}
