import { useEffect, useState, VFC } from 'react'
import { FormSpy, useForm } from 'react-final-form'
import { useBeforeUnload } from 'utils/useBeforeUnload'
import { useMe } from 'providers/MeProvider'
import { AppSource, useAppSource } from 'providers/AppSourceProvider'
import { CustomFormFields } from 'components/CustomFormFields'
import { useDirtyCheckDialog } from '../../Common/useDirtyCheckDialog'
import { RoleChangeDialog } from '../../Common/RoleChangeDialog'
import { Footer } from '../../Common'
import { MappedUserField } from '../../Settings'
import { Consents } from '../Consents'
import { FormProps, FormValues } from './types'
import { Container, Root, CustomFieldsWrapper } from './styled-components'
import { Avatar } from './Avatar'
import { PersonalLinkEdit } from './PersonalLinkEdit'
import { CanCreateInstance } from './CanCreateInstance'
import { StandardFormFields } from './StandardFormFields'
import { CanManageSettings } from './CanManageSettings'

export const EditForm: VFC<FormProps> = ({
  formRenderProps: { dirty, handleSubmit, values, initialValues },
  user,
  formFields: { standardFields, customFields },
  onCancel,
  onSave,
}) => {
  const unload = useBeforeUnload()
  const isSourceOutlook = useAppSource().app === AppSource.Outlook
  const location = new URLSearchParams(window.location.search).get('location')

  const {
    id: profileUserId,
    attributes: {
      selfConsentMessage,
      optOutMessage,
      personalLink,
      userType,
      canRequestNewInstance,
    },
  } = user

  const {
    id: loggedInUserId,
    attributes: { role: loggedInUserRole },
  } = useMe().data

  const { change, submit } = useForm()
  const { role, mappedUsers } = values
  const { role: initialRole } = initialValues

  const isMyProfile = loggedInUserId === profileUserId

  useDirtyCheckDialog({
    isDirty: dirty,
    onSave: () => onSave(values as FormValues),
  })

  useEffect(() => {
    unload(dirty)
  }, [dirty, unload])

  const [isOpen, setIsOpen] = useState<boolean>(false)

  useEffect(() => {
    if (initialRole !== role && mappedUsers?.length) {
      setIsOpen(true)
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [initialRole, role])

  return (
    <>
      <form noValidate onSubmit={handleSubmit}>
        <Container>
          <Root>
            <Avatar />
            {personalLink && userType === 'internal' && (
              <PersonalLinkEdit personalLink={personalLink} />
            )}
          </Root>
          <CustomFieldsWrapper>
            <StandardFormFields fields={standardFields} />
          </CustomFieldsWrapper>
          {!isSourceOutlook &&
            (role === 'EA' || role === 'EX') &&
            !location && <MappedUserField name="mappedUsers" role={role} />}
          {loggedInUserRole === 'CSM' &&
            role === 'MM' &&
            canRequestNewInstance && <CanCreateInstance />}
          {userType === 'internal' && <CanManageSettings />}
          <CustomFieldsWrapper>
            <CustomFormFields fields={customFields} />
          </CustomFieldsWrapper>
          {isMyProfile && (
            <Consents consent={selfConsentMessage} optOut={optOutMessage} />
          )}
        </Container>
        <FormSpy
          subscription={{
            submitting: true,
            validating: true,
            dirty: true,
            values: true,
            valid: true,
          }}
        >
          {({ submitting, validating, dirty, values, valid }) => (
            <Footer
              onDiscard={onCancel}
              isLoading={submitting}
              isDisabled={!dirty || submitting || validating}
              hasForm
              onSave={() => (valid ? onSave(values as FormValues) : submit())}
              pendoPrefix="profile/edit"
            />
          )}
        </FormSpy>
      </form>
      {isOpen && (
        <RoleChangeDialog
          showMessage
          onSuccess={() => {
            change('mappedUsers', [])
            setIsOpen(false)
          }}
          onClose={() => {
            setIsOpen(false)
          }}
          onCancel={() => {
            change('role', initialRole)
            setIsOpen(false)
          }}
          pendoName="profile/role-change-dialog"
        />
      )}
    </>
  )
}
