import { DateTime } from 'luxon'
import {
  getUTCDateFromMinutes,
  getDateTimeInTimeZone,
  getUTCDateTimeFromTimeZone,
  getFullMinutes,
  getUTCFullMinutes,
  getISOTimeFromDate,
  getISODateFromWeekDayAndTime,
  getUTCStartDate,
  getEndDateOfDay,
  getPlusDate,
} from '../DateTimeConversions'
import {
  TimeZoneId,
  TimeRangeInDates,
  TimeRangeInMinutes,
  Availability,
  UTCMinutesAvailability,
} from '../types'
import { Weekdays } from '../Common/weekDayMappings'
import { Day, WeekDay } from './types'

// Base empty availability to avoid missing any week day after transformations.
const baseAvailabilityHash = Object.values(Weekdays)
  .filter(weekDay => typeof weekDay === 'number')
  .reduce((acc, curr) => ({ ...acc, [curr]: [] }), {})

// Merges continuous time slots after UTC <=> TimeZone transformations
function mergeContinuousTimeRanges<T extends any[]>(timeRanges: T): T {
  return timeRanges
    .sort((a, b) => (a.start >= b.start ? 1 : -1))
    .reduce((acc, curr) => {
      const prevRange = acc.pop()

      return prevRange
        ? prevRange.end === curr.start
          ? [...acc, { start: prevRange.start, end: curr.end }]
          : [...acc, prevRange, curr]
        : [...acc, curr]
    }, [])
}

const transformMinutesInUTC = (data: UTCMinutesAvailability[]) =>
  data.map(({ day, timeRanges }, dayIndex) => ({
    day,
    date: getUTCDateFromMinutes({ days: dayIndex }),
    timeRanges: timeRanges.map(({ start, end }) => ({
      start: getUTCDateFromMinutes({ days: dayIndex, minutes: start }),
      end: getUTCDateFromMinutes({ days: dayIndex, minutes: end }),
    })),
  }))

export const getAvailabilityInTimeZone = (
  availability: UTCMinutesAvailability[],
  timeZone: TimeZoneId
) => {
  const availabilityInUTC = transformMinutesInUTC(availability)
  const reducedAvailability = availabilityInUTC
    .flatMap(({ timeRanges }) =>
      timeRanges.flatMap(range => {
        const startDateTime = getDateTimeInTimeZone(range.start, timeZone)
        const endDateTime = getDateTimeInTimeZone(range.end, timeZone)
        const start = startDateTime.toISO()
        const end = endDateTime.toISO()

        return startDateTime.toISODate() === endDateTime.toISODate()
          ? { start, end }
          : [
              { start, end: getEndDateOfDay(start) },
              { start: endDateTime.startOf('day').toISO(), end },
            ]
      })
    )
    .reduce((accRanges, range) => {
      const { start, end } = range
      const weekday = DateTime.fromISO(start).weekday

      return {
        ...accRanges,
        [weekday]: [
          ...(accRanges[weekday] ?? []),
          ...(start === end
            ? []
            : [
                {
                  start: getISOTimeFromDate(start),
                  end: getISOTimeFromDate(end),
                },
              ]),
        ],
      }
    }, baseAvailabilityHash as { [key: string]: TimeRangeInDates })

  return Object.keys(reducedAvailability)
    .sort((next: string, prev: string) => Number(next) - Number(prev))
    .map(weekDay => ({
      weekDay: Number(weekDay) as WeekDay,
      day: Weekdays[Number(weekDay)] as Day,
      timeRanges: mergeContinuousTimeRanges<TimeRangeInDates>(
        reducedAvailability[weekDay]
      ),
    }))
}

const transformISOTimeToDates = (data: Availability[]) =>
  data.map(({ day, weekDay, timeRanges }) => ({
    day,
    weekDay,
    timeRanges: timeRanges.map(({ start, end }) => ({
      start: getISODateFromWeekDayAndTime(weekDay, start),
      end: getISODateFromWeekDayAndTime(weekDay, end),
    })),
  }))

export const getAvailabilityInUTC = (
  availability: Availability[],
  timeZone: TimeZoneId
) => {
  const availabilityInUTC = transformISOTimeToDates(availability)
  const reducedAvailability = availabilityInUTC
    .flatMap(({ timeRanges }) =>
      timeRanges.flatMap(range => {
        const endDate =
          getFullMinutes(range.end) === 0
            ? getPlusDate(range.end, { day: 1 })
            : range.end
        const startDateTime = getUTCDateTimeFromTimeZone(range.start, timeZone)
        const endDateTime = getUTCDateTimeFromTimeZone(endDate, timeZone)
        const start = startDateTime.toISO()
        const end = endDateTime.toISO()

        return startDateTime.toISODate() === endDateTime.toISODate() ||
          (endDateTime.hour === 0 && endDateTime.minute === 0)
          ? { start, end }
          : [
              { start, end: getUTCStartDate(startDateTime.toISODate()) },
              { start: endDateTime.startOf('day').toISO(), end },
            ]
      })
    )
    .reduce((accRanges, range) => {
      const { start, end } = range
      const weekday = DateTime.fromISO(start).toUTC().weekday

      const startInMinutes = getUTCFullMinutes(start)
      const endInMinutes = getUTCFullMinutes(end)

      return {
        ...accRanges,
        [weekday]: [
          ...(accRanges[weekday] ?? []),
          ...(startInMinutes === endInMinutes && endInMinutes !== 0
            ? []
            : [
                {
                  start: startInMinutes,
                  end: endInMinutes || 1440,
                },
              ]),
        ],
      }
    }, baseAvailabilityHash as { [key: string]: TimeRangeInMinutes })

  return Object.keys(reducedAvailability)
    .sort((next: string, prev: string) => Number(next) - Number(prev))
    .map(weekDay => ({
      day: Weekdays[Number(weekDay)] as Day,
      timeRanges: mergeContinuousTimeRanges<TimeRangeInMinutes>(
        reducedAvailability[weekDay]
      ),
    }))
}
