import { useState, VFC, useMemo, useCallback, useEffect } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import {
  Drawer,
  MenuOptions as DrawerNavMenuOptions,
} from 'ui-components/Drawer'
import { AppSource, useAppSource } from 'providers/AppSourceProvider'
import { useMe } from 'providers/MeProvider'
import { useLocation } from 'react-router-dom'
import { EventSubTypes, EventTypes as EventType } from 'gql/types/globalTypes'
import { EngagementTypes } from './EngagementTypes'
import { Profile } from './Profile'
import { Settings } from './Settings'
import { TopicsMapping } from './Topic'
import { Availability } from './Availability'
import { Integrations } from './Integrations'
import { Campaigns } from './Campaigns'
import { useProfileUser } from './providers/ProfileUserProvider'
import { EventNameProvider } from './providers/EventNameProvider'
import { useDirtyCheck } from './providers/ProfileSectionDirtyCheckProvider'
import { Root } from './styled-components'
import { useEventInfo } from './providers/EventInfoProvider'
import { useNavProvider } from './providers/NavProvider'
import { Keys, Role } from './types'

const menuItemLabels = defineMessages({
  profile: {
    defaultMessage: 'Profile',
    description: 'Tab header',
  },
  topicMapped: {
    defaultMessage: 'Topics',
    description: 'Tab header',
  },
  engagementTypeMapped: {
    defaultMessage: 'Engagement Types',
    description: 'Tab header',
  },
  settings: {
    defaultMessage: 'Settings',
    description: 'Tab header',
  },
  availability: {
    defaultMessage: 'Availability',
    description: 'Tab header',
  },
  integrations: {
    defaultMessage: 'Integrations',
    description: 'Tab header',
  },
  events: {
    defaultMessage: 'Event Instances',
    description: 'Tab header',
  },
  campaignList: {
    defaultMessage: 'Listing',
    description: 'Tab header',
  },
  eMeet: {
    defaultMessage: 'eMeet Instances',
    description: 'Tab header',
  },
})

interface MenuItems {
  labelKey: keyof typeof menuItemLabels
  key: Keys
  children?: MenuItems[]
  eventType?: string
}

interface MenuOptions extends DrawerNavMenuOptions {
  children?: MenuOptions[]
  key: Keys
  eventType?: string
}

const componentMapping: Record<Keys, VFC<any>> = {
  profile: Profile,
  integrations: Integrations,
  availability: Availability,
  campaigns: props => <Campaigns {...props} />,
  campaignList: props => <Campaigns {...props} />,
  engagementTypeMapped: props => <EngagementTypes {...props} />,
  topic: props => <TopicsMapping {...props} />,
  settings: props => <Settings {...props} />,
  eMeet: props => <Campaigns {...props} />,
  eMeetCampaignList: props => <Campaigns {...props} />,
  eMeetEngagementTypeMapped: props => <EngagementTypes {...props} />,
  eMeetTopicMapped: props => <TopicsMapping {...props} />,
  eMeetSettings: props => <Settings {...props} />,
}

const adminRoles: Set<Role> = new Set(['CSM', 'MM', 'JMM'])

const getMenuItems: (
  isOutlook: boolean,
  isExternal: boolean,
  role: Role,
  eventRole?: Role
) => MenuItems[] = (isOutlook, isExternal, role, eventRole) => {
  const location = new URLSearchParams(window.location.search).get('location')
  const showInstanceList = role === 'CSM' || (role === 'MM' && !location)
  const isNonAdmin = !adminRoles.has(eventRole || role)

  return [
    {
      key: 'profile',
      labelKey: 'profile',
    },
    ...(isExternal
      ? []
      : ([
          {
            key: 'integrations',
            labelKey: 'integrations',
          },
          {
            key: 'availability',
            labelKey: 'availability',
          },
        ] as MenuItems[])),
    {
      key: 'campaigns',
      labelKey: 'events',
      ...(isOutlook
        ? {}
        : {
            children: [
              ...(showInstanceList
                ? ([
                    {
                      key: 'campaignList',
                      labelKey: 'campaignList',
                    },
                  ] as MenuItems[])
                : []),
              ...(isExternal || isNonAdmin
                ? []
                : ([
                    {
                      key: 'engagementTypeMapped',
                      labelKey: 'engagementTypeMapped',
                    },
                    {
                      key: 'topic',
                      labelKey: 'topicMapped',
                    },
                  ] as MenuItems[])),

              {
                key: 'settings',
                labelKey: 'settings',
              },
            ],
          }),
    },
    {
      key: 'eMeet',
      labelKey: 'eMeet',
      eventType: 'Emeet',
      ...(isOutlook
        ? {}
        : {
            children: [
              ...(showInstanceList
                ? ([
                    {
                      key: 'eMeetCampaignList',
                      labelKey: 'campaignList',
                      eventType: 'Emeet',
                    },
                  ] as MenuItems[])
                : []),
              ...(isExternal || isNonAdmin
                ? []
                : ([
                    {
                      key: 'eMeetEngagementTypeMapped',
                      labelKey: 'engagementTypeMapped',
                      eventType: 'Emeet',
                    },
                    {
                      key: 'eMeetTopicMapped',
                      labelKey: 'topicMapped',
                      eventType: 'Emeet',
                    },
                  ] as MenuItems[])),
              {
                key: 'eMeetSettings',
                labelKey: 'settings',
                eventType: 'Emeet',
              },
            ],
          }),
    },
  ]
}

export const Nav: VFC = () => {
  const { formatMessage } = useIntl()
  const { data } = useEventInfo().eventTypes
  const { app } = useAppSource()
  const { isDirty, setShowConfirmation, onCloseConfirmation } = useDirtyCheck()
  const isSourceOutlook = app === AppSource.Outlook
  const {
    attributes: { userType, canViewAvailability },
  } = useProfileUser()
  const { role, eventRole } = useMe().data.attributes

  const setAvailabilityParam = new URLSearchParams(window.location.search).get(
    'setAvailability'
  )
  const location = new URLSearchParams(useLocation().search).get('location')
  const eventInfo = useEventInfo().location
  const locationEventType = eventInfo?.subType
  const hideInstanceType =
    locationEventType === EventSubTypes.Emeet ? 'campaigns' : 'eMeet'

  const showAvailability =
    isSourceOutlook || (location && locationEventType) || canViewAvailability

  const menuItems = useMemo(
    () =>
      getMenuItems(isSourceOutlook, userType === 'external', role, eventRole)
        .filter(
          item =>
            isSourceOutlook ||
            (location && locationEventType
              ? item.key !== hideInstanceType
              : (item.key !== 'eMeet' || data.includes(EventType.Emeet)) &&
                (item.key !== 'campaigns' ||
                  data.includes(EventType.TradeShow)))
        )
        .filter(item => item.key !== 'availability' || showAvailability),
    [
      data,
      hideInstanceType,
      role,
      eventRole,
      isSourceOutlook,
      location,
      locationEventType,
      userType,
      showAvailability,
    ]
  )

  const getLandingMenu = () =>
    showAvailability && setAvailabilityParam
      ? 'availability'
      : location && !isSourceOutlook
      ? locationEventType === 'Emeet'
        ? 'eMeetSettings'
        : 'settings'
      : menuItems[0].key

  const {
    state: { selectedKey = getLandingMenu() },
    dispatch,
  } = useNavProvider()

  const setSelectedKey = (key: Keys) => {
    dispatch({
      type: 'setSelectedKey',
      payload: key,
    })
  }

  const [isLastItem, setIsLastItem] = useState(false)

  const itemMapper = (isChild = false) => (item: MenuItems): MenuOptions => {
    const { labelKey, children, key, ...rest } = item

    return {
      ...rest,
      key,
      label: formatMessage(menuItemLabels[labelKey]),
      isActive: key === selectedKey,
      ...(children ? { children: children.map(itemMapper(true)) } : {}),
      ...(isChild ? { isChild } : {}),
    }
  }

  const menuOptions = menuItems.map(itemMapper())

  const findItem = useCallback(({ key }: MenuOptions) => key === selectedKey, [
    selectedKey,
  ])

  const flattenedMenu = menuOptions.flatMap(
    (item: MenuOptions) => item.children ?? [item]
  )

  useEffect(() => {
    const currentIndex = flattenedMenu.findIndex(findItem)
    setIsLastItem(currentIndex + 1 === flattenedMenu.length)
  }, [findItem, flattenedMenu])

  const navigateToNextItem = () => {
    const currentIndex = flattenedMenu.findIndex(findItem)
    currentIndex + 1 !== flattenedMenu.length &&
      setSelectedKey(flattenedMenu[currentIndex + 1].key)
  }

  const eventType =
    menuOptions.find(findItem)?.eventType ??
    menuOptions
      .filter(({ children }) => children)
      .flatMap(({ children }) => children!)
      .find(findItem)?.eventType

  const SelectedItem =
    componentMapping[
      (
        menuOptions.find(findItem) ??
        menuOptions
          .filter(({ children }) => children)
          .flatMap(({ children }) => children!)
          .find(findItem)
      )?.key as Keys
    ]

  return (
    <>
      <Root>
        <Drawer.Nav
          options={menuOptions}
          onOptionSelect={item => {
            if (!item.isActive) {
              if (isDirty) {
                onCloseConfirmation(() => () => setSelectedKey(item.key))
                setShowConfirmation(true)
                if (selectedKey === 'availability') {
                  dispatch({
                    type: 'setTempSelectedKey',
                    payload: item.key,
                  })
                }
              } else {
                setSelectedKey(item.key)
              }
            }
          }}
          selectedKey={selectedKey}
        />
      </Root>

      <EventNameProvider eventType={eventType}>
        <SelectedItem
          key={selectedKey}
          onNavigate={navigateToNextItem}
          isLastItem={isLastItem}
        />
      </EventNameProvider>
    </>
  )
}
