import { ReactNode, useState, VFC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { FieldRenderProps, useForm, useFormState } from 'react-final-form'
import { Select } from '@jifflenow/brightui/select'
import { Field, Validation } from 'ui-components/Field'
import { roles as roleLabel } from 'utils/messages'
import { RoleChangeDialog } from '../Common/RoleChangeDialog'
import { useEventInfo } from '../providers/EventInfoProvider'
import { useCampaign } from '../Common'
import { FormValues, RoleFieldProps, RoleOption } from './types'
import { StyledFormControl } from './styled-components'
import { eventTypeMapping } from './constants'

const messages = defineMessages({
  label: {
    defaultMessage: '{eventType} Role',
    description: 'Field label',
  },
})

const EventRoleField: (props: FieldRenderProps<any>) => ReactNode = ({
  input,
  meta: { initial, touched, error },
  isDisabled,
  dropdownOptions,
}) => {
  const { formatMessage } = useIntl()
  const [isOpen, setIsOpen] = useState(false)
  const { campaign } = useCampaign()
  const [showMessage, setShowMessage] = useState(false)
  const { change } = useForm<FormValues>()
  const { subType = campaign?.subType } = useEventInfo().location || {}

  const {
    values: { mappedTo },
  } = useFormState<FormValues>()

  return (
    <>
      <StyledFormControl
        label={formatMessage(messages.label, {
          eventType: subType ? eventTypeMapping[subType] : '',
        })}
        error={touched && error}
      >
        <Select
          {...input}
          required
          id="eventRole"
          disabled={isDisabled}
          options={dropdownOptions}
          onChange={({ value }: { value: RoleOption }) => {
            input.onChange(value)
            const role = value[0]?.id
            const eventRole = input.value[0]?.id

            if (initial !== role) {
              !(
                (eventRole === 'EX' && initial === 'EA') ||
                (eventRole === 'EA' && initial === 'EX')
              ) &&
                mappedTo?.length &&
                setShowMessage(true)
              setIsOpen(true)
            }
          }}
        />
      </StyledFormControl>

      {isOpen && (
        <RoleChangeDialog
          showMessage={showMessage}
          showNote
          onSuccess={() => {
            change('mappedTo', [])
            setIsOpen(false)
            setShowMessage(false)
          }}
          onClose={() => {
            setIsOpen(false)
            setShowMessage(false)
          }}
          onCancel={() => {
            change('eventRole', initial)
            setIsOpen(false)
            setShowMessage(false)
          }}
        />
      )}
    </>
  )
}

export const RoleField: VFC<RoleFieldProps> = ({ roles, isDisabled }) => {
  const { formatMessage } = useIntl()
  const dropdownOptions = roles.map(({ clientDisplayName: id }) => ({
    label: formatMessage(roleLabel[id]),
    id,
  }))

  return (
    <Field<(string | number | undefined)[] | undefined>
      name="eventRole"
      parse={value => value[0]?.id}
      format={value => (value ? [{ id: value, label: value }] : [])}
      render={EventRoleField}
      validations={[Validation.REQUIRED]}
      dropdownOptions={dropdownOptions}
      isDisabled={isDisabled}
    />
  )
}
