import { VFC, useState, useEffect, useRef } from 'react'
import { useQuery } from 'react-query'
import { getTopicsPreferencesQueryFn, useUserEngagementsMapping } from 'rest'
import { ListLoader, useCampaign } from '../../Common'
import { useSelection } from '../UnMappedTopicsSelectionProvider'
import { useSelection as useMappedSelection } from '../MappedTopicsSelectionsProvider'
import { MappingsList } from '../Common/MappingsList'
import {
  MappedTypeProps,
  MappingItems,
  UserTopicsMapped,
} from '../Common/types'
import { Root } from '../Common/styled-components'
import { PaginationContainer, PAGE_SIZE } from '../PaginationContainer'

export const UnMapped: VFC<MappedTypeProps> = ({
  id,
  setTotalCount,
  searchTerm,
  refresh,
  mappingsCount,
  setDefaultSelections,
}) => {
  const { eventName, location } = useCampaign().campaign!
  const [mappingTable, setMappingsTable] = useState<MappingItems | []>([])
  const [totalItems, setTotalItems] = useState<number>(0)
  const [pageSize, setPageSize] = useState(0)

  const isMounted = useRef(false)

  const {
    pageNumber,
    setPageNumber,
    selections,
    isAllSelected,
    toggleSelectAll,
    toggleSelectItem,
    isItemSelected,
    clearAll,
  } = useSelection()

  const { clearAll: clearAllMapped } = useMappedSelection()

  const resetToFirstPage = () => {
    setPageNumber(1)
    clearAll()
    clearAllMapped()
  }

  const instanceType =
    eventName === 'Emeet' ? 'emeetInstances' : 'eventInstances'

  useQuery(
    [
      'topicsPreferences',
      {
        path: { eventName },
        query: {
          location_uuid: location,
        },
      },
    ],
    getTopicsPreferencesQueryFn,
    {
      onSuccess: ({ data }) => {
        setPageSize(data[instanceType]?.pageSize ?? PAGE_SIZE)
      },
      onError: () => {
        setPageSize(PAGE_SIZE)
      },
    }
  )

  const state = useUserEngagementsMapping({
    id,
    mappingType: 'TopicMapping',
    subSystem: eventName,
    onResolve: userEngagementsMapping => {
      const { data, meta } = userEngagementsMapping as UserTopicsMapped
      setTotalItems(meta.page?.size!)
      setDefaultSelections(prev => ({
        ...prev,
        mapped: data.reduce(
          (obj, item) => ({
            ...obj,
            [item.id]: item.attributes,
          }),
          {}
        ),
      }))
      setTotalCount({
        mapped: meta?.mapped.number,
        unMapped: meta?.unmapped.number,
      })
      setMappingsTable(
        data.map(item => {
          const { data } = item.relationships.topic

          return {
            id: item.id,
            displayName: data.attributes.displayName,

            mapTo: item.attributes.mapTo,
          }
        })
      )
    },
  })
  const { isLoading, run: fetchUserEngagementsMapping } = state

  useEffect(() => {
    if (isMounted.current) {
      resetToFirstPage()
    } else {
      isMounted.current = true
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [searchTerm])

  useEffect(() => {
    pageSize > 0 &&
      fetchUserEngagementsMapping({
        locationUuid: location,
        searchTerm,
        mapped: false,
        pageNumber,
        pageSize,
      })
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [pageNumber, searchTerm, eventName, location, refresh, pageSize])

  const isListEmpty = !mappingTable.length
  const providerSelections = Object.values(selections())

  const selectionsWhenIsAllSelectedUndefined =
    providerSelections.some(item => !item.mapTo) ||
    providerSelections.some(item => item.mapTo)

  const selectionWhenIsAllSelectedPresent = isAllSelected().mapTo
    ? providerSelections.some(item => !item.mapTo)
    : providerSelections.some(item => item.mapTo)

  const hasSomeItemSelected =
    isAllSelected().mapTo === undefined
      ? selectionsWhenIsAllSelectedUndefined
      : selectionWhenIsAllSelectedPresent

  return (
    <Root>
      {(isLoading || pageSize === 0) && <ListLoader />}
      {!isLoading && (
        <>
          <MappingsList
            items={mappingTable}
            toggleSelectAll={toggleSelectAll}
            toggleSelectItem={toggleSelectItem}
            isSelectAll={isAllSelected().mapTo}
            isItemSelected={isItemSelected}
            hasSomeItemSelected={hasSomeItemSelected}
            mappingsCount={mappingsCount}
          />
          {totalItems > PAGE_SIZE && !isListEmpty && (
            <PaginationContainer
              eventType={instanceType}
              totalItems={totalItems}
              pageSize={pageSize}
              pageNumber={pageNumber}
              onPageSizeChange={setPageSize}
              onPageNumberChange={setPageNumber}
            />
          )}
        </>
      )}
    </Root>
  )
}
