import { VFC, useEffect, useState, ChangeEvent } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { Select, Value, Option } from '@jifflenow/brightui/select'
import { useEventCities } from 'rest'
import { useDebounce } from 'utils/debounce'
import { SelectCityProps, City } from './types'
import { SelectControl } from './styled-components'
import { selectOverrides } from './overrides'

const messages = defineMessages({
  selectTimezone: {
    defaultMessage: 'Select city name',
    description: 'Placeholder text',
  },
  noResultsMsg: {
    defaultMessage: 'Please enter 3 or more characters to search',
    description: 'Placeholder text',
  },
})

export const SelectCity: VFC<SelectCityProps> = props => {
  const { formatMessage } = useIntl()
  const [city, setCity] = useState<Value>([])
  const [searchText, setSearchText] = useState('')
  const search = useDebounce(searchText)

  const {
    isPending,
    run: searchCityName,
    data: { data: options = [] } = {},
  } = useEventCities()

  useEffect(() => {
    if (!search || search.length < 3) {
      return
    }
    searchCityName({ search })
  }, [search, searchCityName])

  return (
    <SelectControl>
      <Select
        searchable
        overrides={selectOverrides}
        clearable
        value={city}
        options={options}
        disabled={props.disabled ?? false}
        backspaceClearsInputValue
        isLoading={isPending}
        placeholder={
          props.placeholder ?? formatMessage(messages.selectTimezone)
        }
        noResultsMsg={formatMessage(messages.noResultsMsg)}
        filterOptions={(options: City[]): Value =>
          options.map(
            ({ description, placeId }: City): Option => ({
              label: description,
              id: placeId,
            })
          )
        }
        onInputChange={(e: ChangeEvent<HTMLInputElement>) =>
          setSearchText(e.target.value.trim())
        }
        onChange={({ value }: { value: Value }) => {
          setCity(value)
          props.onChange(value[0]?.id as string)
        }}
      />
    </SelectControl>
  )
}
