import { VFC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { Select, Value } from '@jifflenow/brightui/select'
import { Field } from 'ui-components/Field'
import { MappedCISUserEmailProps } from './types'
import { StyledFormControl } from './styled-components'

const messages = defineMessages({
  label: {
    defaultMessage: 'Choose Email to Sync Free/Busy',
    description: 'Field label',
  },
})

export const MappedCISUsersField: VFC<MappedCISUserEmailProps> = ({
  mappedCISUserEmails,
  isDisabled,
}) => {
  const { formatMessage } = useIntl()

  const dropdownOptions = mappedCISUserEmails?.map(id => ({
    label: id,
    id,
  }))

  return (
    <Field<string>
      name="cisEmail"
      parse={value => value[0]?.id}
      format={value => (value ? [{ id: value, label: value }] : [])}
      render={({ input, meta: { touched, error } }) => (
        <StyledFormControl
          label={formatMessage(messages.label)}
          error={touched && error}
        >
          <Select
            {...input}
            id="cisEmail"
            disabled={isDisabled}
            options={dropdownOptions}
            onChange={({ value }: { value: Value[] }) => input.onChange(value)}
          />
        </StyledFormControl>
      )}
    />
  )
}
