import { VFC } from 'react'
import { FormattedMessage } from 'react-intl'
import { Button, KIND } from '@jifflenow/brightui/button'
import { ModalFooter } from '@jifflenow/brightui/modal'
import { FooterProps } from './types'
import { buttonOverrides } from './overrides'

export const Footer: VFC<FooterProps> = ({ onCancel, onDone, hasError }) => (
  <ModalFooter>
    <Button
      kind={KIND.tertiary}
      overrides={buttonOverrides}
      onClick={onCancel}
      data-pendo="profile/add-exception/cancel"
    >
      <FormattedMessage defaultMessage="Cancel" description="Button label" />
    </Button>
    <Button
      overrides={buttonOverrides}
      disabled={hasError}
      onClick={onDone}
      data-pendo="profile/add-exception/done"
    >
      <FormattedMessage defaultMessage="Done" description="Button label" />
    </Button>
  </ModalFooter>
)
