import { VFC } from 'react'
import { Close } from '@jifflenow/brightui/icon'
import { IconButtonProps, IconButton } from '@jifflenow/brightui/button'
import { IconButtonOverrides } from './overrides'

const ImageDelete: VFC<Partial<IconButtonProps>> = props => (
  <IconButton overrides={IconButtonOverrides} size={10} {...props} />
)

export const ImageDeleteButton: VFC<Partial<IconButtonProps>> = props => (
  <ImageDelete type="button" {...props} aria-label="close">
    <Close color="#706E6B" />
  </ImageDelete>
)
