import { Exception, PartitionedExceptions } from '../types'

export const getCombinedExceptions = (data: Partial<Exception>[]) =>
  Object.values(
    data.reduce(
      (acc, curr) => ({
        ...acc,
        [curr.date!]: {
          ...{ available: [], unavailable: [], inverted: [] },
          ...acc[curr.date!],
          ...curr,
        },
      }),
      {} as { [key: string]: Exception }
    )
  ).sort((a, b) => (a.date >= b.date ? 1 : -1))

export const getPartitionedExceptionsHash = (data: Exception[]) =>
  data.reduce(
    (acc, curr) => {
      const { date, available, unavailable } = curr

      if (available.length) {
        acc.available.push({ date, timeRanges: available })
      }

      if (unavailable.length) {
        acc.unavailable.push({ date, timeRanges: unavailable })
      }

      return acc
    },
    { available: [], unavailable: [] } as PartitionedExceptions
  )
