import { VFC } from 'react'
import { Select } from '@jifflenow/brightui/select'
import { useUserMappingsQuery } from 'rest'
import { defineMessages, useIntl } from 'react-intl'
import { Field } from 'react-final-form'
import { ErrorPage } from 'ui-components/ErrorPage'
import { useEventInfo } from '../providers/EventInfoProvider'
import { useCampaign } from '../Common'
import { StyledFormControl } from './styled-components'
import {
  MappingValue,
  UserMappingResponse,
  EngagementTypeMappings,
  EngagementRelationship,
} from './types'
import { eventTypeMapping } from './constants'

const messages = defineMessages({
  canRequest: {
    description: 'Field Label',
    defaultMessage:
      'Can request meetings of the following engagement types for this {eventType}',
  },
  canParticipate: {
    description: 'Field Label',
    defaultMessage:
      'Can participate in meetings of the following engagement types for this {eventType}',
  },
  none: {
    description: 'Field placeholder',
    defaultMessage: 'None',
  },
})

const transformOption = ({
  id,
  attributes,
  relationships,
}: UserMappingResponse['data'][0]): MappingValue => {
  const { canParticipate, canRequest } = attributes as EngagementTypeMappings
  const {
    displayName: name,
  } = (relationships as EngagementRelationship).engagement.data.attributes

  return {
    label: name,
    id,
    canRequest: canRequest!,
    canParticipate: canParticipate!,
  }
}

export const MappedEngagementType: VFC<{
  eventName: string
  location: string
  userId: string
}> = ({ eventName, userId, location }) => {
  const { formatMessage } = useIntl()
  const { campaign } = useCampaign()
  const { subType = campaign?.subType } = useEventInfo().location || {}
  const { data, isLoading, error, refetch } = useUserMappingsQuery({
    path: { eventName, userId, mappingType: 'EngagementMapping' },
    query: {
      page: { number: 1, size: 100 },
      search_term: '',
      location_uuid: location,
      mapped: true,
    },
  })

  const options = data?.data.map(transformOption)

  const canRequestList = options?.filter(i => i.canRequest)

  const canParticiateList = options?.filter(i => i.canParticipate)

  if (error) {
    return <ErrorPage onButtonClick={() => refetch()} />
  }

  return (
    <>
      <Field
        name="userCanRequest"
        render={() => (
          <StyledFormControl
            label={formatMessage(messages.canRequest, {
              eventType: subType ? eventTypeMapping[subType] : '',
            })}
          >
            <Select
              multi
              loading={isLoading}
              value={canRequestList}
              options={canRequestList}
              disabled
              placeholder={formatMessage(messages.none)}
            />
          </StyledFormControl>
        )}
      />
      <Field
        name="userCanParticiate"
        render={() => (
          <StyledFormControl
            label={formatMessage(messages.canParticipate, {
              eventType: subType ? eventTypeMapping[subType] : '',
            })}
          >
            <Select
              multi
              loading={isLoading}
              value={canParticiateList}
              options={canParticiateList}
              disabled
              placeholder={formatMessage(messages.none)}
            />
          </StyledFormControl>
        )}
      />
    </>
  )
}
