import { VFC } from 'react'
import {
  Modal,
  ModalBody,
  ModalFooter,
  ModalHeader,
  SIZE,
} from '@jifflenow/brightui/modal'
import { KIND } from '@jifflenow/brightui/button'
import { FormattedMessage } from 'react-intl'
import { ConfirmationMessage, FooterButton, Note } from './styled-components'
import { RoleChangeDialogProps } from './types'
import { buttonOverrides, modalOverrides } from './overrides'

export const RoleChangeDialog: VFC<RoleChangeDialogProps> = ({
  pendoName = 'confirmation-dialog',
  onClose,
  onCancel,
  onSuccess,
  showMessage,
  showNote,
}) => (
  <Modal
    size={SIZE.medium}
    overrides={modalOverrides}
    closeable={false}
    onClose={onClose}
    isOpen
  >
    <ModalHeader>
      <FormattedMessage
        defaultMessage="Are you sure?"
        description="Conformation Text"
      />
    </ModalHeader>
    <ModalBody>
      {showMessage && (
        <ConfirmationMessage>
          <FormattedMessage
            defaultMessage="This user has one or more other user accounts mapped to them. Updating their role will undo this mapping."
            description="Modal Body Text"
          />
        </ConfirmationMessage>
      )}
      {showNote && (
        <Note>
          <FormattedMessage
            defaultMessage="Please review the mapped engagement types, topics (if enabled) and respective settings after role update."
            description="Modal Body Text"
          />
        </Note>
      )}
    </ModalBody>
    <ModalFooter>
      <FooterButton
        type="button"
        overrides={buttonOverrides}
        onClick={onCancel}
        data-pendo={`${pendoName}/cancel`}
        kind={KIND.tertiary}
      >
        <FormattedMessage defaultMessage="No" description="Button label" />
      </FooterButton>

      <FooterButton
        type="button"
        overrides={buttonOverrides}
        onClick={onSuccess}
        data-pendo={`${pendoName}/submit`}
      >
        <FormattedMessage defaultMessage="Yes" description="Button label" />
      </FooterButton>
    </ModalFooter>
  </Modal>
)
