import { createContext, FC, useContext, useMemo } from 'react'
import { useParams } from 'react-router-dom'
import { components } from 'types/openapi'
import { useFetchUser } from 'rest'
import { ErrorPage } from 'ui-components/ErrorPage'
import { Spinner } from 'ui-components/Spinner'

type UserResponse = components['schemas']['UserResponse']['data']
type UserContext = { data: UserResponse; refetch: () => void }

const Context = createContext<UserContext | undefined>(undefined)

export const ProfileUserProvider: FC = ({ children }) => {
  const { userId: id } = useParams<{ userId: string }>()

  const { isLoading: isPending, error, refetch, data } = useFetchUser(id)

  const value = useMemo(
    () => ({ data: data ? data.data : ({} as UserResponse), refetch }),
    [data, refetch]
  )

  if (error) {
    return <ErrorPage onButtonClick={() => refetch()} />
  }

  return (
    <>
      {!data && isPending && <Spinner />}
      {data && <Context.Provider value={value}>{children}</Context.Provider>}
    </>
  )
}

export const useProfileUser = () => {
  const context = useContext(Context) as UserContext
  if (context === undefined && process.env.NODE_ENV !== 'production') {
    throw new Error('useProfileUser must be used within a ProfileUserProvider')
  }

  return context.data
}

export const useRefetchUser = () => {
  const context = useContext(Context) as UserContext
  if (context === undefined && process.env.NODE_ENV !== 'production') {
    throw new Error('useProfileUser must be used within a ProfileUserProvider')
  }

  return context.refetch
}
