import { VFC, useRef, useState } from 'react'
import { FormattedMessage } from 'react-intl'
import { Button, KIND } from '@jifflenow/brightui/button'
import { Avatar as UserAvatar } from '@jifflenow/brightui/avatar'
import { useProfileUser } from 'components/ProfileDrawer/providers/ProfileUserProvider'
import { showErrorToast } from 'utils/toast'
import { PicContainer, HiddenInput, UploadRoot } from './styled-components'
import { avatarOverrides, changeButtonOverrides } from './overrides'
import { DeleteAvatar } from './DeleteAvatar'
import { EditAvatar } from './EditAvatar'

const allowedExtensions = /(\.jpg|\.jpeg|\.png)$/iu

export const Avatar: VFC = () => {
  const { avatar, firstName, lastName } = useProfileUser().attributes

  const [image, setImage] = useState<Blob | null>()
  const [userAvatar, setUserAvatar] = useState<string | undefined>(avatar)

  const fileInput = useRef<HTMLInputElement>(null)

  const showInvalidImageError = () =>
    showErrorToast(
      <FormattedMessage
        defaultMessage="Supported formats are JPEG and PNG"
        description="Wrong file type error message for avatar image"
      />
    )

  const onFileChange = () => {
    if (
      fileInput.current?.value &&
      !allowedExtensions.test(fileInput.current.value)
    ) {
      showInvalidImageError()
      fileInput.current.value = ''
    } else if (fileInput.current?.files?.length) {
      setImage(fileInput.current?.files[0])
      fileInput.current.value = ''
    }
  }

  return (
    <UploadRoot>
      <PicContainer>
        <UserAvatar
          overrides={avatarOverrides}
          name={`${firstName} ${lastName}`}
          src={userAvatar}
          size="80px"
        />
        {userAvatar && <DeleteAvatar revertAvatar={() => setUserAvatar('')} />}
      </PicContainer>
      <HiddenInput
        type="file"
        name="profilePicture"
        accept=".png, .jpg, .jpeg"
        multiple={false}
        ref={fileInput}
        onChange={onFileChange}
      />
      {image ? (
        <EditAvatar
          image={image}
          onSuccess={image => {
            setUserAvatar(image)
            setImage(null)
          }}
          onClose={() => setImage(null)}
          onCancel={() => setImage(null)}
          onError={() => {
            setImage(null)
            showInvalidImageError()
          }}
        />
      ) : null}
      <Button
        type="button"
        kind={KIND.minimal}
        onClick={() => fileInput.current?.click()}
        overrides={changeButtonOverrides}
        data-pendo="profile/edit/upload-photo"
      >
        <FormattedMessage
          defaultMessage="Change photo"
          description="Button label"
        />
      </Button>
    </UploadRoot>
  )
}
