import ContentLoader from 'react-content-loader'
import { useStyletron } from '@jifflenow/brightui'

export const TextareaLoader = () => {
  const [css, theme] = useStyletron()
  const rootStyle = {
    marginBottom: theme.sizing.scale800,
    width: '100%',
    maxHeight: '102px',
    marginLeft: theme.sizing.scale300,
    marginRight: theme.sizing.scale300,
    [theme.breakpoints.small]: {
      maxWidth: '208px',
    },
  }
  const contentLoaderOptions = {
    className: css(rootStyle),
    height: 102,
    width: 208,
    speed: 2,
    primaryColor: theme.colors.mono200,
    secondaryColor: theme.colors.mono300,
  }

  return (
    <ContentLoader {...contentLoaderOptions} ariaLabel="">
      <rect y="0" width="208" height="102" />
    </ContentLoader>
  )
}
