import { VFC, useState } from 'react'
import { FormattedMessage, useIntl } from 'react-intl'
import { KIND } from '@jifflenow/brightui/button'
import {
  Modal,
  ModalBody,
  ModalFooter,
  ModalHeader,
  ModalButton,
} from '@jifflenow/brightui/modal'
import { weekDays as weekDayNames } from 'utils/messages'
import { Weekdays } from '../Common/weekDayMappings'
import { CopySlotsModalBodyWrapper } from '../styled-components'
import { modalButtonOverrides } from '../overrides'
import { CopyAvailabilityProps, WeekDay } from './types'
import { CopyDaySlotsTo } from './CopyDaySlotsTo'

export const CopyAvailability: VFC<CopyAvailabilityProps> = props => {
  const { formatMessage } = useIntl()
  const {
    sourceWeekDay,
    availabilityInTZ,
    setAvailabilityInTZ,
    ...modalProps
  } = props

  const [targetWeekDays, setTargetWeekDays] = useState<WeekDay[]>([])

  const addCopyDestinationDays = (weekDay: WeekDay) => {
    setTargetWeekDays([...targetWeekDays, weekDay])
  }

  const handleCopySlots = () => {
    if (sourceWeekDay) {
      const { timeRanges } = availabilityInTZ.find(
        ({ weekDay }) => weekDay === sourceWeekDay
      )!

      setAvailabilityInTZ(
        availabilityInTZ.map(availability =>
          targetWeekDays.includes(availability.weekDay)
            ? { ...availability, timeRanges }
            : availability
        )
      )

      modalProps.onClose()
      // setCopyFromDay(null)
      setTargetWeekDays([])
    }
  }

  return (
    <Modal {...modalProps}>
      <ModalHeader>
        <FormattedMessage
          defaultMessage="Copy availability of {day} to"
          description="Header title"
          values={{
            day: formatMessage(
              weekDayNames[
                Weekdays[sourceWeekDay!] as keyof typeof weekDayNames
              ]
            ),
          }}
        />
      </ModalHeader>
      <ModalBody>
        <CopySlotsModalBodyWrapper>
          {availabilityInTZ
            ?.filter(({ weekDay }) => weekDay !== sourceWeekDay)
            .map(({ weekDay }, key) => (
              <CopyDaySlotsTo
                key={key}
                weekDay={weekDay}
                addCopyDestinationDays={addCopyDestinationDays}
              />
            ))}
        </CopySlotsModalBodyWrapper>
      </ModalBody>
      <ModalFooter>
        <ModalButton
          kind={KIND.tertiary}
          overrides={modalButtonOverrides}
          onClick={modalProps.onClose}
          data-pendo="profile/availability/copy-cancel"
        >
          <FormattedMessage
            defaultMessage="Cancel"
            description="Button label"
          />
        </ModalButton>
        <ModalButton
          kind={KIND.primary}
          onClick={handleCopySlots}
          overrides={modalButtonOverrides}
          data-pendo="profile/availability/copy-ok"
        >
          <FormattedMessage defaultMessage="OK" description="Button label" />
        </ModalButton>
      </ModalFooter>
    </Modal>
  )
}
