import { FC, VFC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { useUserIntegrations } from 'rest'
import { ErrorSection } from 'ui-components/ErrorPage'
import { useProfileUser } from '../providers/ProfileUserProvider'
import { Container, Root, Body } from '../Common/styled-components'
import { Header, Footer, Title } from '../Common'
import { SSOIntegration } from './SSOIntegration'
import { IntegrationsListLoader } from './IntegrationsLoader'
import { IntegrationsContainerViewProps, IntegrationsViewProps } from './types'
import { IntegrationsContainer } from './styled-components'
import { Integration } from './Integration'

const messages = defineMessages({
  calendarIntegration: {
    defaultMessage: 'Calendar Integration',
    description: 'Integration type',
  },
  vConIntegration: {
    defaultMessage: 'Video Conference Integration',
    description: 'Integration type',
  },
})

const IntegrationsContainerView: FC<IntegrationsContainerViewProps> = ({
  title,
  children,
}) => (
  <>
    <Title>{title}</Title>
    <IntegrationsContainer>{children}</IntegrationsContainer>
  </>
)

const IntegrationsView: FC<IntegrationsViewProps> = ({ data, children }) => {
  const { formatMessage } = useIntl()

  const {
    ssoIntegration: { userSsoEnabled },
    calendarIntegrations,
    vconIntegrations,
  } = data

  return (
    <Body>
      <Container>
        <SSOIntegration ssoEnabled={userSsoEnabled} />
        {!!calendarIntegrations.length && (
          <IntegrationsContainerView
            title={formatMessage(messages.calendarIntegration)}
          >
            {calendarIntegrations.map(item => (
              <Integration
                {...item}
                key={item.uuid}
                integrationType="calendar"
              />
            ))}
          </IntegrationsContainerView>
        )}
        {!!vconIntegrations.length && (
          <IntegrationsContainerView
            title={formatMessage(messages.vConIntegration)}
          >
            {vconIntegrations.map(item => (
              <Integration {...item} key={item.uuid} integrationType="vcon" />
            ))}
          </IntegrationsContainerView>
        )}
      </Container>
      {children}
    </Body>
  )
}

export const Integrations: VFC<{ onNavigate?: () => void }> = ({
  onNavigate,
}) => {
  const { id: userId } = useProfileUser()
  const { isLoading, data, error, refetch } = useUserIntegrations({
    query: { userBasedAuthAttribs: true },
    path: { userId },
  })

  return (
    <Root>
      <Header />
      {isLoading && <IntegrationsListLoader />}
      {error && <ErrorSection onButtonClick={() => refetch()} />}
      {data && (
        <IntegrationsView data={data.data}>
          <Footer
            onNext={onNavigate}
            hasOnlyNext
            pendoPrefix="profile/integrations"
          />
        </IntegrationsView>
      )}
    </Root>
  )
}
