import { VFC, useState } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { DateTime } from 'luxon'
import { ModalSpinner } from '@jifflenow/brightui/modal-spinner'
import { StatefulTooltip } from '@jifflenow/brightui/tooltip'
import { InfoOutlined } from '@jifflenow/brightui/icon'
import { IconButton } from '@jifflenow/brightui/button'
import { useBulkUserDownload, useConferenceDetail } from 'rest'
import { showErrorToast, showSuccessToast } from 'utils/toast'
import { RefetchIcon } from 'ui-components/icons/RefetchIcon'
import { useProfileUser } from '../providers/ProfileUserProvider'
import { IntegrationAdapter, IntegrationProps } from './types'
import { iconButtonOverrides } from './overrides'
import {
  Container,
  Logo,
  Name,
  Description,
  LastSyncedTime,
  RefetchContainer,
  AuthorizedContainer,
  AuthorizedText,
} from './styled-components'
import {
  GoogleCalendarLogo,
  ZoomLogo,
  MicrosoftOutlookLogo,
  MicrosoftTeamsConferencingLogo,
  WebexLogo,
} from './logos'
import { ConnectButton, DisconnectButton } from './ActionButtons'

const messages = defineMessages({
  calendarIntegrationConnectSuccess: {
    defaultMessage: 'Calendar integration connected successfully.',
    description: 'Success message',
  },
  calendarIntegrationDisconnectSuccess: {
    defaultMessage: 'Calendar integration disconnected successfully.',
    description: 'Success message',
  },
  vconIntegrationConnectSuccess: {
    defaultMessage: 'Video conference integration connected successfully.',
    description: 'Success message',
  },
  vconIntegrationDisconnectSuccess: {
    defaultMessage: 'Video conference integration disconnected successfully.',
    description: 'Success message',
  },
  calendarSettingsDescription: {
    defaultMessage:
      'Connect your {adapterName} to Jifflenow and manage your calendar settings',
    description: 'Calendar settings description',
  },
  vconSettingsDescription: {
    defaultMessage:
      'Automatically include {adapterName} details in Jifflenow meetings',
    description: 'V-Con settings description',
  },
  LastSyncedTime: {
    defaultMessage: 'Last Synced Time',
    description: 'Sync time label',
  },
  fetchFreeBusy: {
    defaultMessage: 'Fetch Free/Busy',
    description: 'Fetch Free/Busy label',
  },
  configurationMessage: {
    defaultMessage: 'Your admin has already integrated with {adapterName}',
    description: 'configuration message for integrations',
  },
  fetchFreeBusySuccess: {
    defaultMessage:
      'Synchronization with external calendar has been initiated. This might take a few minutes.',
    description: 'Fetch Free/busy message',
  },
  connectError: {
    defaultMessage:
      '{adapterName} authorization is unsuccessful. Email in Jifflenow doesnt match with {adapterName} email.',
    description: 'Error message',
  },
  genericError: {
    defaultMessage: 'Something went wrong. Please try again.',
    description: 'Generic error',
  },
})

const adapterNames = defineMessages<IntegrationAdapter>({
  Zoom: {
    defaultMessage: 'Zoom',
    description: 'Integration name',
  },
  Webex: {
    defaultMessage: 'Webex',
    description: 'Integration name',
  },
  MicrosoftTeams: {
    defaultMessage: 'Microsoft Teams Conferencing',
    description: 'Integration name',
  },
  MicrosoftOutlook: {
    defaultMessage: 'Office 365 Calendar',
    description: 'Integration name',
  },
  GoogleCalendar: {
    defaultMessage: 'Google Calendar',
    description: 'Integration name',
  },
})

const logoImages: Record<IntegrationAdapter, string> = {
  Zoom: ZoomLogo,
  Webex: WebexLogo,
  GoogleCalendar: GoogleCalendarLogo,
  MicrosoftOutlook: MicrosoftOutlookLogo,
  MicrosoftTeams: MicrosoftTeamsConferencingLogo,
}

export const Integration: VFC<IntegrationProps> = ({
  adapterName: adapter,
  integrationName,
  userAuthorized,
  lastCalendarSync,
  integrationType,
  userBasedAuth,
}) => {
  const { formatMessage } = useIntl()
  const { id: userUuid } = useProfileUser()

  const [isConnected, setIsConnected] = useState(userAuthorized)
  const [lastSyncTime, setLastSyncTime] = useState(lastCalendarSync)
  const [loading, setLoading] = useState(false)

  const {
    refetch: refetchConference,
    isRefetching: conferenceLoading,
  } = useConferenceDetail(
    {
      path: { conferenceName: integrationName },
      query: { userUuid, userBasedAuthAttribs: true, profile: true },
    },
    {
      enabled: false,
      onSuccess: ({ data }) => {
        isConnected
          ? showSuccessToast(formatMessage(messages.fetchFreeBusySuccess))
          : data.userAuthorized &&
            showSuccessToast(
              formatMessage(
                messages[
                  integrationType === 'calendar'
                    ? 'calendarIntegrationConnectSuccess'
                    : 'vconIntegrationConnectSuccess'
                ]
              )
            )
        setIsConnected(data.userAuthorized)
        setLastSyncTime(data.lastCalendarSync)
      },
      onError: () => {
        showErrorToast(formatMessage(messages.genericError))
      },
    }
  )
  const { refetch: bulkUserRefetch } = useBulkUserDownload(
    {
      path: { userUuid },
    },
    { enabled: false }
  )

  const adapterName = formatMessage(adapterNames[adapter])
  const description = formatMessage(
    messages[
      integrationType === 'calendar'
        ? 'calendarSettingsDescription'
        : 'vconSettingsDescription'
    ],
    { adapterName }
  )

  const fetchFreeBusyIntegration = async () => {
    try {
      setLoading(true)
      await bulkUserRefetch()
      setLoading(false)
      refetchConference()
    } catch (error) {
      setLoading(false)
      showErrorToast(formatMessage(messages.genericError))
    }
  }

  const onAuthFailure = (error: string) => {
    error === 'INTEGRATIONS_EMAIL_MISMATCH' &&
      showErrorToast(formatMessage(messages.connectError, { adapterName }))
  }

  const onDisconnectSuccess = () => {
    showSuccessToast(
      formatMessage(
        messages[
          integrationType === 'calendar'
            ? 'calendarIntegrationDisconnectSuccess'
            : 'vconIntegrationDisconnectSuccess'
        ]
      )
    )
    setIsConnected(false)
  }

  return (
    <>
      {(loading || conferenceLoading) && <ModalSpinner />}
      <Container>
        <Logo src={logoImages[adapter]} />
        <Name>{adapterName}</Name>
        <Description>{description}</Description>
        <RefetchContainer>
          {userBasedAuth ? (
            isConnected ? (
              <DisconnectButton
                integrationName={integrationName}
                onSuccess={onDisconnectSuccess}
              />
            ) : (
              <ConnectButton
                integrationName={integrationName}
                onSuccess={refetchConference}
                onFailure={onAuthFailure}
              />
            )
          ) : (
            <AuthorizedContainer>
              <InfoOutlined />
              <AuthorizedText>
                {formatMessage(messages.configurationMessage, {
                  adapterName,
                })}
              </AuthorizedText>
            </AuthorizedContainer>
          )}

          {integrationType === 'calendar' && userBasedAuth && isConnected && (
            <StatefulTooltip content={formatMessage(messages.fetchFreeBusy)}>
              <IconButton
                size={20}
                overrides={iconButtonOverrides}
                onClick={fetchFreeBusyIntegration}
                data-pendo={`profile/integration-${integrationName}/user-status`}
              >
                <RefetchIcon />
              </IconButton>
            </StatefulTooltip>
          )}
        </RefetchContainer>
        {integrationType === 'calendar' &&
          userBasedAuth &&
          isConnected &&
          lastSyncTime && (
            <LastSyncedTime>{`${formatMessage(
              messages.LastSyncedTime
            )}: ${DateTime.fromISO(lastSyncTime).toFormat(
              'FFF'
            )}`}</LastSyncedTime>
          )}
      </Container>
    </>
  )
}
