import { components } from 'types/openapi'
import { FormFieldProps, Conditions, FieldCondition } from './types'

type Role = components['schemas']['Role']

export const resolveFieldRules = ({
  rules,
  value,
  role,
}: {
  value: string
  rules: { conditions: Conditions[]; name: string; type: string }
  role: Role
}) => {
  const { name, conditions, type } = rules
  const conditionResult: FieldCondition = {
    name,
    show: false,
    optionsToShow: [],
  }
  conditions.forEach((condition: Conditions) => {
    const {
      action,
      operator,
      valuesToCompare,
      valuesToDisplay,
      roleNamesToCheck = [],
    } = condition

    const valueMatched =
      type === 'role'
        ? roleNamesToCheck?.includes(role)
        : valuesToCompare.includes(value)
    const resolveOperator = operator === 'is' ? valueMatched : !valueMatched
    if (resolveOperator && action === 'show') {
      conditionResult.show = true
      conditionResult.optionsToShow = valuesToDisplay
    }
  })
  return conditionResult
}

export const processRulesOnFieldsMount = (
  fields: FormFieldProps[],
  values: Record<string, any>,
  role: Role
) =>
  fields.filter(({ name, rules }) => {
    if (rules?.length && rules[0].source && rules[0].conditions.length) {
      const [{ source, conditions, type }] = rules

      return resolveFieldRules({
        value: values?.[source.split('.')[1]] ?? '',
        rules: { conditions, name, type },
        role,
      }).show
    }

    return true
  })
