import { VFC } from 'react'
import {
  TableBuilder,
  TableBuilderColumn,
} from '@jifflenow/brightui/table-semantic'
import { useMe } from 'providers/MeProvider'
import { MappingValue } from '../types'
import { Name } from './styled-components'
import { MappingTableProps, Role } from './types'
import { CanRequest } from './CanRequest'
import { CanParticipate } from './CanParticipate'
import { RequestAll } from './RequestAll'
import { ParticipateAll } from './ParticipateAll'
import { EmptyMessage } from './EmptyMessage'
import { mappingTableOverrides } from './overrides'

const adminRoles: Role[] = ['CSM', 'MM', 'JMM']
const csmAndMmRoles: Role[] = ['CSM', 'MM']

export const MappingTable: VFC<MappingTableProps> = ({
  items,
  activeTab,
  state,
  dispatch,
  selectedLocation,
  user,
}) => {
  const {
    id: loggedInUserId,
    attributes: { role: companyRole, eventRole },
  } = useMe().data
  const {
    id: selectedUserId,
    attributes: { role: profileUserRole },
  } = user

  const handleCanRequestChange = (item: MappingValue, canRequest: boolean) =>
    dispatch({
      type: 'setCanRequest',
      payload: { activeTab, item: { ...item, canRequest } },
    })

  const handleCanParticipateChange = (
    item: MappingValue,
    canParticipate: boolean
  ) =>
    dispatch({
      type: 'setCanParticipate',
      payload: { activeTab, item: { ...item, canParticipate } },
    })

  const handleRequestAllChange = (requestAll: boolean) =>
    dispatch({
      type: 'requestAll',
      payload: { activeTab, requestAll },
    })

  const handleParticipateAllChange = (participateAll: boolean) =>
    dispatch({
      type: 'participateAll',
      payload: { activeTab, participateAll },
    })

  const canEdit =
    (adminRoles.includes(companyRole) ||
      (!!eventRole && adminRoles.includes(eventRole))) &&
    loggedInUserId !== selectedUserId

  const isCsmOrMm = csmAndMmRoles.includes(profileUserRole)

  const {
    initialItems,
    mappedItems,
    requestAll,
    participateAll,
    mappedCount,
    unMappedCount,
  } = state[activeTab]

  let requestCount = 0,
    participateCount = 0

  for (const [key, value] of mappedItems) {
    const initialItem = initialItems.get(key)
    requestAll === undefined &&
      value.canRequest !== initialItem?.canRequest &&
      requestCount++
    requestAll !== undefined &&
      value.canRequest !== requestAll &&
      requestCount++
    participateAll === undefined &&
      value.canParticipate !== initialItem?.canParticipate &&
      participateCount++
    participateAll !== undefined &&
      value.canParticipate !== participateAll &&
      participateCount++
  }

  const data = items.map(item => {
    const value = mappedItems.get(item.id)

    return value
      ? value
      : {
          ...item,
          canRequest: requestAll ?? item.canRequest,
          canParticipate: participateAll ?? item.canParticipate,
        }
  })

  return (
    <TableBuilder
      data={data}
      emptyMessage={
        <EmptyMessage
          isNoEngagementType={!mappedCount && !unMappedCount}
          selectedLocation={selectedLocation}
          activeTab={activeTab}
        />
      }
      overrides={mappingTableOverrides}
    >
      <TableBuilderColumn>
        {(row: MappingValue) => <Name key={row.id}>{row.name}</Name>}
      </TableBuilderColumn>
      <TableBuilderColumn
        header={
          <RequestAll
            isEmpty={!items.length}
            canEdit={canEdit && !isCsmOrMm}
            requestAll={requestAll}
            onChange={handleRequestAllChange}
            modifiedRequestMappingCount={requestCount}
          />
        }
      >
        {(row: MappingValue) => (
          <CanRequest
            key={row.id}
            checked={row.canRequest}
            onChange={canRequest => handleCanRequestChange(row, canRequest)}
            canEdit={canEdit && !isCsmOrMm}
          />
        )}
      </TableBuilderColumn>
      <TableBuilderColumn
        header={
          <ParticipateAll
            isEmpty={!items.length}
            canEdit={canEdit}
            participateAll={participateAll}
            onChange={handleParticipateAllChange}
            modifiedParticipateMappingCount={participateCount}
          />
        }
      >
        {(row: MappingValue) => (
          <CanParticipate
            key={row.id}
            checked={row.canParticipate}
            canEdit={canEdit}
            onChange={canParticipate =>
              handleCanParticipateChange(row, canParticipate)
            }
          />
        )}
      </TableBuilderColumn>
    </TableBuilder>
  )
}
