import { FC, useMemo, VFC } from 'react'
import { useIntl, defineMessages, FormattedMessage } from 'react-intl'
import { CopyToClipboard } from 'react-copy-to-clipboard'
import { Table, DIVIDER } from '@jifflenow/brightui/table-semantic'
import { Avatar as UserAvatar } from '@jifflenow/brightui/avatar'
import { Tag, VARIANT } from '@jifflenow/brightui/tag'
import { Button, KIND, IconButton } from '@jifflenow/brightui/button'
import { CopyOutlined } from '@jifflenow/brightui/icon'
import { useMe } from 'providers/MeProvider'
import { useAppSource, AppSource } from 'providers/AppSourceProvider'
import { roles } from 'utils/messages'
import { useMediaQuery } from 'utils/useMediaQuery'
import { FooterContainer } from 'ui-components/Drawer'
import { components } from 'types/openapi'
import { showSuccessToast } from 'utils/toast'
import { processRulesOnFieldsMount } from 'components/CustomFormFields/utils'
import { FormFieldProps } from 'components/CustomFormFields'
import { transformField } from '../EditProfile/transformFields'
import { ProfileTitle } from '../ProfileTitle'
import { Container } from '../../Common/styled-components'
import { Consents } from '../Consents'
import { standardFieldsToDisplay, UserStandardFormFieldInfo } from '../types'
import { ViewProfileProps } from './types'
import {
  tableOverrides,
  tagOverrides,
  avatarOverrides,
  editButtonOverride,
  copyButtonOverrides,
} from './overrides'
import {
  AvatarImg,
  AvatarRoot,
  PersonalLinkRoot,
  PersonalLinkLabel,
  PersonalLink,
  CopyRoot,
  CopyButtonRoot,
  RichTextWrapper,
} from './styled-components'

type CustomForm = components['schemas']['CustomFormFieldInfo']

const messages = defineMessages({
  altAvatar: {
    defaultMessage: 'User avatar',
    description: 'Alt-text',
  },
  role: {
    defaultMessage: 'Company Role',
    description: 'Field label',
  },
  editButtonLabel: {
    defaultMessage: 'Edit Profile',
    description: 'Button label',
  },
  personalLinkLabel: {
    defaultMessage: 'Personal Meeting Link',
    description: 'Field label',
  },
  copiedLabel: {
    defaultMessage: 'Copied',
    description: 'Field label',
  },
  labelForEA: {
    defaultMessage: 'Managed Attendees at Company',
    description: 'Field label',
  },
  labelForEX: {
    defaultMessage: 'Executive Admins at Company',
    description: 'Field label',
  },
  createInstance: {
    defaultMessage: 'Can request a new instance',
    description: 'Field label',
  },
  No: {
    defaultMessage: 'No',
    description: 'Disabled',
  },
  Yes: {
    defaultMessage: 'Yes',
    description: 'Enabled',
  },
  canManageSettingsLabel: {
    defaultMessage: 'Can manage settings',
    description: 'Field label',
  },
})

const customInputFields: CustomForm['widget'][] = [
  'textfield',
  'numberfield',
  'checkbox',
  'textarea',
  'dropdown',
  'multiselect_dropdown',
]

const EditButton: VFC<{ onClick: () => void }> = ({ onClick }) => {
  const { formatMessage } = useIntl()

  return (
    <Button
      kind={KIND.secondary}
      onClick={onClick}
      overrides={useMediaQuery('medium') ? {} : editButtonOverride}
      data-pendo="profile/view/edit-mode"
    >
      {formatMessage(messages.editButtonLabel)}
    </Button>
  )
}

export const ViewProfile: FC<ViewProfileProps> = props => {
  const { formatMessage } = useIntl()
  const isSourceOutlook = useAppSource().app === AppSource.Outlook
  const { fields, onEditClick } = props
  const user = props.user.attributes
  const {
    id: loggedInUserId,
    attributes: {
      role: loggedInUserRole,
      privileges: { canManageSettings },
    },
  } = useMe().data
  const { id: profileUserId, relationships: relations } = props.user
  const location = new URLSearchParams(window.location.search).get('location')
  const isMyProfile = loggedInUserId === profileUserId

  const transformedFields = fields.map(field => transformField({ field, user }))
  const customFields = transformedFields.reduce((acc, curr) => {
    curr.name.indexOf('customFields.') === 0 && acc.push(curr)
    return acc
  }, [] as FormFieldProps[])

  const filteredCustomFields = processRulesOnFieldsMount(
    customFields,
    user.customFields || {},
    loggedInUserRole
  )

  const userInfo = useMemo(
    // eslint-disable-next-line complexity
    () => [
      ...standardFieldsToDisplay
        .filter(key =>
          fields.find(
            ({ type, attributes }) =>
              (type === 'InternalUserStandardForm' ||
                type === 'ExternalUserStandardForm') &&
              attributes.fieldName === key &&
              (attributes.fieldInfo as UserStandardFormFieldInfo).show
          )
        )
        .map(key => {
          const value = user[key]

          return {
            label: fields.find(field => field.attributes.fieldName === key)
              ?.attributes.displayName,
            value: Array.isArray(value)
              ? value.map((value, index) => (
                  <Tag
                    key={index}
                    closeable={false}
                    variant={VARIANT.outlined}
                    overrides={tagOverrides}
                  >
                    {value}
                  </Tag>
                ))
              : value,
          }
        }),
      ...(isSourceOutlook
        ? []
        : user.userType === 'internal'
        ? [
            {
              label: formatMessage(messages.role),
              value: formatMessage(roles[user.role]),
            },
            ...((user.role === 'EA' || user.role === 'EX') && !location
              ? [
                  {
                    label: formatMessage(
                      messages[user.role === 'EA' ? 'labelForEA' : 'labelForEX']
                    ),
                    value: relations?.mappedUsers?.map(
                      ({ attributes: { firstName, lastName } }, index) => (
                        <Tag
                          key={index}
                          closeable={false}
                          variant={VARIANT.outlined}
                          overrides={tagOverrides}
                        >
                          {firstName}&nbsp;{lastName}
                        </Tag>
                      )
                    ),
                  },
                ]
              : []),
          ]
        : [
            {
              label: formatMessage(messages.role),
              value: formatMessage(roles[user.role]),
            },
          ]),
      ...(loggedInUserRole === 'CSM' &&
      user.role === 'MM' &&
      user.canRequestNewInstance
        ? [
            {
              label: formatMessage(messages.createInstance),
              value: user.canCreateEvent
                ? formatMessage(messages.Yes)
                : formatMessage(messages.No),
            },
          ]
        : []),
      ...(user.userType === 'internal' &&
      user.role !== 'CSM' &&
      (loggedInUserRole === 'CSM' ||
        (canManageSettings && (isMyProfile || loggedInUserRole === 'MM')))
        ? [
            {
              label: formatMessage(messages.canManageSettingsLabel),
              value: formatMessage(
                user.canManageSettings ? messages.Yes : messages.No
              ),
            },
          ]
        : []),
      ...fields
        .filter(({ attributes: { fieldName } }) =>
          filteredCustomFields.find(
            ({ name }) => name.indexOf(fieldName) !== -1
          )
        )
        .filter(({ type, attributes }) => {
          const { widget, hidden } = attributes.fieldInfo as CustomForm

          return (
            (type === 'InternalUserCustomForm' ||
              type === 'ExternalUserCustomForm') &&
            customInputFields.includes(widget) &&
            !hidden
          )
        })
        .map(({ attributes }) => {
          const { label, widget } = attributes.fieldInfo as CustomForm
          const value = user.customFields?.[attributes.fieldName]

          return {
            label,
            value: value ? (
              Array.isArray(value) ? (
                value.map((val, index) => (
                  <Tag
                    key={index}
                    closeable={false}
                    variant={VARIANT.outlined}
                    overrides={tagOverrides}
                  >
                    {val}
                  </Tag>
                ))
              ) : widget === ('textarea' as CustomForm['widget']) ? (
                <RichTextWrapper
                  dangerouslySetInnerHTML={{
                    __html: value,
                  }}
                />
              ) : (
                value
              )
            ) : (
              '--'
            ),
          }
        }),
    ],
    [
      formatMessage,
      user,
      isSourceOutlook,
      location,
      relations?.mappedUsers,
      loggedInUserRole,
      isMyProfile,
      canManageSettings,
      fields,
      filteredCustomFields,
    ]
  )

  const handleClick = () =>
    showSuccessToast(
      <FormattedMessage
        defaultMessage="The link is copied to clipboard and is ready to be shared."
        description="Personal link copy success message"
      />
    )

  return (
    <>
      <Container>
        {useMediaQuery('medium') ? (
          <ProfileTitle>
            <EditButton onClick={onEditClick} />
          </ProfileTitle>
        ) : null}
        <AvatarRoot>
          {user.avatar ? (
            <AvatarImg
              src={user.avatar}
              alt={formatMessage(messages.altAvatar)}
            />
          ) : (
            <UserAvatar
              overrides={avatarOverrides}
              name={`${user.firstName} ${user.lastName}`}
              size="80px"
            />
          )}
          {user.personalLink && user.userType === 'internal' && (
            <PersonalLinkRoot>
              <PersonalLinkLabel>
                {formatMessage(messages.personalLinkLabel)}
              </PersonalLinkLabel>
              <CopyRoot>
                <PersonalLink>{user.personalLink}</PersonalLink>
                <CopyButtonRoot>
                  <CopyToClipboard text={user.personalLink}>
                    <IconButton
                      type="button"
                      size={16}
                      overrides={copyButtonOverrides}
                      onClick={handleClick}
                    >
                      <CopyOutlined />
                    </IconButton>
                  </CopyToClipboard>
                </CopyButtonRoot>
              </CopyRoot>
            </PersonalLinkRoot>
          )}
        </AvatarRoot>

        <Table
          /**
           * Below columns prop is required to render the table and
           * it's hidden form the overrides as there is no need to
           * render the table header as per the requirements.
           */
          columns={['', '']}
          data={userInfo.map(({ label, value }) => [label, value])}
          overrides={tableOverrides}
          horizontalScrollWidth="420px"
          divider={DIVIDER.clean}
        />
        {isMyProfile && (
          <Consents
            consent={user.selfConsentMessage}
            optOut={user.optOutMessage}
          />
        )}
      </Container>
      {useMediaQuery('medium') ? null : (
        <FooterContainer>
          <EditButton onClick={onEditClick} />
        </FooterContainer>
      )}
    </>
  )
}
