import { ChangeEvent, VFC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { Checkbox } from '@jifflenow/brightui/checkbox'
import { RequestAllCheckboxProps, RequestAllProps } from './types'
import { EmptyHeaderLabel } from './styled-components'

const messages = defineMessages({
  requestMeetingLabel: {
    defaultMessage: 'Request meeting',
    description: 'Column header',
  },
})

const RequestAllCheckbox: VFC<RequestAllCheckboxProps> = ({
  canEdit,
  requestAll,
  modifiedRequestMappingCount,
  onChange,
}) => {
  const { formatMessage } = useIntl()

  return (
    <Checkbox
      checked={requestAll}
      isIndeterminate={modifiedRequestMappingCount > 0}
      onChange={({ target: { checked } }: ChangeEvent<HTMLInputElement>) =>
        onChange(checked)
      }
      disabled={!canEdit}
    >
      {formatMessage(messages.requestMeetingLabel)}
    </Checkbox>
  )
}

const EmptyRequestAll = () => {
  const { formatMessage } = useIntl()

  return (
    <EmptyHeaderLabel>
      {formatMessage(messages.requestMeetingLabel)}
    </EmptyHeaderLabel>
  )
}

export const RequestAll: VFC<RequestAllProps> = ({ isEmpty, ...rest }) =>
  isEmpty ? <EmptyRequestAll /> : <RequestAllCheckbox {...rest} />
