import { useCallback, VFC } from 'react'
import { useIntl, defineMessages } from 'react-intl'
import { Field } from 'react-final-form'
import { Toggle } from '@jifflenow/brightui/toggle'
import { useMe } from 'providers/MeProvider'
import { useProfileUser } from '../providers/ProfileUserProvider'
import { useEventInfo } from '../providers/EventInfoProvider'
import { useCampaign } from '../Common'
import { CanManageSettingsProps } from './types'
import { FieldLabel, ToggleRoot, ToggleWrapper } from './styled-components'
import { eventTypeMapping } from './constants'

const messages = defineMessages({
  canManageSelfServeSettings: {
    defaultMessage: 'Can manage settings for this {eventType}',
    description: 'Field Label',
  },
})

export const CanManageSettings: VFC<CanManageSettingsProps> = ({
  settings,
}) => {
  const { formatMessage } = useIntl()
  const {
    attributes: {
      role: companyRole,
      privileges: { canManageSettings },
    },
    id: loggedInUserId,
  } = useMe().data
  const {
    locationSpecificDetails: { canManageSelfServeSettings = false } = {},
    eventRole: { name: eventRole = '' } = {},
  } = settings ?? {}
  const {
    id: profileUserId,
    attributes: { role: profileUserRole },
  } = useProfileUser()
  const { campaign } = useCampaign()
  const { subType = campaign?.subType } = useEventInfo().location || {}

  const label = formatMessage(messages.canManageSelfServeSettings, {
    eventType: subType ? eventTypeMapping[subType] : '',
  })

  const isSelfProfile = loggedInUserId === profileUserId

  const Checkbox = useCallback(
    () => (
      <Field
        type="checkbox"
        name="canManageSelfServeSettings"
        render={({ input, meta: { error, submitError } }) => (
          <ToggleRoot $error={submitError || error}>
            <FieldLabel>{label}</FieldLabel>
            <ToggleWrapper>
              <Toggle {...input} ariaLabel={label} disabled={isSelfProfile} />
            </ToggleWrapper>
          </ToggleRoot>
        )}
      />
    ),
    [isSelfProfile, label]
  )

  return profileUserRole !== 'CSM' ? (
    isSelfProfile ? (
      eventRole === 'MM' && canManageSelfServeSettings ? (
        <Checkbox />
      ) : null
    ) : (companyRole === 'MM' &&
        canManageSettings &&
        eventRole === 'MM' &&
        canManageSelfServeSettings) ||
      companyRole === 'CSM' ? (
      <Checkbox />
    ) : null
  ) : null
}
