import { FC } from 'react'
import { Input } from '@jifflenow/brightui/input'
import { Field, Validation } from 'ui-components/Field'
import { TextFieldProps } from '../types'
import { FormControl } from './styled-components'

export const TextField: FC<TextFieldProps> = props => {
  const { name, type, placeholder, label, disabled } = props

  return (
    <Field<string | number>
      name={name}
      validations={
        type === 'email'
          ? [...props.validations, Validation.EMAIL]
          : props.validations
      }
      // Without below identify fn, If you change a form value to '', Final Form will set the value in its state to undefined.
      // More Info here : https://github.com/final-form/final-form/blob/main/docs/faq.md#why-does--final-form-set-my--field-value-to-undefined
      // and here : https://github.com/final-form/react-final-form/issues/130#issuecomment-425482365
      // TODO: Fix needed for custom field dirty check. i.e For a new custom field, if user enters a value and deletes, the form remains dirty.
      parse={value => value}
      render={({ input, meta }) => (
        <FormControl label={label} error={meta.touched && meta.error}>
          <Input
            {...input}
            id={name}
            type={type}
            placeholder={placeholder}
            disabled={disabled}
          />
        </FormControl>
      )}
    />
  )
}
