import { useState } from 'react'
import { DateTime } from 'luxon'
import { useEventTypes, useUserMappedCampaignsV2 } from 'rest'
import { components } from 'types/openapi'
import { useEventName } from '../providers/EventNameProvider'
import { useProfileUser } from '../providers/ProfileUserProvider'
import { useEventInfo } from '../providers/EventInfoProvider'
import { UseEventTypeAndLocationProps } from './types'
import { subTypeToEventTypeMapping } from './constants'

type EventType = components['schemas']['EventType']

const currentDate = DateTime.local()

export const useEventTypeAndLocation = (): UseEventTypeAndLocationProps => {
  const eventType = useEventName().eventType
  const { subType } = useEventInfo().location || {}

  const [selectedEventTypes, setSelectedEventTypes] = useState<EventType[]>([])

  const userId = useProfileUser().id
  const {
    isLoading: eventTypesLoading,
    error: eventTypesError,
    refetch: refetchEventTypes,
    data: { data: eventTypes = [] } = {},
  } = useEventTypes({ enabled: eventType !== 'Emeet' })

  const {
    isLoading: locationsLoading,
    error: locationsError,
    refetch: refetchLocations,
    data: { data: locations = [] } = {},
  } = useUserMappedCampaignsV2(
    {
      path: { userId },
      query: {
        'filter[dateInterval][]': [
          `${currentDate.toISODate()}--${currentDate
            .plus({ years: 2 })
            .toISODate()}`,
        ],
        'filter[eventType][]':
          eventType === 'Emeet'
            ? ['Emeet']
            : selectedEventTypes.length
            ? selectedEventTypes
            : subType
            ? [subTypeToEventTypeMapping[subType]]
            : eventTypes.filter(type => type !== 'Emeet'),
      },
    },
    {
      enabled:
        eventType === 'Emeet' ||
        !!selectedEventTypes.length ||
        !!eventTypes.length,
    }
  )

  return {
    isLoading: eventTypesLoading || locationsLoading,
    isError: eventTypesError || locationsError,
    refetch: () => (eventTypesError ? refetchEventTypes() : refetchLocations()),
    eventTypes:
      eventType !== 'Emeet'
        ? eventTypes.filter(type => type !== 'Emeet')
        : ['Emeet'],
    selectedEventTypes,
    setSelectedEventTypes,
    locations,
  }
}
