import { VFC, useState } from 'react'
import { FormattedMessage } from 'react-intl'
import { Button, KIND } from '@jifflenow/brightui/button'
import { Root, Title } from './styled-components'
import { changeButtonOverrides } from './overrides'
import { SelectCityModal } from './SelectCityModal'
import { TimeZoneChangeProps } from './types'

export const TimeZoneChange: VFC<TimeZoneChangeProps> = ({
  canChangeTimeZone = true,
  ...restProps
}) => {
  const [isOpen, setIsOpen] = useState(false)

  return (
    <>
      <Root>
        <Title>{restProps.timeZone.timeZoneName}</Title>
        {canChangeTimeZone && (
          <Button
            type="button"
            onClick={() => setIsOpen(true)}
            overrides={changeButtonOverrides}
            kind={KIND.minimal}
          >
            <FormattedMessage
              defaultMessage="Change"
              description="Button label"
            />
          </Button>
        )}
      </Root>
      {isOpen && (
        <SelectCityModal
          {...restProps}
          isOpen={isOpen}
          onClose={() => setIsOpen(false)}
        />
      )}
    </>
  )
}
