import { VFC, useState, useEffect } from 'react'
import { useLocations as useCampaigns } from 'rest'
import { KIND, Pagination } from '@jifflenow/brightui/pagination'
import { defineMessages, useIntl } from 'react-intl'
import { AppSource, useAppSource } from 'providers/AppSourceProvider'
import { EventsLoader, LayoutStyle } from 'components/Events'
import { useDrawer } from 'ui-components/Drawer'
import { useWindowSize } from 'utils/useWindowSize'
import { sendMessage, Message } from 'utils/messageDispatcher'
import { Container, Footer, Header, PaginationWrapper } from '../Common'
import { Root, Body } from '../Common/styled-components'
import { useEventName } from '../providers/EventNameProvider'
import { useProfileUser } from '../providers/ProfileUserProvider'
import { ListActions, Filters } from './Actions'
import {
  TitleActionsContainer,
  Title,
  FiltersContainer,
  GridContainer,
  GridWrapper,
} from './styled-components'
import { ItemsGrid } from './Item'
import {
  FilterTypes,
  SortType,
  CampaignsType,
  CampaignsProps,
  FilterType,
} from './types'

const messages = defineMessages({
  genericError: {
    defaultMessage: 'Something is not right. Please retry.',
    description: 'Error message',
  },
  campaignTitle: {
    defaultMessage: 'Events',
    description: 'Page title',
  },
})

const PAGE_SIZE = 8

export const Campaigns: VFC<CampaignsProps> = ({ onNavigate, isLastItem }) => {
  const { formatMessage } = useIntl()
  const { closeDrawer } = useDrawer()
  const { app, platform } = useAppSource()
  const isSourceOutlook = app === AppSource.Outlook

  const [pageNumber, setPageNumber] = useState<number>(1)
  const [totalItems, setTotalItems] = useState<number>(0)
  const [searchTerm, setSearchTerm] = useState<string>('')
  const [sort, setSort] = useState<SortType>('startDate')
  const [filters, setFilters] = useState<FilterTypes>({
    types: [],
    statuses: ['Active'],
    modes: ['Live'],
    dateInterval: '',
    cities: [],
    categories: [],
  })
  const [campaignList, setCampaignList] = useState<CampaignsType>()

  const eventType = useEventName().eventType
  const user = useProfileUser()

  useEffect(() => {
    setPageNumber(1)
  }, [eventType])

  const state = useCampaigns({
    userId: user.id,
    eventType:
      eventType === 'Emeet'
        ? ['Emeet']
        : filters.types.length
        ? filters.types
        : ['TradeShow', 'OngoingSales', 'StaffScheduling'],
    onResolve: locationData => {
      const { page } = locationData.meta

      setTotalItems(page.total)
      setCampaignList(locationData)
    },
  })
  const { run: fetchCampaigns, isPending } = state

  useEffect(() => {
    fetchCampaigns(
      {
        ...filters,
        // Omit archived events from the response by passing all statuses except Archived when no status filter is applied
        ...(filters.statuses.length
          ? {}
          : { statuses: ['Active', 'Past', 'Requested'] }),

        sort,
      },
      { pageNumber, pageSize: PAGE_SIZE, searchTerm: searchTerm.trim() }
    )
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [pageNumber, searchTerm, sort, JSON.stringify(filters)])

  const { windowWidth } = useWindowSize()

  const handleDone = () => {
    isSourceOutlook ? sendMessage(Message.CLOSE_PROFILE) : closeDrawer()
  }

  const onChangeHandler = (filter: FilterType) => {
    setFilters(prevFilters => ({
      ...prevFilters,
      ...filter,
    }))
    setPageNumber(1)
  }

  return (
    <Root>
      <Header />
      <Body>
        <Container>
          <TitleActionsContainer>
            <Title>
              {eventType ? 'eMeet' : formatMessage(messages.campaignTitle)}
            </Title>

            <ListActions
              onSearch={(e: any) => {
                setSearchTerm(e.target.value)
                setPageNumber(1)
              }}
              searchTerm={searchTerm}
              onSort={(sortType: SortType) => {
                setSort(sortType)
                setPageNumber(1)
              }}
              sort={sort}
            />
          </TitleActionsContainer>
          <FiltersContainer>
            <Filters
              filters={filters}
              onChange={onChangeHandler}
              eventType={eventType}
            />
          </FiltersContainer>

          {isPending ? (
            <EventsLoader layout={LayoutStyle.Grid} />
          ) : (
            campaignList && (
              <GridContainer>
                <GridWrapper $isFullMode={totalItems < 4 && windowWidth > 1660}>
                  <ItemsGrid {...campaignList} eventType={eventType} />
                </GridWrapper>
              </GridContainer>
            )
          )}
        </Container>
        {totalItems && totalItems > PAGE_SIZE ? (
          <PaginationWrapper>
            <Pagination
              currentPageNumber={pageNumber}
              currentPageSize={PAGE_SIZE}
              kind={KIND.light}
              totalItems={totalItems}
              onPageNumberChange={nextPage => {
                setPageNumber(Math.min(Math.max(nextPage, 1), totalItems))
              }}
            />
          </PaginationWrapper>
        ) : null}
        {isLastItem && platform === 'mobile' ? null : (
          <Footer
            hasOnlyNext
            isLastItem={isLastItem}
            onNext={onNavigate}
            onDone={handleDone}
            pendoPrefix="profile/campaigns"
          />
        )}
      </Body>
    </Root>
  )
}
