import { FC } from 'react'
import { Value } from '@jifflenow/brightui/select'
import { Validation as FieldValidation } from 'ui-components/Field'
import { components } from 'types/openapi'
import { FieldTypes } from './FormFields'

export type CustomFormFieldsProps = {
  fields: FormFieldProps[]
}

export type FieldTypeToFormFieldMappings = {
  [FieldTypes.TEXTFIELD]: FC<TextFieldProps>
  [FieldTypes.TEXTAREA]: FC<TextFieldProps>
  [FieldTypes.NUMBERFIELD]: FC<TextFieldProps>
  [FieldTypes.CHECKBOX]: FC<CheckboxFieldProps>
  [FieldTypes.DROPDOWN]: FC<SelectFieldProps>
  [FieldTypes.MULTISELECT_DROPDOWN]: FC<MultiSelectFieldProps>
  [FieldTypes.HEADER]: FC<CustomHeaderProps>
  [FieldTypes.PAGE_BREAK]: FC<CustomHeaderProps>
}

export enum InputType {
  TEXT = 'text',
  NUMBER = 'number',
  EMAIL = 'email',
  HIDDEN = 'hidden',
}

export interface FormFieldProps {
  name: string
  label: string
  type?: InputType
  fieldType: FieldTypes
  helpText?: string
  hidden?: boolean
  disabled?: boolean
  rules?: Rules[]
  validations?: Validations
}

interface FieldProps extends Omit<FormFieldProps, 'validations'> {
  validations: FieldValidation[]
}

export interface TextFieldProps extends FieldProps {
  placeholder?: string
  value?: string
}

export interface CheckboxFieldProps extends FieldProps {
  checked?: boolean
}

export interface SelectFieldProps extends FieldProps {
  creatable?: boolean
  clearable?: boolean
  options?: string[]
  value?: string
  handleRules?: (args: { name: string; value: Value }) => void
}

export interface MultiSelectFieldProps
  extends Omit<SelectFieldProps, 'handleRules' | 'value'> {
  value?: string[]
  multi?: boolean
}

export interface FieldCondition {
  name: string
  show: boolean
  optionsToShow: string[]
}

type Roles = components['schemas']['Role']
export interface Conditions {
  action: 'show' | 'hide'
  operator: 'is' | 'is_not'
  valuesToCompare: string[]
  valuesToDisplay: string[]
  roleNamesToCheck?: Roles[]
}

export interface Rules {
  conditions: Conditions[]
  source: string
  type: string
}

export interface SourceBasedRules {
  name: string
  source: string
  conditions: Conditions[]
  type: string
}

export interface Validations {
  required: boolean
  minLength?: number
  maxLength?: number
  email?: boolean
}

export interface CustomHeaderProps extends Pick<FormFieldProps, 'hidden'> {
  label?: string
}
