import { VFC } from 'react'
import { DateTime, Interval } from 'luxon'
import { defineMessages, useIntl } from 'react-intl'
import { DateFilter } from '@jifflenow/brightui/filter'
import { getJSDate } from '../DateTimeConversions'
import { DateLabel } from './styled-components'

const messages = defineMessages({
  dateRange: {
    defaultMessage: 'Date Range',
    description: 'filter label',
  },
})

type FilterProps = {
  dateRange: string[]
  onChange: (dateRange: string[]) => void
  maxDate: string
  minDate: string
}

const toISODate = (date: Date) => DateTime.fromJSDate(date).toISODate()

export const DateRangeFilter: VFC<FilterProps> = ({
  dateRange,
  onChange,
  minDate,
  maxDate,
}) => {
  const { formatMessage } = useIntl()

  const getDate = (date: string) => DateTime.fromISO(date)
  const isFilterNeeded =
    Interval.fromDateTimes(getDate(minDate), getDate(maxDate)).length('days') >
    14

  return isFilterNeeded ? (
    <>
      <DateLabel>{formatMessage(messages.dateRange)}</DateLabel>
      <div>
        <DateFilter
          label={formatMessage(messages.dateRange)}
          onChange={(range: Date[]) =>
            onChange(
              range
                ? [range[0], range[1] || range[0]].map(toISODate)
                : [minDate, maxDate]
            )
          }
          value={dateRange.map(date => getJSDate(date))}
          minDate={getJSDate(minDate)}
          maxDate={getJSDate(maxDate)}
        />
      </div>
    </>
  ) : null
}
