import { FC } from 'react'
import { Validation } from 'ui-components/Field'
import { useRules } from './rules'
import { fieldTypesMappings } from './FormFields'
import { CustomFormFieldsProps, FormFieldProps } from './types'

export const CustomFormFields: FC<CustomFormFieldsProps> = ({
  fields: fieldsList,
}) => {
  const [standardFields, customFields] = fieldsList.reduce(
    (acc, curr) => {
      curr.name.indexOf('customFields.') === 0
        ? acc[1].push(curr)
        : acc[0].push(curr)
      return acc
    },
    [[] as FormFieldProps[], [] as FormFieldProps[]]
  )
  const { fields: filteredCustomFields, handleRules } = useRules(customFields)

  return (
    <>
      {[...standardFields, ...filteredCustomFields].map(
        (field: FormFieldProps, index) => {
          const FormField = fieldTypesMappings[field.fieldType]
          const { required, email } = field.validations ?? {}
          const validations = [
            required && Validation.REQUIRED,
            email && Validation.EMAIL,
          ].filter(Boolean) as Validation[]

          return (
            <FormField
              {...field}
              key={index}
              validations={validations}
              {...(field.fieldType === 'dropdown' ? { handleRules } : null)}
            />
          )
        }
      )}
    </>
  )
}
