import { VFC } from 'react'
import { useQuery } from 'react-query'
import { getTagCloudList } from 'rest'
import { TagCloudField } from './TagCloudField'

export const TagCloud: VFC<{
  eventName: string
  location: string
  isDisabled: boolean
}> = ({ eventName, location, isDisabled }) => {
  const { data: tagCloudList, isSuccess } = useQuery(
    [
      'tagCloudList',
      {
        path: { eventName },
        query: {
          current_location_uuid: location,
        },
      },
    ],
    getTagCloudList
  )

  if (isSuccess) {
    return (
      <div>
        {tagCloudList?.data.tag_cloud_list.map(i => (
          <TagCloudField
            key={i.uuid}
            name={i.uuid}
            label={i.name}
            location={location || ''}
            eventName={eventName}
            isDisabled={isDisabled}
          />
        ))}
      </div>
    )
  }

  return null
}
