import { VFC, useState, useEffect } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { DateTime, Interval } from 'luxon'
import { LinkButton } from '@jifflenow/brightui/button'
import { PlusCircleOutlined } from '@jifflenow/brightui/icon'
import { Calendar } from '@jifflenow/brightui/calendar'
import { RangeTimeSlot } from 'ui-components/RangeTimeSlot'
import { useEventInfo } from 'components/ProfileDrawer/providers/EventInfoProvider'
import { useCampaign } from '../../../Common'
import { useAvailability } from '../../AvailabilityProvider'
import {
  getPlusDate,
  getDateFromJSDate,
  getJSDate,
  getISODateStringFromJSDate,
  getISODateString,
  getISOTimeFromJSDate,
  diffInISODates,
  getPlusISOStringDate,
  getFullMinutes,
  getRefISODateWithTime,
  getEndDateOfDay,
} from '../../DateTimeConversions'
import { ExceptionModalBodyProps } from './types'
import { iconButtonOverrides } from './overrides'
import {
  StyledModalBody,
  SlotsRoot,
  AddExceptionRoot,
  CalendarRoot,
  UnavailableText,
  TimeSlotWrapper,
  HintText,
} from './styled-components'

const messages = defineMessages({
  addSlots: {
    defaultMessage: 'Add available slots',
    description: 'Button text',
  },
  unavailable: {
    defaultMessage: 'Unavailable',
    description: 'Button text',
  },
  hintText: {
    defaultMessage: 'Add new working time slots for the selected date(s)',
    description: 'Hint text',
  },
  overlapError: {
    defaultMessage: 'Time overlap',
    description: 'Error message',
  },
  endTimeLessThanStartTime: {
    defaultMessage: 'Choose an end time later than start time.',
    description: 'Error message',
  },
})

const getZoneAdjustedJSDate = (timeZone: string, date?: DateTime): Date =>
  (date || DateTime.local())
    .setZone(timeZone)
    .setZone('local', { keepLocalTime: true })
    .toJSDate()

const getZoneAdjustedEventDates = (
  startDate: string,
  endDate: string,
  eventTimeZone: string,
  timeZone: string
) => ({
  startDate: getZoneAdjustedJSDate(
    timeZone,
    DateTime.fromISO(startDate).setZone(eventTimeZone, { keepLocalTime: true })
  ),
  endDate: getZoneAdjustedJSDate(
    timeZone,
    DateTime.fromISO(endDate)
      .endOf('day')
      .setZone(eventTimeZone, { keepLocalTime: true })
  ),
})

export const ExceptionModalBody: VFC<ExceptionModalBodyProps> = props => {
  const { formatMessage } = useIntl()
  const { campaign } = useCampaign()
  const { availability } = useAvailability()
  const {
    startDate: startDateISO,
    endDate: endDateISO,
    timeZone: eventTimeZone,
  } = useEventInfo().location || {}

  const isEvent = !!startDateISO && !!endDateISO && !!eventTimeZone
  const {
    calendarDates,
    timeSlots,
    setTimeSlots,
    setCalendarDates,
    timeZone,
  } = props

  const currentDate = getZoneAdjustedJSDate(timeZone.timeZoneId)
  const isLiveEvent =
    isEvent &&
    Interval.fromDateTimes(
      DateTime.fromISO(startDateISO),
      DateTime.fromISO(endDateISO).endOf('day')
    ).contains(DateTime.local())

  const { startDate, endDate } = isEvent
    ? getZoneAdjustedEventDates(
        startDateISO,
        endDateISO,
        eventTimeZone,
        timeZone.timeZoneId
      )
    : ({} as { startDate: Date; endDate: Date })

  const initialCalendarRange = calendarDates.length
    ? [
        getJSDate(calendarDates[0]),
        getJSDate(calendarDates[calendarDates.length - 1]),
      ]
    : isEvent && !isLiveEvent
    ? [startDate, startDate]
    : [currentDate, currentDate]

  const [calendarRange, setCalendarRange] = useState<Date[]>(
    initialCalendarRange
  )

  const lastSlotEndsAt12AM = timeSlots.length
    ? DateTime.fromISO(timeSlots[timeSlots.length - 1].timeSlot[1]).hour === 0
    : false

  useEffect(() => {
    const dates = Array.from(
      new Set(calendarRange.map(date => getISODateStringFromJSDate(date)))
    )
    const [start, end] = dates
    const datesToExtract = dates.length === 2 && diffInISODates(start, end).days

    setCalendarDates(
      datesToExtract
        ? [
            start,
            ...Array.from({ length: datesToExtract - 1 }, (_, index) =>
              getPlusISOStringDate(start, { day: index + 1 })
            ),
            end,
          ]
        : dates
    )
  }, [calendarRange, setCalendarDates])

  useEffect(() => {
    setCalendarRange(
      campaign
        ? [getJSDate(campaign?.startDate), getJSDate(campaign?.endDate)]
        : initialCalendarRange
    )
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [campaign])

  const addNewSlot = () => {
    const preferredStartTime = timeSlots.length
      ? timeSlots[timeSlots.length - 1].timeSlot[1]
      : getRefISODateWithTime('09:00:00')

    const preferredStartDT = DateTime.fromISO(preferredStartTime)

    setTimeSlots([
      ...timeSlots,
      {
        timeSlot: [
          preferredStartTime,
          preferredStartDT.hour === 23
            ? getRefISODateWithTime('24:00:00')
            : timeSlots.length
            ? getPlusDate(preferredStartTime, { hour: 1 })
            : getRefISODateWithTime('17:00:00'),
        ],
      },
    ])
  }

  const checkForOverlaps = () => {
    setTimeSlots(timeSlots =>
      timeSlots.some(({ timeSlot: [start, end] }) => end <= start)
        ? timeSlots
        : timeSlots.map((currentTimeSlot, currentSlotIndex) => {
            const [curStart, curEnd] = currentTimeSlot.timeSlot

            const otherSlots = timeSlots.filter(
              (_, index) => index !== currentSlotIndex
            )

            const hasOverlap = otherSlots.some(
              ({ timeSlot: [start, end] }) => start < curEnd && end > curStart
            )

            return {
              timeSlot: currentTimeSlot.timeSlot,
              ...(hasOverlap
                ? { error: formatMessage(messages.overlapError) }
                : null),
            }
          })
    )
  }

  const changeSlotStartTime = (date: Date, index: number) => {
    const startDateTime = DateTime.fromJSDate(date)
    setTimeSlots(
      timeSlots.map((slot, slotIndex) =>
        slotIndex === index
          ? {
              timeSlot: [
                getDateFromJSDate(date),
                date.valueOf() >= getJSDate(slot.timeSlot[1]).valueOf()
                  ? startDateTime.hour === 23
                    ? DateTime.fromISO(
                        `${startDateTime.toISODate()}T24:00:00`
                      ).toISO()
                    : getPlusDate(startDateTime.toISO(), { hour: 1 })
                  : slot.timeSlot[1],
              ],
            }
          : slot
      )
    )
    checkForOverlaps()
  }

  const changeSlotEndTime = (date: Date, index: number) => {
    const is12AM = getFullMinutes(date.toISOString()) === 0

    setTimeSlots(
      timeSlots.map((slot, slotIndex) => {
        if (slotIndex !== index) {
          return slot
        }

        const start = slot.timeSlot[0]

        const end = is12AM
          ? getEndDateOfDay(date.toISOString())
          : `${getISODateString(start)}T${getISOTimeFromJSDate(date)}`

        return {
          timeSlot: [start, end],
          ...(end <= start
            ? { error: formatMessage(messages.endTimeLessThanStartTime) }
            : null),
        }
      })
    )
    checkForOverlaps()
  }

  const deleteSlot = (index: number) => {
    setTimeSlots(timeSlots.filter((_, slotIndex) => slotIndex !== index))
    checkForOverlaps()
  }

  const changeCalendarRange = (dates: Date[]) => {
    if (dates.length === 1) {
      const currentDate = getISODateStringFromJSDate(dates[0])

      const timeSlots =
        props.exceptions.find(({ date }) => date === currentDate)?.available ??
        availability
          .find(
            ({ weekDay }) => weekDay === DateTime.fromJSDate(dates[0]).weekday
          )
          ?.timeRanges.map(({ start, end }) => [
            `${currentDate}T${start}`,
            `${currentDate}T${end}`,
          ]) ??
        []

      setTimeSlots(timeSlots.map(timeSlot => ({ timeSlot })))
    }
    setCalendarRange(dates)
  }

  return (
    <StyledModalBody>
      <AddExceptionRoot>
        <CalendarRoot>
          <Calendar
            selectRange
            allowPartialRange
            minDate={startDate}
            maxDate={endDate}
            value={calendarRange}
            onChange={changeCalendarRange}
          />
        </CalendarRoot>
        <SlotsRoot>
          <HintText>{formatMessage(messages.hintText)}</HintText>
          <LinkButton
            size={18}
            disabled={lastSlotEndsAt12AM}
            onClick={addNewSlot}
            startEnhancer={PlusCircleOutlined}
            overrides={iconButtonOverrides}
          >
            {formatMessage(messages.addSlots)}
          </LinkButton>
          {timeSlots.length ? (
            timeSlots.map(({ timeSlot: [start, end], error }, slotIndex) => (
              <TimeSlotWrapper key={`${slotIndex}-${start}-${end}`}>
                <RangeTimeSlot
                  start={start}
                  end={end}
                  error={error}
                  onStartChange={date => changeSlotStartTime(date, slotIndex)}
                  onEndChange={date => changeSlotEndTime(date, slotIndex)}
                  onDelete={() => deleteSlot(slotIndex)}
                />
              </TimeSlotWrapper>
            ))
          ) : (
            <UnavailableText>
              {formatMessage(messages.unavailable)}
            </UnavailableText>
          )}
        </SlotsRoot>
      </AddExceptionRoot>
    </StyledModalBody>
  )
}
