import {
  FC,
  Dispatch,
  SetStateAction,
  useState,
  createContext,
  useContext,
  useMemo,
} from 'react'

// TODO: check if both mapped and unmapped providers can be managed by one

type All = {
  mapTo?: boolean
}

type Item = {
  uuid: string
  mapTo?: boolean
}

type ItemsAgainstIDs = {
  [uuid: string]: Item
}

type ContextT<All, Item> = {
  pageNumber: number
  setPageNumber: Dispatch<SetStateAction<number>>
  isAllSelected: () => All
  isUnMappedUuidListEmpty: () => boolean
  toggleSelectAll: (all: All) => void
  toggleSelectItem: (item: Item) => void
  isItemSelected: (uuid: string) => All
  selections: () => ItemsAgainstIDs
  unMappedUuidList: () => Set<string>
  clearAll: () => void
}

const Context = createContext<ContextT<All, Item> | undefined>(undefined)

const useUnMappedTopicsMappedSelection = () => {
  const [pageNumber, setPageNumber] = useState(1)
  const [selectAllInfo, setSelectAllInfo] = useState<All>({})

  const [editedIDs, setEditedIDs] = useState<Set<string>>(new Set())

  const [editedItems, setEditedItems] = useState<ItemsAgainstIDs>({})

  const toggleSelectAll = (all: All) => {
    setSelectAllInfo(items => ({ ...items, ...all }))
    const editedItemsCopy = { ...editedItems }

    Object.keys(editedItemsCopy).forEach(key => {
      editedItemsCopy[key] = { ...editedItemsCopy[key], ...all }
    })

    setEditedItems(editedItemsCopy)
  }

  const toggleSelectItem = (item: Item) => {
    if (editedIDs.has(item.uuid)) {
      const selectionValueCopy = { ...editedItems }
      selectionValueCopy[item.uuid] = {
        ...selectionValueCopy[item.uuid],
        ...item,
      }
      setEditedItems(selectionValueCopy)
    } else {
      setEditedIDs(items => new Set(items).add(item.uuid))
      setEditedItems(items => ({
        ...items,
        [item.uuid]: { ...item },
      }))
    }
  }

  const isAllSelected = () => selectAllInfo

  const isItemSelected = (uuid: string) => editedItems[uuid]

  const selections = () => editedItems

  const isUnMappedUuidListEmpty = () => Array.from(editedIDs).length === 0
  const unMappedUuidList = () => editedIDs

  const clearAll = () => {
    setEditedIDs(new Set())
    setEditedItems({})
    setSelectAllInfo({})
  }

  return useMemo(
    () => ({
      pageNumber,
      setPageNumber,
      isAllSelected,
      toggleSelectAll,
      toggleSelectItem,
      isUnMappedUuidListEmpty,
      isItemSelected,
      selections,
      unMappedUuidList,
      clearAll,
    }),
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [isAllSelected, editedIDs, editedItems, pageNumber]
  )
}

export const UnMappedTopicsSelectionsProvider: FC = props => (
  <Context.Provider value={useUnMappedTopicsMappedSelection()}>
    {props.children}
  </Context.Provider>
)

export const useSelection = () => {
  const context = useContext(Context)
  /* istanbul ignore next */
  if (context === undefined && process.env.NODE_ENV !== 'production') {
    throw new Error(
      'useSelection must be used within a UnMappedTopicsSelectionProvider'
    )
  }

  return context!
}
