import {
  createContext,
  Dispatch,
  FC,
  useContext,
  useMemo,
  useReducer,
} from 'react'
import { Keys } from '../types'

type State = {
  selectedKey: Keys | undefined
  tempSelectedKey: Keys | undefined
}

type Action =
  | { type: 'setSelectedKey'; payload: Keys }
  | { type: 'setTempSelectedKey'; payload: Keys | undefined }

const initialState: State = {
  selectedKey: undefined,
  tempSelectedKey: undefined,
}

const reducer = (state: State, action: Action): State => {
  const { type } = action

  switch (type) {
    case 'setSelectedKey': {
      return {
        ...state,
        selectedKey: action.payload,
      }
    }
    case 'setTempSelectedKey': {
      return {
        ...state,
        tempSelectedKey: action.payload,
      }
    }
    default:
      return state
  }
}

type NavState = {
  state: State
  dispatch: Dispatch<Action>
}

const Context = createContext<NavState>({
  state: initialState,
  dispatch: () => {},
})

export const NavProvider: FC = ({ children }) => {
  const [state, dispatch] = useReducer(reducer, initialState)

  const value = useMemo(() => ({ state, dispatch }), [state])

  return <Context.Provider value={value}>{children}</Context.Provider>
}

export const useNavProvider = () => {
  const context = useContext(Context)
  /* istanbul ignore next */
  if (context === undefined) {
    throw new Error('useNavProvider must be used within a NavProvider')
  }

  return context
}
