import { VFC } from 'react'
import { Select, OnChangeParams, Option } from '@jifflenow/brightui/select'
import { useIntl } from 'react-intl'
import { filterOptionLabel } from 'ui-components/common/messages'
import { StatusText } from './styled-components'
import { campaignFilterOverride } from './overrides'
import { CampaignFilterV2Props, Location, Mode } from './types'

const optionsMapper = ({
  id,
  attributes: { displayName: label, mode },
}: Location) => ({
  id,
  label,
  mode,
})

export const CampaignFilterV2: VFC<CampaignFilterV2Props> = ({
  locations,
  selectedLocations,
  onChange,
  isLoading,
}) => {
  const { formatMessage } = useIntl()
  const options = locations.map(optionsMapper)
  const value = selectedLocations.map(optionsMapper)
  const handleChange = ({ value: [{ id }] }: OnChangeParams) =>
    onChange(locations.filter(({ id: uuid }) => uuid === id))

  const getLabel = ({ option: { label, mode } }: Option) => (
    <>
      {label}
      {mode && (
        <StatusText>
          {formatMessage(filterOptionLabel[mode as Mode])}
        </StatusText>
      )}
    </>
  )

  return (
    <Select
      searchInput
      clearable={false}
      onCloseResetsInput
      onBlurResetsInput
      placeholder="Select"
      isLoading={isLoading}
      options={options}
      value={value}
      getOptionLabel={getLabel}
      onChange={handleChange}
      overrides={campaignFilterOverride}
    />
  )
}
