import { useEffect, useState, VFC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { useDebounce } from 'utils/debounce'
import {
  CampaignFilterV2,
  EventTypesFilterV2,
  FilterContainer,
  Search,
  SearchContainer,
  TitleContainer,
} from '../Common'
import { EventSelectLabel } from '../Common/EventSelectLabel'
import { useEventName } from '../providers/EventNameProvider'
import { EngagementTypesFooter } from './EngagementTypesFooter'
import { Container } from './styled-components'
import { Tabs } from './Tabs'
import { EngagementTypesViewProps, EventType, Location } from './types'
import { useEngagementTypes } from './useEngagementTypes'

const messages = defineMessages({
  title: {
    defaultMessage: 'Engagements Types for',
    description: 'Page title',
  },
})

export const EngagementTypesView: VFC<EngagementTypesViewProps> = ({
  onNavigate,
  isLoading,
  eventTypes,
  selectedEventTypes,
  setSelectedEventTypes,
  locations,
  selectedLocations,
  setSelectedLocations,
  user,
}) => {
  const { formatMessage } = useIntl()
  const eventType = useEventName().eventType
  const [search, setSearch] = useState('')
  const location = new URLSearchParams(window.location.search).get('location')
  const searchText = useDebounce(search)
  const [state, dispatch] = useEngagementTypes()

  const handleEventTypeOnChange = (eventTypes: EventType[]) => {
    dispatch({ type: 'resetValues' })
    setSelectedEventTypes(eventTypes)
  }

  const handleLocationOnChange = (locations: Location[]) => {
    dispatch({ type: 'resetValues' })
    setSelectedLocations(locations)
  }

  useEffect(() => {
    dispatch({ type: 'resetValues' })
  }, [dispatch, searchText, selectedEventTypes])

  return (
    <>
      <Container>
        <TitleContainer>
          <EventSelectLabel title={formatMessage(messages.title)} />
          {!location && (
            <>
              <FilterContainer>
                {eventType !== 'Emeet' && (
                  <EventTypesFilterV2
                    eventTypes={eventTypes}
                    selectedEventTypes={selectedEventTypes}
                    onChange={handleEventTypeOnChange}
                  />
                )}
                <CampaignFilterV2
                  locations={locations}
                  selectedLocations={selectedLocations}
                  onChange={handleLocationOnChange}
                />
              </FilterContainer>
              <SearchContainer>
                <Search search={search} onSearchChange={setSearch} />
              </SearchContainer>
            </>
          )}
        </TitleContainer>
        <Tabs
          search={searchText}
          state={state}
          selectedLocation={selectedLocations[0]}
          dispatch={dispatch}
          user={user}
        />
      </Container>
      {!isLoading && !!selectedLocations.length && (
        <EngagementTypesFooter
          user={user}
          search={searchText}
          state={state}
          selectedLocation={selectedLocations[0]}
          onNavigate={onNavigate}
          dispatch={dispatch}
        />
      )}
    </>
  )
}
