import { VFC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { FlexGrid, FlexGridItem } from '@jifflenow/brightui/flex-grid'
import { Card } from '@jifflenow/brightui/card'
import {
  AspectRatioBox,
  AspectRatioBoxBody,
} from '@jifflenow/brightui/aspect-ratio-box/index'
import {
  flexGridOverridesForNoItems,
  cardOverrides,
  aspectRatioBoxOverrides,
  aspectRatioBoxBodyOverrides,
} from 'components/Events'

const messages = defineMessages({
  noCampaigns: {
    defaultMessage: 'There are no Events.',
    description: 'Message for empty events list',
  },
  noInstance: {
    defaultMessage: 'There are no Instances.',
    description: 'Message for empty events list',
  },
})

export const NoItem: VFC<{ eventType?: string }> = ({ eventType }) => {
  const { formatMessage } = useIntl()

  return (
    <FlexGrid
      flexGridColumnCount={[1, 2, 3, 5]}
      flexGridColumnGap="9px"
      flexGridRowGap="8px"
      overrides={flexGridOverridesForNoItems}
    >
      <FlexGridItem>
        <Card overrides={cardOverrides(false)}>
          <AspectRatioBox
            overrides={aspectRatioBoxOverrides}
            aspectRatio={2.5 / 1}
          >
            <AspectRatioBoxBody overrides={aspectRatioBoxBodyOverrides}>
              {formatMessage(
                eventType === 'Emeet'
                  ? messages.noInstance
                  : messages.noCampaigns
              )}
            </AspectRatioBoxBody>
          </AspectRatioBox>
        </Card>
      </FlexGridItem>
    </FlexGrid>
  )
}
