import { VFC, useImperativeHandle, forwardRef } from 'react'
import ContentLoader from 'react-content-loader'
import { useStyletron } from '@jifflenow/brightui'
import { FlexGrid, FlexGridItem } from '@jifflenow/brightui/flex-grid'

const LIST_CARD_HEIGHT = 110

const LoadingListCard: VFC<{ height: number }> = ({ height }) => {
  const [, theme] = useStyletron()
  const contentLoaderOptions = {
    style: { width: '100%', height: `${height}` },
    width: '1000',
    height: '120',
    viewBox: '0 0 1000 100',
    speed: 2,
    primaryColor: theme.colors.mono200,
    secondaryColor: theme.colors.mono300,
  }

  return (
    <ContentLoader {...contentLoaderOptions}>
      <circle cx="80" cy="50" r="35" />
      <rect x="15" y="90" rx="0" ry="0" width="133" height="40" />
      <circle cx="250" cy="50" r="35" />
      <rect x="185" y="90" rx="0" ry="0" width="133" height="40" />
      <circle cx="430" cy="50" r="35" />
      <rect x="365" y="90" rx="0" ry="0" width="133" height="40" />
    </ContentLoader>
  )
}

export const IntegrationsListLoader = forwardRef((_, ref) => {
  const [, theme] = useStyletron()
  useImperativeHandle(ref, () => ({
    getReactContainerStyle: () => ({
      alignSelf: 'flex-start',
      marginTop: theme.sizing.scale600,
    }),
  }))

  return (
    <FlexGrid
      flexGridColumnCount={1}
      overrides={{
        Block: {
          style: ({ $theme: { sizing } }) => ({
            marginTop: sizing.scale400,
            padding: '10px',
          }),
        },
      }}
    >
      {Array.from({ length: 2 }).map((_, i) => (
        <FlexGridItem key={i}>
          <LoadingListCard height={LIST_CARD_HEIGHT} />
        </FlexGridItem>
      ))}
    </FlexGrid>
  )
})
