import { FC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { SortFilter } from '@jifflenow/brightui/filter'
import { Search } from '@jifflenow/brightui/icon'
import { Input } from '@jifflenow/brightui/input'
import { SortOrder, sortOrders, sortOverrides } from 'components/Events'
import { SortType } from '../types'
import { ListActionsProps } from './types'
import { ListActionsContainer } from './styled-components'

const messages = defineMessages({
  searchLocations: {
    defaultMessage: 'Search',
    description: 'Placeholder text',
  },
  sort: {
    defaultMessage: 'Sort by',
    description: 'Filter Label',
  },
  startDate: {
    defaultMessage: 'Start Date (asc.)',
    description: 'Sort option for event instance date',
  },
  '-startDate': {
    defaultMessage: 'Start Date (desc.)',
    description: 'Sort option for event instance date',
  },
  locationName: {
    defaultMessage: 'Name A-Z',
    description: 'Sort option for event instance name',
  },
  '-locationName': {
    defaultMessage: 'Name Z-A',
    description: 'Sort option for event instance name',
  },
})

export const ListActions: FC<ListActionsProps> = ({
  searchTerm,
  onSearch,
  onSort,
  sort,
}) => {
  const { formatMessage } = useIntl()
  const toFilterFormat = (sortOrder: SortOrder) => ({
    id: sortOrder,
    label: formatMessage(messages[sortOrder]),
  })

  return (
    <ListActionsContainer>
      <SortFilter
        label={`${formatMessage(messages.sort)} ${formatMessage(
          messages[sort]
        )}`}
        onChange={newSort => onSort(newSort[0].id as SortType)}
        options={sortOrders.map(toFilterFormat)}
        overrides={sortOverrides}
        value={[sort].map(toFilterFormat)}
      />

      <Input
        type="search"
        IconAfter={Search}
        placeholder={formatMessage(messages.searchLocations)}
        value={searchTerm}
        onChange={onSearch}
      />
    </ListActionsContainer>
  )
}
