import { VFC } from 'react'
import { defineMessages, FormattedMessage, useIntl } from 'react-intl'
import { Button, KIND } from '@jifflenow/brightui/button'
import { IntegrationsContainer } from './styled-components'
import { SSOIntegrationProps } from './types'
import { Container, LogoContainer, SSOLogo, Name } from './styled-components'
import { SSOLogo as logo } from './logos'

const messages = defineMessages({
  SSOUser: {
    defaultMessage: 'SSO User',
    description: 'SSO label',
  },
  NonSSOUser: {
    defaultMessage: 'Non-SSO User',
    description: 'SSO label',
  },
})

export const SSOIntegration: VFC<SSOIntegrationProps> = ({ ssoEnabled }) => {
  const { formatMessage } = useIntl()

  return (
    <IntegrationsContainer>
      <Container>
        <LogoContainer>
          <SSOLogo src={logo} />
        </LogoContainer>
        <Name>
          <FormattedMessage defaultMessage="SSO Login" description="Title" />
        </Name>
        <Button kind={KIND.primary} disabled>
          {ssoEnabled
            ? formatMessage(messages.SSOUser)
            : formatMessage(messages.NonSSOUser)}
        </Button>
      </Container>
    </IntegrationsContainer>
  )
}
