import { VFC } from 'react'
import { Field } from 'react-final-form'
import { useQuery } from 'react-query'
import { Select, Value, Option } from '@jifflenow/brightui/select'
import { getTagsList } from 'rest'
import { StyledFormControl } from './styled-components'
import { TagItem } from './types'

const optionsMapper = (item: TagItem) => ({
  label: item.name,
  id: item.uuid,
})
export const TagCloudField: VFC<{
  name: string
  label: string
  location: string
  eventName: string
  isDisabled: boolean
}> = ({ name, label, location, eventName, isDisabled }) => {
  const { data: tagsList, isLoading, isSuccess } = useQuery(
    [
      'tagsList',
      {
        path: {
          eventName,
        },
        query: {
          search: '',
          tag_cloud_uuid: name,
          per_page: '500',
          current_location_uuid: location || '',
        },
      },
    ],
    getTagsList
  )

  const optionsList = tagsList ? tagsList?.data?.tags.map(optionsMapper) : []

  return (
    <Field
      name={name}
      parse={value => ({
        tagCloudUuid: name,
        tags: value.map(({ id }: Option) => id),
      })}
      format={value =>
        value?.tags?.length && isSuccess
          ? value.tags.map((val: string) => {
              const tag = tagsList?.data?.tags?.find(({ uuid }) => uuid === val)

              return { label: tag?.name, id: tag?.uuid }
            })
          : []
      }
      render={({ input: { value, onChange }, meta: { touched, error } }) => (
        <StyledFormControl label={label} error={touched && error}>
          <Select
            options={optionsList}
            isLoading={isLoading}
            disabled={isDisabled}
            searchable
            multi
            backspaceClearsInputValue
            onChange={({ value }: { value: Value[] }) => {
              onChange(value)
            }}
            value={value}
          />
        </StyledFormControl>
      )}
    />
  )
}
