import { VFC, useState, useCallback, useEffect, ReactNode } from 'react'
import { DateTime } from 'luxon'
import { defineMessages, useIntl } from 'react-intl'
import { updateUserTimeZone, usePortalV2Client, useQueryClient } from 'rest'
import { ModalSpinner } from '@jifflenow/brightui/modal-spinner'
import { TimeZoneChange } from 'components/TimeZoneChange'
import { EventSubTypes } from 'gql/types/globalTypes'
import { EventDatesLabel } from 'components/ProfileDrawer/Common/EventDatesLabel'
import { Header } from '../Common'
import { Root, Body, Title } from '../Common/styled-components'
import { useProfileUser } from '../providers/ProfileUserProvider'
import { useEventInfo } from '../providers/EventInfoProvider'
import { AvailabilityProvider } from './AvailabilityProvider'
import { AvailabilityTabs } from './AvailabilityTabs'
import { TimeZone } from './types'
import { Container, Note } from './styled-components'
import { CombinedAvailability } from './CombinedAvailabilities'

const messages = defineMessages({
  title: {
    defaultMessage: 'My Availability',
    description: 'Page title',
  },
  titleEEvent: {
    defaultMessage: 'My Availability for',
    description: 'Page title',
  },
  note: {
    defaultMessage:
      '<b>Note:</b> The availability is applicable to all the Jifflenow instances falling on below dates.',
    description: 'Page title',
  },
})

export const Availability: VFC = () => {
  const portalClient = usePortalV2Client()
  const { formatMessage } = useIntl()
  const user = useProfileUser()
  const [showLoader, setShowLoader] = useState(false)
  const queryClient = useQueryClient()
  const { location } = useEventInfo()
  const { subType, timeZone: zone = user.attributes.timeZone } = location || {}

  const prepareClientTimeZone = useCallback(userTimeZone => {
    let currentDate = DateTime.local()
    if (userTimeZone) {
      currentDate = currentDate.setZone(userTimeZone)
    }

    const { offsetNameShort, offsetNameLong, zoneName } = currentDate

    return {
      timeZoneId: zoneName,
      timeZoneName:
        offsetNameLong.startsWith('GMT') || offsetNameLong === 'UTC'
          ? offsetNameLong
          : `${offsetNameShort} | ${offsetNameLong}`,
    }
  }, [])

  const [timeZone, setTimeZone] = useState<TimeZone>(
    prepareClientTimeZone(zone)
  )

  useEffect(() => {
    if (zone) {
      setTimeZone(prepareClientTimeZone(zone))
    }
  }, [setTimeZone, prepareClientTimeZone, zone])

  const handleTimeZoneChange = useCallback(
    async timeZoneData => {
      if (location) {
        setTimeZone(timeZoneData)
        return
      }
      setShowLoader(true)
      try {
        await updateUserTimeZone(
          portalClient,
          { timeZone: timeZoneData.timeZoneId },
          user.id
        )
        setTimeZone(timeZoneData)
        queryClient.invalidateQueries('fetchUser')
      } finally {
        setShowLoader(false)
      }
    },
    [setTimeZone, user.id, portalClient, queryClient, location]
  )

  return (
    <Root>
      {showLoader && <ModalSpinner />}
      <Header />
      <Body>
        <Container>
          <Title>
            {subType === EventSubTypes.Jem ? (
              <EventDatesLabel title={formatMessage(messages.titleEEvent)} />
            ) : (
              formatMessage(messages.title)
            )}
          </Title>
          <TimeZoneChange
            timeZone={timeZone}
            setTimeZone={handleTimeZoneChange}
          />
          {subType === EventSubTypes.Jem ? (
            <>
              <Note>
                {formatMessage(messages.note, {
                  b: (chunks: ReactNode) => <strong>{chunks}</strong>,
                })}
              </Note>
              <CombinedAvailability
                timeZoneId={timeZone.timeZoneId}
                userId={user.id}
              />
            </>
          ) : (
            <AvailabilityProvider
              userId={user.id}
              timeZoneId={timeZone.timeZoneId}
            >
              <AvailabilityTabs timeZone={timeZone} />
            </AvailabilityProvider>
          )}
        </Container>
      </Body>
    </Root>
  )
}
