import { VFC } from 'react'
import { defineMessages, useIntl, FormattedMessage } from 'react-intl'
import {
  Modal,
  ModalBody,
  ModalFooter,
  ModalHeader,
  ModalButton,
  SIZE,
} from '@jifflenow/brightui/modal'
import { showErrorToast, showSuccessToast } from 'utils/toast'
import { KIND } from '@jifflenow/brightui/button'
import { SelectCity } from './SelectCity'
import { useTimeZoneFromCity } from './useTimeZoneFromCity'
import { TimeZoneWrapper } from './styled-components'
import { modalButtonOverrides } from './overrides'
import { SelectCityModalProps, TimeZone } from './types'

const messages = defineMessages({
  successTimeZoneChange: {
    defaultMessage: 'Time zone changed successfully',
    description: 'Success message',
  },

  failureTimeZoneChange: {
    defaultMessage: 'Time zone is not changed',
    description: 'Failure message',
  },
})

export const SelectCityModal: VFC<SelectCityModalProps> = ({
  timeZone,
  setTimeZone,
  ...modalProps
}) => {
  const { formatMessage } = useIntl()
  const { isPending, cityTimeZone, onCityChange } = useTimeZoneFromCity()

  const handleDone = () => {
    modalProps.onClose()
    Promise.resolve(setTimeZone(cityTimeZone as TimeZone))
      .then(() =>
        showSuccessToast(formatMessage(messages.successTimeZoneChange))
      )
      .catch(() =>
        showErrorToast(formatMessage(messages.failureTimeZoneChange))
      )
  }

  return (
    <Modal {...modalProps} size={SIZE.small} autoFocus={false}>
      <ModalHeader>
        <FormattedMessage
          defaultMessage="Select city to change time zone"
          description="Header title"
        />
      </ModalHeader>
      <ModalBody>
        <TimeZoneWrapper>
          <SelectCity onChange={onCityChange} />
        </TimeZoneWrapper>
      </ModalBody>
      <ModalFooter>
        <ModalButton
          kind={KIND.secondary}
          onClick={modalProps.onClose}
          overrides={modalButtonOverrides}
        >
          <FormattedMessage
            defaultMessage="Cancel"
            description="Button label"
          />
        </ModalButton>
        <ModalButton
          kind={KIND.primary}
          onClick={handleDone}
          overrides={modalButtonOverrides}
          disabled={!cityTimeZone || isPending}
        >
          <FormattedMessage
            defaultMessage="Confirm"
            description="Button label"
          />
        </ModalButton>
      </ModalFooter>
    </Modal>
  )
}
