import { FieldTypeToFormFieldMappings } from './types'
import {
  TextField,
  TextAreaField,
  CheckboxField,
  SelectField,
  MultiSelectField,
  Header,
  PageBreak,
} from './Fields'

export enum FieldTypes {
  TEXTFIELD = 'textfield',
  TEXTAREA = 'textarea',
  NUMBERFIELD = 'numberfield',
  CHECKBOX = 'checkbox',
  DROPDOWN = 'dropdown',
  MULTISELECT_DROPDOWN = 'multiselect_dropdown',
  PAGE_BREAK = 'page_break',
  HEADER = 'header',
}

export const fieldTypesMappings: FieldTypeToFormFieldMappings = {
  [FieldTypes.TEXTFIELD]: TextField,
  [FieldTypes.NUMBERFIELD]: TextField,
  [FieldTypes.DROPDOWN]: SelectField,
  [FieldTypes.TEXTAREA]: TextAreaField,
  [FieldTypes.CHECKBOX]: CheckboxField,
  [FieldTypes.MULTISELECT_DROPDOWN]: MultiSelectField,
  [FieldTypes.HEADER]: Header,
  [FieldTypes.PAGE_BREAK]: PageBreak,
}
