import { FC, useState, useMemo, useEffect } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { Config, FORM_ERROR } from 'final-form'
import { Form as FinalForm } from 'react-final-form'
import createFocusDecorator from 'final-form-focus'
import createDecorator from 'final-form-calculate'
import { useQueryClient } from 'react-query'
import { ModalSpinner } from '@jifflenow/brightui/modal-spinner'
import { Option } from '@jifflenow/brightui/select'
import { useMe } from 'providers/MeProvider'
import { AppSource, useAppSource } from 'providers/AppSourceProvider'
import { showErrorToast, showSuccessToast } from 'utils/toast'
import { roles as roleLabel } from 'utils/messages'
import { HttpError, patchUserProfile, usePortalV2Client } from 'rest'
import { useRolesLazyQuery } from 'gql'
import { RolesDisplayNames } from 'gql/types/globalTypes'
import { fetchRoles_fetchRoles } from 'gql/types/fetchRoles'
import { FieldTypes, FormFieldProps } from 'components/CustomFormFields'
import { components } from 'types/openapi'
import {
  Role,
  standardFieldsToDisplay,
  UserStandardFormFieldInfo,
} from '../types'
import { EditProfileProps, FormValues } from './types'
import { transformField } from './transformFields'
import { EditForm } from './EditForm'
import { splitPersonalLink } from './PersonalLinkEdit'

type StaticFormField = Omit<
  FormFieldProps,
  'validations' | 'rules' | 'hidden'
> & {
  value: string | string[]
  multi?: boolean
  options?: Option[] | string[]
  validations?: { required: boolean }
  checked?: boolean
}

type GeneralError = components['schemas']['GeneralError']

type Roles = (keyof typeof RolesDisplayNames)[]

const internalEditableRoles: Roles = ['MM', 'JMM', 'EX', 'EA', 'AM', 'Partner']

const adminRoles: Set<Role> = new Set(['CSM', 'MM', 'JMM'])

const jmmEditableRoles: Roles = ['EX', 'EA', 'AM', 'Partner']

const externalEditableRoles: Roles = ['ET']

const filterRoles = (roles: Roles) => ({
  clientDisplayName,
}: fetchRoles_fetchRoles) => roles.includes(clientDisplayName)

const focusDecorator = createFocusDecorator<FormValues>()

const messages = defineMessages({
  role: {
    defaultMessage: 'Company Role',
    description: 'Field label',
  },
  roleHelpText: {
    defaultMessage:
      'To change or view the instance role go to Event Instances \u2192 Settings',
    description: 'Help text',
  },
  genericError: {
    defaultMessage: 'Something is not right. Please retry.',
    description: 'Error message',
  },
  success: {
    defaultMessage: 'User profile updated successfully.',
    description: 'Success message',
  },
})

export const EditProfile: FC<EditProfileProps> = ({
  user: currentUser,
  fields,
  onCancel,
  onNavigate,
}) => {
  const { formatMessage } = useIntl()
  const queryClient = useQueryClient()
  const isSourceOutlook = useAppSource().app === AppSource.Outlook
  const location = new URLSearchParams(window.location.search).get('location')

  const portalClient = usePortalV2Client()
  const {
    id: loggedInUserId,
    attributes: { role: loggedInUserRole },
  } = useMe().data
  const [showLoader, setShowLoader] = useState(false)

  const { id, attributes: user, relationships } = currentUser
  const isViewingMyOwnOrAssumedProfile = loggedInUserId === id

  const { fetchRoles, roles = [] } = useRolesLazyQuery({
    onError: () => showErrorToast(formatMessage(messages.genericError)),
  })

  useEffect(() => {
    !isViewingMyOwnOrAssumedProfile && fetchRoles()
  }, [isViewingMyOwnOrAssumedProfile, fetchRoles])

  const formFields = useMemo(() => {
    const isRoleFieldDisabled =
      loggedInUserRole === 'JMM' && !jmmEditableRoles.includes(user.role)

    const isAdminRole = adminRoles.has(loggedInUserRole)

    const roleField: StaticFormField = {
      name: 'role',
      value: user.role,
      validations: { required: true },
      fieldType: FieldTypes.DROPDOWN,
      disabled:
        !!location ||
        !isAdminRole ||
        isRoleFieldDisabled ||
        isViewingMyOwnOrAssumedProfile ||
        user.userType === 'external',
      helpText: formatMessage(messages.roleHelpText),
      label: formatMessage(messages.role),
      options: isViewingMyOwnOrAssumedProfile
        ? [
            {
              label: formatMessage(roleLabel[user.role]),
              id: user.role,
            },
          ]
        : roles
            .filter(
              filterRoles(
                user.userType === 'internal'
                  ? internalEditableRoles
                  : externalEditableRoles
              )
            )
            .filter(
              ({ clientDisplayName: role }) =>
                loggedInUserRole !== 'JMM' ||
                isRoleFieldDisabled ||
                jmmEditableRoles.includes(role)
            )
            .map(({ clientDisplayName: role }) => ({
              label: formatMessage(roleLabel[role]),
              id: role,
            })),
    }

    const transformedFields = fields
      .filter(
        ({ attributes: { fieldInfo }, type }) =>
          !(
            (type === 'InternalUserStandardForm' ||
              type === 'ExternalUserStandardForm') &&
            !(fieldInfo as UserStandardFormFieldInfo).show
          )
      )
      .map(field => transformField({ field, user }))
    const [standardFormFields, customFields] = transformedFields.reduce(
      (acc, curr) => {
        curr.name.indexOf('customFields.') === 0
          ? acc[1].push(curr)
          : acc[0].push(curr)
        return acc
      },
      [[] as FormFieldProps[], [] as FormFieldProps[]]
    )

    const standardFields = standardFieldsToDisplay
      .filter(fieldName =>
        standardFormFields.find(({ name }) => name === fieldName)
      )
      .map(
        fieldName =>
          standardFormFields.find(
            ({ name }) => name === fieldName
          ) as FormFieldProps
      )

    return {
      customFields,
      standardFields: [
        ...standardFields,
        ...(isSourceOutlook ? [] : [roleField]),
      ],
    }
  }, [
    location,
    fields,
    formatMessage,
    isSourceOutlook,
    isViewingMyOwnOrAssumedProfile,
    loggedInUserRole,
    roles,
    user,
  ])

  const initialValues = useMemo(
    () => ({
      ...user,
      ...(user.personalLink
        ? { personalUrlToken: splitPersonalLink(user.personalLink)[1] }
        : null),
      mappedUsers:
        relationships?.mappedUsers?.map(
          ({ id, attributes: { firstName, lastName } }) => ({
            label: `${firstName} ${lastName}`,
            id,
          })
        ) ?? [],
    }),
    [relationships?.mappedUsers, user]
  )

  const roleFieldDecorator = useMemo(
    () =>
      createDecorator({
        field: 'role',
        updates: {
          mappedUsers: (_, allValues, prevValues) => {
            const { role, mappedUsers } = allValues as FormValues
            const { role: prevRole } = prevValues as FormValues

            if (!Object.keys(prevValues as FormValues).length) {
              return mappedUsers
            }

            if (
              (initialValues.role === 'EA' && role === 'EA') ||
              (initialValues.role === 'EX' && role === 'EX')
            ) {
              return initialValues.mappedUsers
            }

            if (
              (prevRole === 'EX' && initialValues.role === 'EA') ||
              (prevRole === 'EA' && initialValues.role === 'EX')
            ) {
              return []
            }

            return mappedUsers
          },
        },
      }),
    [initialValues.role, initialValues.mappedUsers]
  )

  const handleSubmit: (arg: FormValues) => void = async ({
    mappedUsers,
    ...restValues
  }) => {
    try {
      setShowLoader(true)
      await patchUserProfile(
        portalClient,
        {
          ...restValues,
          relationships: {
            mappedUsers: mappedUsers?.map(({ id }) => id as string) ?? [],
          },
        },
        id
      )
      queryClient.invalidateQueries('fetchUser')
      onCancel()
      showSuccessToast(formatMessage(messages.success))
      return undefined
    } catch (e) {
      setShowLoader(false)
      if (e instanceof HttpError && e.response.status === 403) {
        const errorMessage =
          (e as GeneralError).errors[0].title ??
          formatMessage(messages.genericError)
        showErrorToast(
          <span
            className="anchor-primary"
            dangerouslySetInnerHTML={{ __html: errorMessage }}
          />
        )
        return undefined
      }
      showErrorToast(formatMessage(messages.genericError))
      return { [FORM_ERROR]: { code: 'GenericError' } }
    }
  }

  const handleSaveAndNext: Config<FormValues>['onSubmit'] = async args => {
    const successFullSubmission = await handleSubmit(args)
    successFullSubmission === undefined && onNavigate?.()
  }

  return (
    <>
      {showLoader && <ModalSpinner />}
      <FinalForm<FormValues>
        onSubmit={handleSaveAndNext}
        initialValues={initialValues}
        decorators={[
          focusDecorator,
          // TODO: Awaiting fix for this [bug](https://github.com/final-form/final-form-calculate/issues/38)
          // to remove `ts-expect-error`.
          // @ts-expect-error
          roleFieldDecorator,
        ]}
        render={props => (
          <EditForm
            formRenderProps={props}
            user={currentUser}
            formFields={formFields}
            onCancel={onCancel}
            onSave={handleSubmit}
          />
        )}
      />
    </>
  )
}
