import { useEffect } from 'react'
import { useDirtyCheck } from '../providers/ProfileSectionDirtyCheckProvider'

type DirtyCheckArgs = {
  isDirty: boolean
  onSave: () => any
}

export const useDirtyCheckDialog = ({ isDirty, onSave }: DirtyCheckArgs) => {
  const { setIsDirty, onSuccessConfirmation } = useDirtyCheck()

  useEffect(() => {
    setIsDirty(isDirty)

    return () => {
      setIsDirty(false)
    }
  }, [isDirty, setIsDirty])

  useEffect(() => {
    onSuccessConfirmation(() => () => onSave())

    return () => {
      onSuccessConfirmation(() => () => {})
    }
  }, [onSave, onSuccessConfirmation])
}
