import { VFC, useState, useEffect } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { Modal, ModalHeader, SIZE } from '@jifflenow/brightui/modal'
import { Button, KIND } from '@jifflenow/brightui/button'
import { SelectCity, useTimeZoneFromCity } from 'components/TimeZoneChange'
import { useEventInfo } from 'components/ProfileDrawer/providers/EventInfoProvider'
import { EventDatesLabel } from 'components/ProfileDrawer/Common/EventDatesLabel'
import { CampaignFilter, useCampaign } from '../../../Common'
import {
  getISOTimeFromDate,
  getStartOfDate,
  getEndDateOfDay,
  getPlusISOStringDate,
  getFullMinutes,
} from '../../DateTimeConversions'
import {
  SelectEventLabel,
  SelectEventRoot,
  SelectEventWrapper,
} from '../styled-components'
import { clearButtonOverrides } from '../overrides'
import { AddEditExceptionProps, TimeSlots, TimeSlot } from './types'
import { ExceptionModalBody } from './ExceptionModalBody'
import { Footer } from './Footer'
import { modalOverrides } from './overrides'
import {
  HeaderTitle,
  TimeZoneWrapper,
  HeaderRoot,
  TitleBar,
} from './styled-components'

const messages = defineMessages({
  addException: {
    defaultMessage: 'Add Exception',
    description: 'Modal header',
  },
  editException: {
    defaultMessage: 'Edit Exception',
    description: 'Modal header',
  },
  addExceptionEvent: {
    defaultMessage: 'Add Exception for',
    description: 'Modal header',
  },
  editExceptionEvent: {
    defaultMessage: 'Edit Exception for',
    description: 'Modal header',
  },
  selectDateLabel: {
    defaultMessage: 'Select Event dates for',
    description: 'Modal header',
  },
  clear: {
    defaultMessage: 'Clear',
    description: 'Button label',
  },
  timeZone: {
    defaultMessage: 'Time zone',
    description: 'Button label',
  },
})

export const AddEditException: VFC<AddEditExceptionProps> = ({
  isOpen,
  onClose,
  exceptions,
  setExceptions,
  listedExceptionEditIndex,
  listedExceptions,
  timeZone,
  ...restProps
}) => {
  const { formatMessage } = useIntl()
  const { setCampaign } = useCampaign()
  const { onCityChange } = useTimeZoneFromCity()
  const [calendarDates, setCalendarDates] = useState<string[]>([])
  const [timeSlots, setTimeSlots] = useState<TimeSlot[]>([])

  const { location } = useEventInfo()

  useEffect(() => {
    if (restProps.calendarDates && restProps.timeSlots) {
      setCalendarDates(restProps.calendarDates)
      setTimeSlots(restProps.timeSlots.map(timeSlot => ({ timeSlot })))
    }
  }, [restProps.calendarDates, restProps.timeSlots])

  const isEditingException =
    listedExceptionEditIndex !== undefined && listedExceptionEditIndex >= 0

  const unavailableSlots = exceptions
    .filter(exception => calendarDates.includes(exception.date))
    .reduce<TimeSlots>(
      (unavailableSlotsAcc, exception) =>
        [...unavailableSlotsAcc, ...exception.unavailable] as TimeSlots,
      []
    )

  const hasOverlapsWithUnavailability = (timeSlots: TimeSlots) => {
    const hasOverlap = unavailableSlots?.find(([unStart, unEnd]) =>
      timeSlots.some(([start, end]) => end >= unStart && start <= unEnd)
    )

    return hasOverlap
  }

  const getAvailableSlots = (date: string) =>
    timeSlots.map(({ timeSlot: [start, end] }) => {
      const isSlotEndingAt12AM = getFullMinutes(end) === 0

      return [
        `${date}T${getISOTimeFromDate(start)}`,
        `${
          isSlotEndingAt12AM ? getPlusISOStringDate(date, { day: 1 }) : date
        }T${getISOTimeFromDate(end)}`,
      ]
    }) as TimeSlots

  const handleDone = () => {
    let overlappedUnavailableSlot: string[] | undefined = []

    const getUnavailableSlots = (date: string) => {
      overlappedUnavailableSlot =
        hasOverlapsWithUnavailability(getAvailableSlots(date)) ?? []

      return (timeSlots.length === 0
        ? [[getStartOfDate(date), getEndDateOfDay(date)]]
        : unavailableSlots?.length && !overlappedUnavailableSlot.length
        ? exceptions.find(exception => exception.date === date)?.unavailable ??
          []
        : []) as TimeSlots
    }

    setExceptions(
      [
        ...calendarDates.map(date => ({
          date,
          available: getAvailableSlots(date),
          unavailable: getUnavailableSlots(date),
        })),
        ...(listedExceptions && isEditingException
          ? exceptions.filter(
              ({ date }) =>
                !(
                  listedExceptions[listedExceptionEditIndex!].date.includes(
                    date
                  ) || calendarDates.includes(date)
                )
            )
          : exceptions.filter(({ date }) => !calendarDates.includes(date))),
      ]
        .sort((next, prev) => (prev.date < next.date ? 1 : -1))
        .map((exception, index) =>
          overlappedUnavailableSlot?.length &&
          calendarDates.length === 1 &&
          (exception.date ===
            getPlusISOStringDate(calendarDates[0], { day: 1 }) ||
            exception.date ===
              getPlusISOStringDate(calendarDates[0], { day: -1 })) &&
          exceptions[index].unavailable.find(
            ([start, end]) =>
              end === overlappedUnavailableSlot?.[0] ||
              start === overlappedUnavailableSlot?.[1]
          )
            ? {
                ...exception,
                unavailable: exception.unavailable.filter(
                  ([start, end]) =>
                    !(
                      end === overlappedUnavailableSlot?.[0] ||
                      start === overlappedUnavailableSlot?.[1]
                    )
                ),
              }
            : exception
        )
        .filter(
          ({ available, unavailable }) => available.length || unavailable.length
        )
    )
    onClose()
  }

  const hasError = timeSlots.some(timeSlot => !!timeSlot.error)

  return (
    <Modal
      isOpen={isOpen}
      size={SIZE.large}
      onClose={onClose}
      overrides={modalOverrides}
    >
      <ModalHeader>
        <HeaderRoot>
          {!location ? (
            <>
              <TitleBar>
                <HeaderTitle>
                  {formatMessage(
                    isEditingException
                      ? messages.editException
                      : messages.addException
                  )}
                </HeaderTitle>
                <TimeZoneWrapper>
                  <SelectCity
                    disabled
                    onChange={onCityChange}
                    placeholder={formatMessage(messages.timeZone)}
                  />
                </TimeZoneWrapper>
              </TitleBar>
              <SelectEventRoot>
                <SelectEventLabel>
                  {formatMessage(messages.selectDateLabel)}
                </SelectEventLabel>
                <SelectEventWrapper>
                  <CampaignFilter setDefaultValue={false} />
                  <Button
                    type="button"
                    onClick={() => setCampaign(null)}
                    overrides={clearButtonOverrides}
                    kind={KIND.minimal}
                  >
                    {formatMessage(messages.clear)}
                  </Button>
                </SelectEventWrapper>
              </SelectEventRoot>
            </>
          ) : (
            <EventDatesLabel
              title={formatMessage(
                isEditingException
                  ? messages.editExceptionEvent
                  : messages.addExceptionEvent
              )}
            />
          )}
        </HeaderRoot>
      </ModalHeader>
      <ExceptionModalBody
        exceptions={exceptions}
        calendarDates={calendarDates}
        timeSlots={timeSlots}
        setCalendarDates={setCalendarDates}
        setTimeSlots={setTimeSlots}
        timeZone={timeZone}
      />
      <Footer onCancel={onClose} onDone={handleDone} hasError={hasError} />
    </Modal>
  )
}
