import { DateTime } from 'luxon'
import {
  getDateInTimeZone,
  getDateTimeInTimeZone,
  getUTCDateTimeFromTimeZone,
  getISODateStringFromUTCDate,
  getUTCStartDate,
  getEndDateOfDay,
  getEndDateOfDayInUTC,
  getFullMinutes,
} from '../DateTimeConversions'
import {
  getPartitionedExceptionsHash,
  getCombinedExceptions,
} from './data-adapters'
import { TimeSlots, CommonException, Exception, ExceptionType } from './types'

// Merges continuous time slots after UTC <=> TimeZone transformations
function mergeContinuousTimeRanges<T extends any[]>(timeRanges: T): T {
  return timeRanges
    .sort((a, b) => (a[0] >= b[0] ? 1 : -1))
    .reduce((acc, curr) => {
      const prevRange = acc.pop()

      return prevRange
        ? prevRange[1] === curr[0]
          ? [...acc, [prevRange[0], curr[1]]]
          : [...acc, prevRange, curr]
        : [...acc, curr]
    }, [])
}

export const getExceptionsInTimeZone = (
  exceptions: Exception[],
  timeZone: string
) => {
  const { available, unavailable } = getPartitionedExceptionsHash(exceptions)

  return getCombinedExceptions([
    ...transformExceptionsInTimeZone(available, timeZone, 'available'),
    ...transformExceptionsInTimeZone(unavailable, timeZone, 'unavailable'),
  ])
}

const transformExceptionsInTimeZone = (
  exceptions: CommonException[],
  timeZone: string,
  type: ExceptionType
) => {
  const flatted = exceptions.flatMap(({ date, timeRanges }) => {
    const unavailableDate = getDateInTimeZone(getUTCStartDate(date), timeZone)

    return timeRanges.length === 0
      ? [[unavailableDate, unavailableDate]]
      : timeRanges.flatMap(([start, end]: string[]) => {
          const startDateTime = getDateTimeInTimeZone(start, timeZone)
          const endDateTime = getDateTimeInTimeZone(end, timeZone)
          const startDate = startDateTime.toISO()
          const endDate = endDateTime.toISO()

          return startDateTime.toISODate() === endDateTime.toISODate()
            ? [[startDate, endDate]]
            : endDateTime.startOf('day').toISO() === endDate
            ? [[startDate, getEndDateOfDay(startDate)]]
            : [
                [startDate, getEndDateOfDay(startDate)],
                [endDateTime.startOf('day').toISO(), endDate],
              ]
        })
  })
  const dateBasedTimeRanges = flatted.reduce((accRanges, range) => {
    const [start, end] = range
    const dateKey = DateTime.fromISO(start).toISODate()

    return {
      ...accRanges,
      [dateKey]: [
        ...(accRanges[dateKey] ?? []),
        ...(start === end ? [] : [range]),
      ],
    }
  }, {} as { [key: string]: string[][] })

  return Object.keys(dateBasedTimeRanges).map(date => ({
    date,
    [type]: mergeContinuousTimeRanges(dateBasedTimeRanges[date]),
  }))
}

export const getExceptionsInUTC = (
  exceptions: Exception[],
  timeZone: string
) => {
  const { available, unavailable } = getPartitionedExceptionsHash(exceptions)

  return getCombinedExceptions([
    ...transformExceptionsInUTC(available, timeZone, 'available'),
    ...transformExceptionsInUTC(unavailable, timeZone, 'unavailable'),
    ...transformInvertedAvailability(available, timeZone),
  ])
}

const transformInvertedAvailability = (
  exceptions: CommonException[],
  timeZone: string
) =>
  exceptions.map(({ date, timeRanges }) => ({
    date,
    inverted: timeRanges
      .sort((range1, range2) =>
        getFullMinutes(range1[0]) >= getFullMinutes(range2[0]) ? 1 : -1
      )
      .flatMap(([start, end], index) => {
        const startTimeInUserTZ = getUTCDateTimeFromTimeZone(
          start,
          timeZone
        ).setZone(timeZone)

        const endTimeInUserTZ = getUTCDateTimeFromTimeZone(
          end,
          timeZone
        ).setZone(timeZone)

        const rangeStartsAt12AM =
          startTimeInUserTZ.hour === 0 && startTimeInUserTZ.minute === 0
        const rangeEndsAt12AM =
          (endTimeInUserTZ.hour === 0 && endTimeInUserTZ.minute === 0) ||
          (endTimeInUserTZ.hour === 23 &&
            endTimeInUserTZ.minute === 59 &&
            endTimeInUserTZ.second === 59)

        const startTime = startTimeInUserTZ.toISO()
        const endTime = endTimeInUserTZ.toISO()
        const dayStart = startTimeInUserTZ.startOf('day').toISO()
        const dayEnd = startTimeInUserTZ.endOf('day').toISO()

        const inAvailabilityExceptions = []

        if (timeRanges.length === 1) {
          if (!rangeStartsAt12AM) {
            inAvailabilityExceptions.push([dayStart, startTime])
          }
          if (!rangeEndsAt12AM) {
            inAvailabilityExceptions.push([endTime, dayEnd])
          }
        } else {
          if (index !== timeRanges.length - 1) {
            const nextRangeStartTime = getUTCDateTimeFromTimeZone(
              timeRanges[index + 1][0],
              timeZone
            )
              .setZone(timeZone)
              .toISO()

            if (index === 0 && !rangeStartsAt12AM) {
              inAvailabilityExceptions.push([dayStart, startTime])
            }
            inAvailabilityExceptions.push([endTime, nextRangeStartTime])
          } else if (!rangeEndsAt12AM) {
            inAvailabilityExceptions.push([endTime, dayEnd])
          }
        }

        return inAvailabilityExceptions as TimeSlots
      }),
  }))

const transformExceptionsInUTC = (
  exceptions: CommonException[],
  timeZone: string,
  type: ExceptionType
) => {
  const flatted = exceptions.flatMap(({ date, timeRanges }) => {
    const unavailableDate = getUTCStartDate(date)

    return timeRanges.length === 0
      ? [[unavailableDate, unavailableDate]]
      : timeRanges.flatMap(([start, end]: string[]) => {
          const startDateTime = getUTCDateTimeFromTimeZone(start, timeZone)
          const endDateTime = getUTCDateTimeFromTimeZone(end, timeZone)
          const startDate = startDateTime.toISO()
          const endDate = endDateTime.toISO()

          return startDateTime.toISODate() === endDateTime.toISODate()
            ? [[startDate, endDate]]
            : endDateTime.startOf('day').toISO() === endDate
            ? [[startDate, getEndDateOfDayInUTC(startDateTime.toISODate())]]
            : [
                [startDate, getEndDateOfDayInUTC(startDateTime.toISODate())],
                [endDateTime.startOf('day').toISO(), endDate],
              ]
        })
  })
  const dateBasedTimeRanges = flatted.reduce((accRanges, range) => {
    const [start, end] = range
    const dateKey = getISODateStringFromUTCDate(start)

    return {
      ...accRanges,
      [dateKey]: [
        ...(accRanges[dateKey] ?? []),
        ...(start === end ? [] : [range]),
      ],
    }
  }, {} as { [key: string]: string[][] })

  return Object.keys(dateBasedTimeRanges).map(date => ({
    date,
    [type]: dateBasedTimeRanges[date] as TimeSlots,
  }))
}
