import { useEffect, useState, VFC, ChangeEvent } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { Select, Value } from '@jifflenow/brightui/select'
import { Field } from 'ui-components/Field'
import { showErrorToast } from 'utils/toast'
import { useMappableUsers } from 'rest'
import { useDebounce } from 'utils/debounce'
import { useProfileUser } from '../providers/ProfileUserProvider'
import { useEventInfo } from '../providers/EventInfoProvider'
import { MappableUsers, MappedUserFieldProps, UserOption } from './types'
import { StyledFormControl } from './styled-components'
import { eventTypeMapping } from './constants'

const messages = defineMessages({
  labelForEA: {
    defaultMessage: 'Managed Attendees at Company',
    description: 'Field label',
  },
  labelForEX: {
    defaultMessage: 'Executive Admins at Company',
    description: 'Field label',
  },
  labelForEventLevelEA: {
    defaultMessage:
      '\u2018Managed Attendees\u2019 for this {eventType} instance',
    description: 'Field label',
  },
  labelForEventLevelEX: {
    defaultMessage:
      '\u2018Executive Admins\u2019 for this {eventType} instance',
    description: 'Field label',
  },
  placeholder: {
    defaultMessage: 'Search by name',
    description: 'Placeholder text',
  },
  helpText: {
    defaultMessage: 'Please enter 1 or more characters to search',
    description: 'Help text',
  },
  genericError: {
    defaultMessage: 'Something is not right. Please retry.',
    description: 'Error message',
  },
  managedAttendeesHelpText: {
    defaultMessage:
      'To manage or view instance attendees go to Event Instances \u2192 Settings',
    description: 'Help text',
  },
  executiveAdminsHelpText: {
    defaultMessage:
      'To manage or view instance EA go to Event Instances \u2192 Settings',
    description: 'Help text',
  },
  none: {
    defaultMessage: 'None',
    description: 'Field placeholder',
  },
})

const valueMapper = ({ name, uuid }: UserOption) => ({
  label: name,
  id: uuid,
})

export const MappedUserField: VFC<MappedUserFieldProps> = ({
  campaign,
  name,
  isDisabled,
  role,
}) => {
  const { formatMessage } = useIntl()
  const { id: userId } = useProfileUser()
  const { subType = campaign?.subType } = useEventInfo().location || {}
  const [searchText, setSearchText] = useState('')
  const [options, setOptions] = useState<MappableUsers>([])

  const isUserEA = role === 'EA'

  const { run: searchUsers, isPending, cancel } = useMappableUsers({
    userId,
    systemName: campaign?.eventName,
    locationId: campaign?.location,
    searchUsersByRole: isUserEA ? 'EX' : 'EA',
    onResolve: ({ users }) => setOptions(users),
    onReject: () => showErrorToast(formatMessage(messages.genericError)),
  })

  const optionsList = options.map(valueMapper).filter(({ id }) => id !== userId)

  const search = useDebounce(searchText.trim())

  useEffect(() => {
    setOptions([])
  }, [role])

  useEffect(() => {
    if (!search) {
      cancel()
      setOptions([])
      return
    }
    searchUsers({ search })
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [search])

  return (
    <Field
      name={name}
      render={({ input, meta }) => (
        <StyledFormControl
          error={meta.touched && meta.error}
          label={formatMessage(
            isUserEA
              ? messages.labelForEventLevelEA
              : messages.labelForEventLevelEX,
            {
              eventType: eventTypeMapping[subType!],
            }
          )}
          caption={
            name === 'mappedUsers'
              ? isUserEA
                ? formatMessage(messages.managedAttendeesHelpText)
                : formatMessage(messages.executiveAdminsHelpText)
              : undefined
          }
        >
          <Select
            {...input}
            multi
            searchable
            options={optionsList}
            isLoading={isPending}
            disabled={isDisabled ?? false}
            backspaceClearsInputValue
            filterOptions={false}
            onCloseResetsInput={false}
            onBlurResetsInput={false}
            placeholder={
              isDisabled
                ? formatMessage(messages.none)
                : formatMessage(messages.placeholder)
            }
            onInputChange={(e: ChangeEvent<HTMLInputElement>) =>
              setSearchText(e.target.value.trim())
            }
            noResultsMsg={!search ? formatMessage(messages.helpText) : ''}
            onChange={({ value }: { value: Value[] }) => input.onChange(value)}
          />
        </StyledFormControl>
      )}
    />
  )
}
