import { FC } from 'react'
import { Select, OnChangeParams } from '@jifflenow/brightui/select'
import { Field, Validation } from 'ui-components/Field'
import { SelectFieldProps } from '../types'
import { FormControl } from './styled-components'

export const SelectField: FC<SelectFieldProps> = props => {
  const {
    name,
    label,
    options,
    disabled,
    creatable,
    clearable,
    handleRules,
    validations,
    helpText,
  } = props

  return (
    <Field<(string | number | undefined)[] | undefined>
      name={name}
      validations={validations}
      parse={value => value[0]?.id}
      format={value => (value ? [{ id: value, label: value }] : [])}
      render={({ input, meta }) => (
        <FormControl
          label={label}
          error={meta.touched && meta.error}
          caption={helpText}
        >
          <Select
            {...input}
            id={name}
            required={validations.includes(Validation.REQUIRED)}
            searchable
            creatable={creatable}
            clearable={clearable}
            disabled={disabled}
            backspaceClearsInputValue
            onCloseResetsInput={false}
            onBlurResetsInput={false}
            options={options?.map(option =>
              typeof option === 'object'
                ? option
                : { id: option, label: option }
            )}
            onChange={({ value }: OnChangeParams) => {
              input.onChange(value)
              handleRules?.({ name, value })
            }}
          />
        </FormControl>
      )}
    />
  )
}
