import { useEffect, useState, VFC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { useBeforeUnload } from 'utils/useBeforeUnload'
import { ModalSpinner } from '@jifflenow/brightui/modal-spinner'
import { showErrorToast, showSuccessToast } from 'utils/toast'
import { updateCombinedUserAvailability, usePortalV2Client } from 'rest'
import { components } from 'types/openapi'
import { useParams } from 'react-router-dom'
import { Footer } from '../Common/Footer'
import { UTCMinutesCombinedAvailability } from '../types'
import { CombinedAvailabilityDate } from './CombinedAvailabilityDate'
import { useCombinedAvailability } from './CombinedAvailabilityProvider'
import { getAvailability } from './transform'
import { CombinedAvailabilitiesProps } from './types'
import { getDayFromDate } from './DateTimeConversions'
import { CopyCombinedAvailability } from './CopyCombinedAvailability'
import { Root } from './styled-components'

type UpdateCombinedAvailability = components['schemas']['AvailabilityTimeRange']

const messages = defineMessages({
  success: {
    defaultMessage: 'Availabilities Saved Successfully.',
    description: 'Success message',
  },
  genericError: {
    defaultMessage: 'Error Setting Availabilities.',
    description: 'Error message',
  },
})

const matchPayloadToAPI = ({
  date,
  timeRanges,
}: UTCMinutesCombinedAvailability) =>
  ({
    availabilityDate: date,
    availabilityDay: getDayFromDate(date),
    availableSlots: timeRanges.reduce(
      (acc, elem) => [...acc, elem.start, elem.end],
      [] as number[]
    ),
  } as UpdateCombinedAvailability)

export const CombinedAvailabilities: VFC<CombinedAvailabilitiesProps> = ({
  timeZoneId,
  dateRange,
}) => {
  const {
    availability,
    isDirty,
    resetAvailability,
    setDefaultAvailability,
    setAvailability,
  } = useCombinedAvailability()
  const { formatMessage } = useIntl()
  const unload = useBeforeUnload()
  const client = usePortalV2Client()
  const { userId } = useParams<{ userId: string }>()
  const location = new URLSearchParams(window.location.search).get('location')
  const [isOpen, setIsOpen] = useState(false)
  const [showLoader, setShowLoader] = useState(false)
  const [copySource, setCopySource] = useState<string | null>(null)

  const hasError = availability
    .flatMap(({ timeRanges }) => timeRanges)
    .some(({ error }) => error)

  const handleSave = async () => {
    setAvailability(
      availability.map(availability => ({
        ...availability,
        timeRanges: availability.timeRanges.sort((a, b) =>
          a.start < b.start ? -1 : a.start > b.start ? 1 : 0
        ),
      }))
    )
    setShowLoader(true)
    const payload = {
      availabilities: getAvailability(availability).map(matchPayloadToAPI),
      users: [userId],
      locationUuid: location!,
      weekwise: false,
      userTimeZone: timeZoneId,
    }
    try {
      await updateCombinedUserAvailability(client, payload)
      setDefaultAvailability(availability)
      setShowLoader(false)
      showSuccessToast(formatMessage(messages.success))
      return undefined
    } catch (error) {
      setShowLoader(false)
      showErrorToast(formatMessage(messages.genericError))
      return false
    }
  }

  const handleCopySlots = (date: string) => {
    setIsOpen(true)
    setCopySource(date)
  }

  useEffect(() => {
    unload(isDirty)
  }, [isDirty, unload])

  return (
    <>
      <Root>
        {availability.map(availability => (
          <CombinedAvailabilityDate
            dateAvailability={availability}
            onCopySlots={handleCopySlots}
            key={availability.date}
          />
        ))}
      </Root>

      <Footer
        clearTempActiveKey={() => {}}
        isDirty={isDirty}
        onSave={handleSave}
        isDisabled={showLoader || !isDirty || hasError}
        pendoPrefix="profile/availability"
        handleDiscard={resetAvailability}
      />
      {isOpen && (
        <CopyCombinedAvailability
          isOpen
          sourceDate={copySource!}
          onClose={() => setIsOpen(false)}
          availabilityInTZ={availability}
          setAvailabilityInTZ={setAvailability}
          dateRange={dateRange}
        />
      )}
      {showLoader && <ModalSpinner />}
    </>
  )
}
