import { FC, useState } from 'react'
import { useLocation } from 'react-router-dom'
import { CampaignFilter, EventTypeFilter, EventType } from './CampaignFilter'

export const EventTypeAndLocationFilter: FC<{
  eventType?: string
  eventTypes?: EventType[]
}> = ({ eventType }) => {
  const [eventTypes, setEventTypes] = useState([] as EventType[])
  const location = new URLSearchParams(useLocation().search).get('location')

  return (
    <>
      {eventType !== 'Emeet' && !location && (
        <EventTypeFilter
          types={eventTypes}
          onChange={eventTypes => setEventTypes(eventTypes)}
        />
      )}
      <CampaignFilter
        eventType={eventType}
        key={eventType}
        eventTypes={eventTypes}
      />
    </>
  )
}
