import { VFC, useState, useEffect } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { useParams } from 'react-router-dom'
import { ModalSpinner } from '@jifflenow/brightui/modal-spinner'
import { updateUserAvailability, usePortalV2Client } from 'rest'
import { showErrorToast, showSuccessToast } from 'utils/toast'
import { useBeforeUnload } from 'utils/useBeforeUnload'
import { useAvailability } from '../AvailabilityProvider'
import { Footer } from '../Common/Footer'
import { getAvailabilityInUTC } from './transform'
import { AvailabilitiesProps, WeekDay } from './types'
import { AvailabilityDay } from './AvailabilityDay'
import { CopyAvailability } from './CopyAvailability'
import { Root } from './styled-components'

const messages = defineMessages({
  success: {
    defaultMessage: 'Availabilities Saved Successfully.',
    description: 'Success message',
  },
  genericError: {
    defaultMessage: 'Error Setting Availabilities.',
    description: 'Error message',
  },
})

export const Availabilities: VFC<AvailabilitiesProps> = ({
  timeZone,
  onSuccess,
  clearTempActiveKey,
}) => {
  const { formatMessage } = useIntl()
  const unload = useBeforeUnload()
  const { userId: id } = useParams<{ userId: string }>()
  const client = usePortalV2Client()
  const [isOpen, setIsOpen] = useState(false)
  const [showLoader, setShowLoader] = useState(false)

  const {
    isDirty,
    availability,
    setAvailability,
    daysToIdMappings,
    resetAvailability,
    setDefaultAvailability,
  } = useAvailability()

  const [copySource, setCopySource] = useState<WeekDay | null>(null)

  const hasError = availability
    .flatMap(({ timeRanges }) => timeRanges)
    .some(({ error }) => error)

  const handleCopySlots = (weekDay: WeekDay) => {
    setIsOpen(true)
    setCopySource(weekDay)
  }

  const handleSave = async () => {
    setAvailability(
      availability.map(availability => ({
        ...availability,
        timeRanges: availability.timeRanges.sort((a, b) =>
          a.start < b.start ? -1 : a.start > b.start ? 1 : 0
        ),
      }))
    )
    setShowLoader(true)
    const payload = {
      data: getAvailabilityInUTC(availability, timeZone.timeZoneId).map(
        availabilityUTC => ({
          id: daysToIdMappings[availabilityUTC.day],
          timeRanges: availabilityUTC.timeRanges,
        })
      ),
    }
    try {
      await updateUserAvailability(client, id, payload)
      setDefaultAvailability(availability)
      setShowLoader(false)
      showSuccessToast(formatMessage(messages.success))
      onSuccess()
      return undefined
    } catch (error) {
      setShowLoader(false)
      showErrorToast(formatMessage(messages.genericError))
      return false
    }
  }

  useEffect(() => {
    unload(isDirty)
  }, [isDirty, unload])

  return (
    <>
      <Root>
        {availability?.map((availability, key) => (
          <AvailabilityDay
            key={key}
            dayAvailability={availability}
            onCopySlots={handleCopySlots}
          />
        ))}
      </Root>
      <Footer
        clearTempActiveKey={clearTempActiveKey}
        isDirty={isDirty}
        onSave={handleSave}
        isDisabled={showLoader || !isDirty || hasError}
        pendoPrefix="profile/availability"
        handleDiscard={resetAvailability}
      />
      {isOpen && (
        <CopyAvailability
          isOpen
          sourceWeekDay={copySource}
          onClose={() => setIsOpen(false)}
          availabilityInTZ={availability}
          setAvailabilityInTZ={setAvailability}
        />
      )}
      {showLoader && <ModalSpinner />}
    </>
  )
}
