import { FC, useState } from 'react'
import { useParams } from 'react-router-dom'
import { useIntl, defineMessages } from 'react-intl'
import { DateTime } from 'luxon'
import { KIND } from '@jifflenow/brightui/button'
import { Input } from '@jifflenow/brightui/input'
import { ModalSpinner } from '@jifflenow/brightui/modal-spinner'
import { showErrorToast, showSuccessToast } from 'utils/toast'
import { Field } from 'ui-components/Field'
import { FormFieldProps } from 'components/CustomFormFields'
import { useMe } from 'providers/MeProvider'
import { putVerifyUser, usePortalV1Client } from 'rest'
import { useProfileUser } from '../../providers/ProfileUserProvider'
import { linkButtonOverrides } from './overrides'
import { EmailControl, InviteSentTime, VerifyButton } from './styled-components'

const messages = defineMessages({
  verify: {
    defaultMessage: 'Verify',
    description: 'Error message',
  },
  resendVerificationLink: {
    defaultMessage: 'Resend verification email',
    description: 'Error message',
  },
  inviteSentTime: {
    defaultMessage: 'Verification email sent on {dateTime}',
    description: 'Field help text',
  },
  success: {
    defaultMessage: 'Verification email is sent successfully',
    description: 'Success message',
  },
  genericError: {
    defaultMessage: 'Error Saving.',
    description: 'Error message',
  },
})

export const PrimaryEmail: FC<FormFieldProps> = ({ name, label }) => {
  const { formatMessage } = useIntl()
  const client = usePortalV1Client()
  const { id: loggedInUsersId } = useMe().data
  const [showLoader, setShowLoader] = useState<boolean>(false)

  const {
    verified,
    inviteLastSent: inviteSentTime,
    userType,
  } = useProfileUser().attributes

  const { userId } = useParams<{ userId: string }>()
  const isSomeoneElsesProfile = userId !== loggedInUsersId
  const [showVerifyButton, setShowVerifyButton] = useState<boolean>(
    isSomeoneElsesProfile && !verified && userType === 'internal'
  )

  const handleVerify = async (value: string) => {
    try {
      setShowLoader(true)
      await putVerifyUser(client, { emails: [value] })
      setShowLoader(false)
      setShowVerifyButton(false)
      showSuccessToast(formatMessage(messages.success))
    } catch (error) {
      setShowLoader(false)
      showErrorToast(formatMessage(messages.genericError))
    }

    return undefined
  }

  return (
    <>
      {showLoader && <ModalSpinner />}
      <Field<string>
        name={name}
        render={({ input, meta }) => (
          <EmailControl label={label} error={meta.touched && meta.error}>
            <>
              {showVerifyButton && (
                <VerifyButton
                  type="button"
                  kind={KIND.minimal}
                  overrides={linkButtonOverrides}
                  onClick={() => handleVerify(input.value)}
                  data-pendo="profile/edit/verify-email"
                >
                  {formatMessage(
                    inviteSentTime
                      ? messages.resendVerificationLink
                      : messages.verify
                  )}
                </VerifyButton>
              )}
              <Input {...input} id={name} disabled type="email" />
              {showVerifyButton && inviteSentTime && (
                <InviteSentTime>
                  {formatMessage(messages.inviteSentTime, {
                    dateTime: DateTime.fromISO(inviteSentTime).toFormat('FF'),
                  })}
                </InviteSentTime>
              )}
            </>
          </EmailControl>
        )}
      />
    </>
  )
}
