import { areDatesAdjacent, getFullMinutes } from '../DateTimeConversions'
import { GroupedException, Exception } from './types'

const minutesMapper = ([start, end]: string[]) => [
  getFullMinutes(start),
  getFullMinutes(end),
]

const areSlotsSame = (last: GroupedException, current: Exception) =>
  JSON.stringify(last.available.map(minutesMapper)) ===
    JSON.stringify(current.available.map(minutesMapper)) &&
  JSON.stringify(last.unavailable.map(minutesMapper)) ===
    JSON.stringify(current.unavailable.map(minutesMapper))

export const getGroupedExceptionDates = (exceptions: Exception[]) =>
  exceptions.reduce((accExceptions, exception) => {
    const lastAccumulatedException = accExceptions[accExceptions.length - 1]

    if (
      lastAccumulatedException &&
      areDatesAdjacent(
        lastAccumulatedException.date[lastAccumulatedException.date.length - 1],
        exception.date
      ) &&
      areSlotsSame(lastAccumulatedException, exception)
    ) {
      const poppedLastException = accExceptions.pop() as GroupedException
      accExceptions.push({
        ...poppedLastException,
        date: [...poppedLastException.date, exception.date],
      })
    } else {
      accExceptions.push({ ...exception, date: [exception.date] })
    }

    return accExceptions
  }, [] as GroupedException[])
