import { FC, useState } from 'react'
import { useMe } from 'providers/MeProvider'
import { defineMessages, FormattedMessage, useIntl } from 'react-intl'
import { KIND } from '@jifflenow/brightui/button'
import { ConfirmationDialog } from 'ui-components/ConfirmationDialog'
import { FormButtonsProps } from './types'
import { FooterButton, FooterContainer } from './styled-components'

const modalLabels = defineMessages({
  modalHeader: {
    defaultMessage: 'Your changes will be lost.',
    description: 'Modal heading',
  },
  modalMessage: {
    defaultMessage: 'Keep changes?',
    description: 'Confirmation message',
  },
  cancelButtonLabel: {
    defaultMessage: 'Discard',
    description: 'Button label',
  },
  successButtonLabel: {
    defaultMessage: 'Keep',
    description: 'Button label',
  },
})

export const Footer: FC<FormButtonsProps> = ({
  isDisabled = false,
  isLoading = false,
  hasOnlyNext = false,
  hasForm = false,
  isLastItem,
  onNext,
  onDone,
  onSave,
  onDiscard,
  onSaveAndNext,
  pendoPrefix,
}) => {
  const { eMeetProfileSetupReminded } = useMe().meta
  const { formatMessage } = useIntl()
  const [showConfirmation, setShowConfirmation] = useState(false)

  const handleFormReset = () => {
    setShowConfirmation(false)
    onDiscard?.()
  }

  return (
    <FooterContainer>
      {hasOnlyNext ? (
        isLastItem ? (
          <FooterButton
            type="button"
            disabled={isDisabled}
            isLoading={isLoading}
            onClick={onDone}
            data-pendo={`${pendoPrefix}/done`}
          >
            <FormattedMessage
              defaultMessage="Done"
              description="Button label"
            />
          </FooterButton>
        ) : (
          <FooterButton
            type="button"
            disabled={isDisabled}
            isLoading={isLoading}
            onClick={onNext}
            data-pendo={`${pendoPrefix}/next`}
          >
            <FormattedMessage
              defaultMessage="Next"
              description="Button label"
            />
          </FooterButton>
        )
      ) : (
        <>
          {showConfirmation && (
            <ConfirmationDialog
              heading={formatMessage(modalLabels.modalHeader)}
              message={formatMessage(modalLabels.modalMessage)}
              cancelLabel={formatMessage(modalLabels.cancelButtonLabel)}
              successLabel={formatMessage(modalLabels.successButtonLabel)}
              onClose={() => setShowConfirmation(false)}
              onCancel={handleFormReset}
              onSuccess={() => setShowConfirmation(false)}
            />
          )}
          <FooterButton
            type="button"
            kind={KIND.tertiary}
            disabled={isDisabled}
            onClick={() => setShowConfirmation(true)}
            data-pendo={`${pendoPrefix}/discard`}
          >
            <FormattedMessage
              defaultMessage="Discard"
              description="Button label"
            />
          </FooterButton>
          {eMeetProfileSetupReminded ? (
            <FooterButton
              type="button"
              disabled={isDisabled}
              isLoading={isLoading}
              onClick={onSave}
              data-pendo={`${pendoPrefix}/save`}
            >
              <FormattedMessage
                defaultMessage="Save"
                description="Button label"
              />
            </FooterButton>
          ) : null}
          {isLastItem ? (
            <FooterButton
              type="submit"
              disabled={isDisabled}
              isLoading={isLoading}
              data-pendo={`${pendoPrefix}/save-and-done`}
            >
              <FormattedMessage
                defaultMessage="Save And Done"
                description="Button label"
              />
            </FooterButton>
          ) : (
            <FooterButton
              type={hasForm ? 'submit' : 'button'}
              disabled={isDisabled}
              isLoading={isLoading}
              onClick={hasForm ? undefined : onSaveAndNext}
              data-pendo={`${pendoPrefix}/save-and-next`}
            >
              <FormattedMessage
                defaultMessage="Save And Next"
                description="Button label"
              />
            </FooterButton>
          )}
        </>
      )}
    </FooterContainer>
  )
}
