import { ModalSpinner } from '@jifflenow/brightui/modal-spinner'
import { useEffect, useState, VFC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { useQueryClient, useUserMappingsMutation } from 'rest'
import { components } from 'types/openapi'
import { ConfirmationDialog } from 'ui-components/ConfirmationDialog'
import { showErrorToast, showSuccessToast } from 'utils/toast'
import { useBeforeUnload } from 'utils/useBeforeUnload'
import { Footer } from '../Common'
import { useDirtyCheckDialog } from '../Common/useDirtyCheckDialog'
import { EngagementTypesFooterProps } from './types'

type UserMappingEntityRequest = components['schemas']['UserMappingEntityRequest']

const messages = defineMessages({
  saveSuccess: {
    defaultMessage: 'Saved Successfully.',
    description: 'Success message',
  },
  genericError: {
    defaultMessage: 'Something went wrong. Please try again.',
    description: 'Error message',
  },
})

const modalLabels = defineMessages({
  modalHeader: {
    defaultMessage: 'Your changes will be lost.',
    description: 'Modal heading',
  },
  modalMessage: {
    defaultMessage: 'Keep changes?',
    description: 'Confirmation message',
  },
  cancelButtonLabel: {
    defaultMessage: 'Discard',
    description: 'Button label',
  },
  successButtonLabel: {
    defaultMessage: 'Keep',
    description: 'Button label',
  },
})

export const EngagementTypesFooter: VFC<EngagementTypesFooterProps> = ({
  user,
  onNavigate,
  selectedLocation,
  search: search_term,
  state,
  dispatch,
}) => {
  const { formatMessage } = useIntl()
  const unload = useBeforeUnload()
  const queryClient = useQueryClient()
  const userId = user.id

  const [showConfirmation, setShowConfirmation] = useState(false)

  const {
    id: location_uuid,
    attributes: { systemName: eventName },
  } = selectedLocation

  const { mapped, unmapped } = state

  const modifiedMappings = new Map([
    ...mapped.mappedItems,
    ...unmapped.mappedItems,
  ])

  const isDirty =
    !!modifiedMappings.size ||
    mapped.requestAll !== undefined ||
    mapped.participateAll !== undefined ||
    unmapped.requestAll !== undefined ||
    unmapped.participateAll !== undefined

  const getPayload = (): UserMappingEntityRequest => {
    const mappings: UserMappingEntityRequest['mappings'] = []
    for (const [
      mappingEntityUuid,
      { canRequest, canParticipate },
    ] of modifiedMappings) {
      mappings.push({
        mappingEntityUuid,
        mappingAttributes: {
          canRequest,
          canParticipate,
        },
      })
    }

    const payload: UserMappingEntityRequest = {
      ...((mapped.requestAll !== undefined ||
        mapped.participateAll !== undefined) && {
        mapped: {
          selectAll: {
            ...(mapped.requestAll !== undefined && {
              canRequest: mapped.requestAll,
            }),
            ...(mapped.participateAll !== undefined && {
              canParticipate: mapped.participateAll,
            }),
          },
        },
      }),
      ...((unmapped.requestAll !== undefined ||
        unmapped.participateAll !== undefined) && {
        unmapped: {
          selectAll: {
            ...(unmapped.requestAll !== undefined && {
              canRequest: unmapped.requestAll,
            }),
            ...(unmapped.participateAll !== undefined && {
              canParticipate: unmapped.participateAll,
            }),
          },
        },
      }),
      mappings,
    }

    return payload
  }

  const { mutateAsync, isLoading } = useUserMappingsMutation({
    path: { eventName, userId, mappingType: 'EngagementMapping' },
    query: { search_term, location_uuid },
  })

  const handleSave = async () => {
    try {
      const payload = getPayload()
      await mutateAsync(payload)
      showSuccessToast(formatMessage(messages.saveSuccess))
      dispatch({ type: 'resetValues' })
      queryClient.resetQueries([
        'userMappings',
        { path: { mappingType: 'EngagementMapping' } },
      ])
      return undefined
    } catch (error) {
      showErrorToast(formatMessage(messages.genericError))
      return false
    }
  }

  const handleSaveAndNext = async () =>
    (await handleSave()) === undefined && onNavigate?.()

  useDirtyCheckDialog({
    isDirty,
    onSave: handleSave,
  })

  useEffect(() => {
    unload(isDirty)
  }, [isDirty, unload])

  const handleDiscard = () => {
    setShowConfirmation(false)
    dispatch({ type: 'discardChanges' })
  }

  return (
    <>
      {isLoading && <ModalSpinner />}
      {showConfirmation && (
        <ConfirmationDialog
          heading={formatMessage(modalLabels.modalHeader)}
          message={formatMessage(modalLabels.modalMessage)}
          cancelLabel={formatMessage(modalLabels.cancelButtonLabel)}
          successLabel={formatMessage(modalLabels.successButtonLabel)}
          onClose={() => setShowConfirmation(false)}
          onCancel={handleDiscard}
          onSuccess={() => setShowConfirmation(false)}
        />
      )}
      <Footer
        isDisabled={!isDirty}
        onDiscard={() => setShowConfirmation(true)}
        onSave={handleSave}
        onSaveAndNext={handleSaveAndNext}
        pendoPrefix="profile/campaign/engagement-type"
      />
    </>
  )
}
