import { useEventInfo } from '../providers/EventInfoProvider'
import { useEventName } from '../providers/EventNameProvider'
import { useCampaign } from './CampaignFilter'
import { subTypeToEventTypeMapping } from './constants'
import { EventType } from './types'

export const useEventType = () => {
  const { campaign } = useCampaign()
  const { subType = campaign?.subType } = useEventInfo().location || {}
  const eventType =
    (useEventName().eventType as EventType) ||
    subTypeToEventTypeMapping[subType!]

  return eventType
}
