import { FC } from 'react'
import { useCKEditor } from '@jifflenow/ckeditor'
import { Field } from 'ui-components/Field'
import { FieldRenderProps } from 'react-final-form'
import { TextFieldProps } from '../../types'
import { FormControl } from '../styled-components'
import { TextareaLoader } from './TextareaLoader'
import { TextareaError } from './TextareaError'

interface TextAreaProps extends FieldRenderProps<string, HTMLElement> {
  placeholder?: string
  disabled?: boolean
}

const ckeditorConfig = {
  resize_enabled: false,
  language: 'en',
}

const TextArea: FC<TextAreaProps> = props => {
  const {
    placeholder,
    disabled,
    input: { name, value, onChange, onFocus, onBlur },
    meta: { touched, error, active },
  } = props

  const { showLoader, setElement, editorStyle, ckeditorError } = useCKEditor({
    placeholder,
    input: { name, value, onChange, onFocus, onBlur },
    meta: { touched, error, active },
    config: { ...ckeditorConfig, readOnly: disabled },
  })

  return !ckeditorError ? (
    <>
      <div id={name} ref={setElement} style={editorStyle} />
      {showLoader && <TextareaLoader />}
    </>
  ) : (
    <TextareaError />
  )
}

export const TextAreaField: FC<TextFieldProps> = props => {
  const { name, placeholder, label, validations, disabled } = props

  return (
    <Field<string>
      name={name}
      validations={validations}
      render={({ input, meta }) => (
        <FormControl label={label} error={meta.touched && meta.error}>
          <TextArea
            input={input}
            meta={meta}
            disabled={disabled}
            placeholder={placeholder}
          />
        </FormControl>
      )}
    />
  )
}
