import { FC, useState, useEffect } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import {
  AspectRatioBox,
  AspectRatioBoxBody,
} from '@jifflenow/brightui/aspect-ratio-box/index'
import { Card } from '@jifflenow/brightui/card'
import { PLACEMENT } from '@jifflenow/brightui/tooltip'
import { formatDateRange, formatToStartOfTheDay } from 'utils/dates'
import {
  EventStatus,
  EventProps,
  cardOverrides,
  noBannerAspectRatioBoxBody,
  imageBoxAspectRatioBoxBody,
  imageAspectRatioBoxBody,
} from 'components/Events'
import { AutoTooltip } from 'ui-components/AutoTooltip'
import { getCardLink } from 'components/Events/Items/commons'
import { useWindowSize } from 'utils/useWindowSize'
import {
  Body,
  CardTitle,
  EventActionsContainer,
  ImageAnchor,
  Anchor,
  EventStatusWrapper,
  Wrapper,
} from './styled-components'
import { UserActions } from './UserActions'

const messages = defineMessages({
  bannerAltText: {
    defaultMessage: 'Instance banner appears here',
    description: 'Event banner image alt text',
  },
})

export const EventCard: FC<EventProps & { eventCount: number }> = props => {
  const { formatMessage } = useIntl()
  const [location, setLocation] = useState(props.location)
  const { windowWidth } = useWindowSize()

  const {
    id,
    attributes: {
      image,
      displayName,
      address,
      startDate,
      endDate,
      filterStatus,
      mode,
      category,
    },
    standardFields,
  } = location

  const userActionType =
    location.relationships?.actions?.userProfileAction ?? 'viewOnly'

  useEffect(() => {
    setLocation(props.location)
  }, [props.location])

  const isFullMode = props.eventCount < 4 && windowWidth > 1660

  const cardLink = getCardLink(location)

  return (
    <Card overrides={cardOverrides(isFullMode)}>
      <AspectRatioBox
        overrides={imageBoxAspectRatioBoxBody}
        aspectRatio={2 / 1}
      >
        <ImageAnchor href={cardLink}>
          {image && (
            <AspectRatioBoxBody
              overrides={imageAspectRatioBoxBody(isFullMode)}
              as="img"
              src={image}
            />
          )}
          {!image && (
            <AspectRatioBoxBody overrides={noBannerAspectRatioBoxBody}>
              {formatMessage(messages.bannerAltText)}
            </AspectRatioBoxBody>
          )}
        </ImageAnchor>
      </AspectRatioBox>
      <AspectRatioBox aspectRatio={isFullMode ? 15 / 12.4 : 15 / 15}>
        <Wrapper>
          <Anchor href={cardLink}>
            <EventStatusWrapper>
              <EventStatus
                status={filterStatus}
                mode={mode}
                category={category}
              />
            </EventStatusWrapper>
            <AutoTooltip
              content={displayName}
              showArrow
              placement={PLACEMENT.top}
            >
              <CardTitle $isFullMode={isFullMode}>{displayName}</CardTitle>
            </AutoTooltip>
            <AutoTooltip content={address} showArrow>
              <Body $isFullMode={isFullMode}>{address}</Body>
            </AutoTooltip>
            {startDate && endDate && location.attributes.subType !== 'Emeet' && (
              <AutoTooltip
                content={formatDateRange(
                  formatToStartOfTheDay(startDate),
                  formatToStartOfTheDay(endDate)
                )}
                showArrow
              >
                <Body $isFullMode={isFullMode}>
                  {formatDateRange(
                    formatToStartOfTheDay(startDate),
                    formatToStartOfTheDay(endDate)
                  )}
                </Body>
              </AutoTooltip>
            )}

            {standardFields &&
              Object.keys(standardFields).map(key => (
                <AutoTooltip key={key} content={standardFields[key]} showArrow>
                  <Body $isFullMode={isFullMode}>
                    {`${key}: ${standardFields[key]}`}
                  </Body>
                </AutoTooltip>
              ))}
          </Anchor>
          <EventActionsContainer>
            {userActionType !== 'viewOnly' && (
              <UserActions
                isUserMapped={userActionType === 'add'}
                locationId={id}
              />
            )}
          </EventActionsContainer>
        </Wrapper>
      </AspectRatioBox>
    </Card>
  )
}
