import { VFC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import {
  Header,
  Root,
  useCampaign,
  CampaignProvider,
  NoItemsRoot,
  NoItemsText,
  EventSelectWrapper,
  EventTypeAndLocationFilter,
} from '../Common'
import { EventSelectLabel } from '../Common/EventSelectLabel'
import { useEventType } from '../Common/useEventType'
import { useProfileUser } from '../providers/ProfileUserProvider'
import { SettingsForm } from './SettingsForm'
import { SettingsLoader } from './SettingsLoader'
import { TitleRoot, Body } from './styled-components'
import { SettingsProps, UserSettingsProps } from './types'

const messages = defineMessages({
  title: {
    defaultMessage: 'Settings for',
    description: 'Page title',
  },
  noCampaignsListed: {
    defaultMessage:
      'No settings to show as you are not mapped to any campaigns yet.',
    description: 'Empty Message',
  },
})

const UserSettings: VFC<UserSettingsProps> = props => {
  const { formatMessage } = useIntl()
  const user = useProfileUser()
  const { campaign } = useCampaign()
  const location = new URLSearchParams(window.location.search).get('location')
  const eventType = useEventType()

  return (
    <Root>
      <Header />
      <Body>
        <TitleRoot>
          <EventSelectLabel title={formatMessage(messages.title)} />
          <EventSelectWrapper $location={!!location}>
            <EventTypeAndLocationFilter eventType={eventType} />
          </EventSelectWrapper>
        </TitleRoot>
        {campaign ? (
          <SettingsForm
            user={user}
            campaign={campaign}
            eventType={eventType}
            {...props}
          />
        ) : (
          <NoItemsRoot>
            <NoItemsText>
              {campaign === null ? (
                <SettingsLoader />
              ) : (
                formatMessage(messages.noCampaignsListed)
              )}
            </NoItemsText>
          </NoItemsRoot>
        )}
      </Body>
    </Root>
  )
}

export const Settings: VFC<SettingsProps> = props => (
  <CampaignProvider>
    <UserSettings {...props} />
  </CampaignProvider>
)
