import { useState, VFC } from 'react'
import { useLocation } from 'react-router-dom'
import { useEventUser } from 'rest'
import { ErrorPage } from 'ui-components/ErrorPage'
import { ListLoader } from '../Common'
import { useProfileUser } from '../providers/ProfileUserProvider'
import { EngagementTypesView } from './EngagementTypesView'
import { PageContainer } from './styled-components'
import { EngagementTypesLocationViewProps, Location } from './types'

export const EngagementTypesLocationView: VFC<EngagementTypesLocationViewProps> = props => {
  const { locations } = props
  const userId = useProfileUser().id
  const defaultLocation = new URLSearchParams(useLocation().search).get(
    'location'
  )

  const [selectedLocations, setSelectedLocations] = useState<Location[]>([
    (defaultLocation && locations.find(({ id }) => id === defaultLocation)) ||
      locations[0],
  ])

  const {
    id: location_uuid,
    attributes: { systemName: subSystem },
  } = selectedLocations[0]

  const { isLoading, isError, refetch, data } = useEventUser({
    path: { subSystem, userId },
    query: { location_uuid },
  })

  if (isLoading) {
    return (
      <PageContainer>
        <ListLoader />
      </PageContainer>
    )
  }

  if (isError || !data) {
    return (
      <PageContainer>
        <ErrorPage onButtonClick={() => refetch()} />
      </PageContainer>
    )
  }

  return (
    <EngagementTypesView
      {...props}
      selectedLocations={selectedLocations}
      setSelectedLocations={setSelectedLocations}
      user={data.data}
    />
  )
}
