import { VFC, useState, useEffect, useMemo } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { useUserForms } from 'rest'
import { useMe } from 'providers/MeProvider'
import { showErrorToast } from 'utils/toast'
import { Header } from '../Common'
import { Root, Body, Container } from '../Common/styled-components'
import { useProfileUser } from '../providers/ProfileUserProvider'
import { EditProfile } from './EditProfile'
import { ViewProfile } from './ViewProfile'
import { ProfileLoader } from './ProfileLoader'
import { ProfileProps } from './types'

const messages = defineMessages({
  genericError: {
    defaultMessage: 'Something is not right. Please retry.',
    description: 'Error message',
  },
})

export const Profile: VFC<ProfileProps> = ({ onNavigate }) => {
  const { formatMessage } = useIntl()
  const user = useProfileUser()

  const [isViewMode, setIsViewMode] = useState(
    !!useMe().meta.eMeetProfileSetupReminded
  )

  const { error, isPending, data: formData } = useUserForms({
    userType: user.attributes.userType,
  })

  const formFields = useMemo(
    () =>
      formData?.data.map(field =>
        field.attributes.fieldName === 'secondaryEmail'
          ? {
              ...field,
              attributes: { ...field.attributes, fieldName: 'secondaryEmails' },
            }
          : field
      ) ?? [],
    [formData?.data]
  )

  if (error) {
    showErrorToast(formatMessage(messages.genericError))
  }

  useEffect(() => {
    if (localStorage.getItem('profile.showInEdit') === 'true') {
      localStorage.removeItem('profile.showInEdit')
      setIsViewMode(false)
    }
  }, [])

  return (
    <Root>
      <Header />
      <Body>
        {isPending && (
          <Container>
            <ProfileLoader />
          </Container>
        )}
        {!isPending && user && formData ? (
          isViewMode ? (
            <ViewProfile
              user={user}
              fields={formFields}
              onEditClick={() => setIsViewMode(false)}
            />
          ) : (
            <EditProfile
              user={user}
              fields={formFields}
              onCancel={() => setIsViewMode(true)}
              onNavigate={onNavigate}
            />
          )
        ) : null}
      </Body>
    </Root>
  )
}
