import { ChangeEvent, FC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { Toggle } from '@jifflenow/brightui/toggle'
import { SettingTypeContainer, SettingsLabel } from './styled-components'
import { DefaultMappingsSettings, ToggleSettingsProps } from './types'

const names = defineMessages<keyof DefaultMappingsSettings>({
  defaultCanManage: {
    defaultMessage: 'Manage all meeting types',
    description: 'Default settings type',
  },
  defaultCanParticipate: {
    defaultMessage: 'Can participate in all future created engagement types',
    description: 'Default settings type',
  },
  defaultCanRequest: {
    defaultMessage: 'Can request all future created engagement types',
    description: 'Default settings Title',
  },
})

export const ToggleSettings: FC<ToggleSettingsProps> = ({
  name,
  disabled,
  value,
  onChange,
}) => {
  const { formatMessage } = useIntl()

  return (
    <SettingTypeContainer>
      <SettingsLabel>{formatMessage(names[name])}</SettingsLabel>
      <Toggle
        checked={value}
        onChange={(e: ChangeEvent<HTMLInputElement>) =>
          onChange(name, e.target.checked)
        }
        disabled={disabled}
      />
    </SettingTypeContainer>
  )
}
