import { FC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { formatDate, formatDateRange } from 'utils/dates'
import {
  getJSDate,
  diffInISODates,
  getFormattedTimeFromDate as getTime,
} from '../../DateTimeConversions'
import { GroupedException } from '../types'
import {
  ExceptionDate,
  ExceptionTime,
  ListItemRoot,
  Unavailable,
} from './styled-components'

const messages = defineMessages({
  unavailable: {
    defaultMessage: 'Unavailable',
    description: 'Exception time slot representation',
  },
})

export const ListItem: FC<{ exception: GroupedException }> = props => {
  const { formatMessage } = useIntl()
  const { date, available, unavailable } = props.exception

  return (
    <ListItemRoot>
      <ExceptionDate>
        {date.length === 1
          ? formatDate(getJSDate(date[0]))
          : formatDateRange(
              getJSDate(date[0]),
              getJSDate(date[date.length - 1])
            )}
      </ExceptionDate>
      <ExceptionTime>
        {available.map((range, index) => (
          <ExceptionTime key={index}>{`${getTime(range[0])}  ${getTime(
            range[1]
          )}`}</ExceptionTime>
        ))}
        {unavailable.length === 1 &&
        diffInISODates(unavailable[0][0], unavailable[0][1]).days === 1 ? (
          <Unavailable>{formatMessage(messages.unavailable)}</Unavailable>
        ) : (
          unavailable.map((range, index) => (
            <ExceptionTime key={index}>{`${getTime(range[0])}  ${getTime(
              range[1]
            )} (${formatMessage(messages.unavailable)})`}</ExceptionTime>
          ))
        )}
      </ExceptionTime>
    </ListItemRoot>
  )
}
