import { VFC, ChangeEvent } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { Field } from 'react-final-form'
import { Toggle } from '@jifflenow/brightui/toggle'
import { components } from 'types/openapi'
import { ToggleProps } from './types'
import { FieldLabel, ToggleRoot, ToggleWrapper } from './styled-components'

type SettingNames = components['schemas']['UserProfileSetting']['name']

const messages = defineMessages<SettingNames>({
  cisEnabled: {
    description: 'Field Label',
    defaultMessage: 'Calendar Integration',
  },
  irpEnabled: { description: 'Field Label', defaultMessage: 'Enable for IRP' },
  doubleBookingEnabled: {
    description: 'Field Label',
    defaultMessage: 'Double Booking',
  },
  showOnlyForMM: {
    description: 'Field Label',
    defaultMessage: 'Show only for Meeting Managers',
  },
  showInMeeting: {
    description: 'Field Label',
    defaultMessage: 'Show as external attendee',
  },
  endorsement: { description: 'Field Label', defaultMessage: 'Endorsement' },
  partialParticipationInMeeting: {
    description: 'Field Label',
    defaultMessage: 'Enable partial participation in meeting',
  },
})

export const ToggleField: VFC<ToggleProps> = ({ isDisabled, name }) => {
  const { formatMessage } = useIntl()

  const label = formatMessage(messages[name as keyof typeof messages])

  return (
    <Field<boolean>
      name={name}
      render={({
        input: { value, onChange },
        meta: { error, submitError },
      }) => (
        <ToggleRoot $error={submitError || error}>
          <FieldLabel>{label}</FieldLabel>
          <ToggleWrapper>
            <Toggle
              name={name}
              ariaLabel={label}
              // Negating the value to match the Non-ECS flow, We dont know why the value is negated and saved.
              checked={name === 'endorsement' ? !value : value}
              disabled={isDisabled}
              onChange={(e: ChangeEvent<HTMLInputElement>) => {
                const isChecked = e.target.checked

                return onChange(name === 'endorsement' ? !isChecked : isChecked)
              }}
            />
          </ToggleWrapper>
        </ToggleRoot>
      )}
    />
  )
}
