import { VFC, useState } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { FormattedMessage } from 'react-intl'
import { KIND } from '@jifflenow/brightui/button'
import { ConfirmationDialog } from 'ui-components/ConfirmationDialog'
import { ButtonProps } from './types'
import { FooterButton } from './styled-components'

const messages = defineMessages({
  confirmationHeader: {
    defaultMessage: 'Unsaved Changes',
    description: 'Modal heading',
  },
  confirmationMessage: {
    defaultMessage:
      'You have unsaved changes, would you like to save and continue?',
    description: 'Confirmation message',
  },
  cancelButtonLabel: {
    defaultMessage: 'Discard',
    description: 'Button label',
  },
  successButtonLabel: {
    defaultMessage: 'Save',
    description: 'Button label',
  },
})

export const CancelButton: VFC<ButtonProps> = props => {
  const { formatMessage } = useIntl()
  const [showConfirmation, setShowConfirmation] = useState(false)

  const handleCancel = () => setShowConfirmation(true)

  const onSuccess = () => setShowConfirmation(false)
  const onClose = () => {
    setShowConfirmation(false)
    props.handleDiscard?.()
  }

  return (
    <>
      {showConfirmation && (
        <ConfirmationDialog
          onClose={onClose}
          onCancel={onClose}
          onSuccess={onSuccess}
          heading={formatMessage(messages.confirmationHeader)}
          message={formatMessage(messages.confirmationMessage)}
          cancelLabel={formatMessage(messages.cancelButtonLabel)}
          successLabel={formatMessage(messages.successButtonLabel)}
        />
      )}
      <FooterButton
        type="button"
        kind={KIND.tertiary}
        onClick={handleCancel}
        disabled={props.isDisabled}
        data-pendo={`${props.pendoPrefix}/cancel`}
      >
        <FormattedMessage defaultMessage="Cancel" description="Button label" />
      </FooterButton>
    </>
  )
}
