import { FC, useState, useEffect } from 'react'
import { useLocation } from 'react-router-dom'
import { DateTime } from 'luxon'
import { Select, OnChangeParams, Option } from '@jifflenow/brightui/select'
import { useUserMappedCampaigns } from 'rest'
import { useIntl } from 'react-intl'
import { filterOptionLabel } from 'ui-components/common/messages'
import { useProfileUser } from '../../providers/ProfileUserProvider'
import { StatusText } from '../styled-components'
import { Campaign, Mode } from '../types'
import { useCampaign } from './CampaignProvider'
import { EventType } from './types'
import { campaignFilterOverride } from './overrides'

const optionsMapper = ({ location, displayName, mode }: Campaign) => ({
  id: location,
  label: displayName,
  mode,
})

const currentDate = DateTime.local()

export const CampaignFilter: FC<{
  setDefaultValue?: boolean
  eventType?: string
  eventTypes?: EventType[]
}> = ({ setDefaultValue = true, eventType, eventTypes }) => {
  const userId = useProfileUser().id
  const { campaign, setCampaign } = useCampaign()
  const [options, setOptions] = useState<Campaign[]>([])
  const { formatMessage } = useIntl()

  const defaultLocation = new URLSearchParams(useLocation().search).get(
    'location'
  )

  const { run: fetchCampaigns, isLoading } = useUserMappedCampaigns({
    id: userId,
    eventType: eventTypes?.length ? eventTypes : eventType,
    // TODO: Check with Products for the date range, considered 2 years for now.
    dateInterval: `${currentDate.toISODate()}--${currentDate
      .plus({ years: 2 })
      .toISODate()}`,
    onResolve: ({ data }) => {
      const campaigns = data.map(
        ({ id, attributes: { systemName, ...rest } }) => ({
          location: id,
          eventName: systemName,
          ...rest,
        })
      )

      setOptions(campaigns)
      setDefaultValue &&
        setCampaign(
          (defaultLocation &&
            campaigns.find(({ location }) => location === defaultLocation)) ||
            campaigns[0]
        )
    },
  })

  useEffect(() => {
    fetchCampaigns({ eventTypes })
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [eventTypes])

  const OptionLabel = ({ option: { label, mode } }: Option) => (
    <>
      {label}
      {mode && (
        <StatusText>
          {formatMessage(filterOptionLabel[mode as Mode])}
        </StatusText>
      )}
    </>
  )

  return !defaultLocation ? (
    <Select
      searchInput
      clearable={false}
      onCloseResetsInput
      onBlurResetsInput
      placeholder="Select"
      isLoading={isLoading}
      options={options.map(optionsMapper)}
      value={(campaign ? [campaign] : []).map(optionsMapper)}
      getOptionLabel={OptionLabel}
      onChange={({ option }: OnChangeParams) => {
        const campaign = options.find(
          ({ location }) => location === option?.id
        )!

        setCampaign(campaign)
      }}
      overrides={campaignFilterOverride}
    />
  ) : null
}
