import { DateTime } from 'luxon'
import {
  createContext,
  useMemo,
  FC,
  useState,
  Dispatch,
  SetStateAction,
  useContext,
  useEffect,
} from 'react'
import { useDirtyCheck } from 'components/ProfileDrawer/providers/ProfileSectionDirtyCheckProvider'
import { useCombinedUserAvailability } from 'rest'
import { ErrorPage } from 'ui-components/ErrorPage'
import { ListLoader } from '../Exceptions'
import { UTCMinutesCombinedAvailability } from '../types'
import { CombinedAvailability as CombinedAvailabilityDay } from './types'
import { getAvailabilityInTimeZone } from './transform'

type CombinedAvailability = CombinedAvailabilityDay[]

const getStateDate = (date: string) =>
  DateTime.fromISO(date).plus({ day: -1 }).toISODate()

const getEndDate = (date: string) =>
  DateTime.fromISO(date).plus({ day: 1 }).toISODate()

type CombinedAvailabilityContext = {
  defaultAvailability: CombinedAvailability
  setDefaultAvailability: Dispatch<SetStateAction<CombinedAvailability>>
  availability: CombinedAvailability
  setAvailability: Dispatch<SetStateAction<CombinedAvailability>>
  UTCAvailability: UTCMinutesCombinedAvailability[]
  isDirty: boolean
  resetAvailability: () => void
  setUTCAvailability: Dispatch<SetStateAction<UTCMinutesCombinedAvailability[]>>
}

const CombinedAvailability = createContext<
  CombinedAvailabilityContext | undefined
>(undefined)

export const CombinedAvailabilityProvider: FC<{
  timeZoneId: string
  userId: string
  dateRange: string[]
  filterRange: string[]
  setDateRange: Dispatch<SetStateAction<string[]>>
}> = ({
  timeZoneId,
  userId,
  dateRange,
  children,
  setDateRange,
  filterRange,
}) => {
  const clipOverFlowDates = ({ date }: { date: string }) =>
    date >= dateRange[0] && date <= dateRange[1]

  const [isDirty, setIsDirty] = useState(false)
  const [defaultAvailability, setDefaultAvailability] = useState(
    [] as CombinedAvailability
  )
  const [availability, setAvailability] = useState([] as CombinedAvailability)
  const [UTCAvailability, setUTCAvailability] = useState(
    [] as UTCMinutesCombinedAvailability[]
  )
  const { onCloseConfirmation } = useDirtyCheck()

  const resetAvailability = useMemo(
    () => () => {
      setAvailability(defaultAvailability)
      setIsDirty(false)
    },
    [defaultAvailability]
  )

  useEffect(() => {
    onCloseConfirmation(() => () => {
      isDirty && resetAvailability()
      setDateRange(filterRange)
    })
  }, [
    filterRange,
    setDateRange,
    isDirty,
    onCloseConfirmation,
    resetAvailability,
  ])

  useEffect(() => {
    setIsDirty(
      JSON.stringify(availability) !== JSON.stringify(defaultAvailability)
    )
  }, [availability, defaultAvailability])

  useEffect(() => {
    if (UTCAvailability.length) {
      const availability = getAvailabilityInTimeZone(
        UTCAvailability,
        timeZoneId
      ).filter(clipOverFlowDates)
      setAvailability(availability)
      setDefaultAvailability(availability)
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [timeZoneId])

  const { isError, refetch, isFetching } = useCombinedUserAvailability(
    {
      path: { userId },
      query: {
        startDate: getStateDate(dateRange[0]),
        endDate: getEndDate(dateRange[1]),
      },
    },
    {
      onSuccess: ({ data: { availabilities } }) => {
        const transformedAvailabilities = getAvailabilityInTimeZone(
          availabilities,
          timeZoneId
        ).filter(clipOverFlowDates)

        setDefaultAvailability(transformedAvailabilities)
        setAvailability(transformedAvailabilities)
        setUTCAvailability(availabilities)
      },
    }
  )

  const value = useMemo<CombinedAvailabilityContext>(
    () => ({
      defaultAvailability,
      availability,
      setDefaultAvailability,
      setAvailability,
      UTCAvailability,
      setUTCAvailability,
      resetAvailability,
      isDirty,
    }),
    [
      defaultAvailability,
      availability,
      UTCAvailability,
      isDirty,
      resetAvailability,
    ]
  )

  if (isError) {
    return (
      <ErrorPage
        onButtonClick={() => {
          refetch()
        }}
      />
    )
  }

  if (isFetching) {
    return <ListLoader />
  }

  return (
    <CombinedAvailability.Provider value={value}>
      {children}
    </CombinedAvailability.Provider>
  )
}

export const useCombinedAvailability = () => {
  const context = useContext(CombinedAvailability)
  if (context === undefined && process.env.NODE_ENV !== 'production') {
    throw new Error(
      'useCombinedAvailability must be used within a CombinedAvailabilityProvider'
    )
  }

  return context!
}
