import { VFC } from 'react'
import { DateTime } from 'luxon'
import { defineMessages, useIntl } from 'react-intl'
import { IconButton } from '@jifflenow/brightui/button'
import { StatefulTooltip } from '@jifflenow/brightui/tooltip'
import { RefetchIcon } from 'ui-components/icons/RefetchIcon'
import { MappedCISUsersField } from './MappedCISUsersField'
import { iconButtonOverrides } from './overrides'
import {
  LastSyncedTime,
  CISConfigContainer,
  LastSyncedTimeContainer,
} from './styled-components'
import { CisSyncSettingProps } from './types'

const messages = defineMessages({
  fetchFreeBusy: {
    defaultMessage: 'Fetch Free/Busy',
    description: 'Fetch Free/Busy label',
  },
  LastSyncedTime: {
    defaultMessage: 'Last Synced Time',
    description: 'Sync time label',
  },
  fetchFreeBusyAriaLabel: {
    defaultMessage: 'Fetch free/busy',
    description: 'Aria label',
  },
})

export const CisSyncSetting: VFC<CisSyncSettingProps> = ({
  cisDetails,
  onHandleFetchFreeBusy,
}) => {
  const { formatMessage } = useIntl()

  return (
    <CISConfigContainer>
      <MappedCISUsersField mappedCISUserEmails={cisDetails?.userMappedEmails} />
      <LastSyncedTimeContainer>
        {cisDetails?.lastSyncTime && (
          <LastSyncedTime>{`${formatMessage(
            messages.LastSyncedTime
          )}: ${DateTime.fromISO(cisDetails?.lastSyncTime).toFormat(
            'FFF'
          )}`}</LastSyncedTime>
        )}

        <StatefulTooltip content={formatMessage(messages.fetchFreeBusy)}>
          <IconButton
            type="button"
            size={20}
            overrides={iconButtonOverrides}
            onClick={onHandleFetchFreeBusy}
            aria-label={formatMessage(messages.fetchFreeBusyAriaLabel)}
          >
            <RefetchIcon />
          </IconButton>
        </StatefulTooltip>
      </LastSyncedTimeContainer>
    </CISConfigContainer>
  )
}
