import { FC } from 'react'
import { fieldTypesMappings } from 'components/CustomFormFields/FormFields'
import { Validation } from 'ui-components/Field'
import { PrimaryEmail } from './PrimaryEmail'
import { SecondaryEmail } from './SecondaryEmail'
import { StandardFieldsProps } from './types'

export const StandardFormFields: FC<StandardFieldsProps> = ({
  fields: fieldsList,
}) => (
  <>
    {fieldsList.map((field, index) => {
      if (field.name === 'email') {
        return <PrimaryEmail key={index} {...field} />
      }

      if (field.name === 'secondaryEmails') {
        return <SecondaryEmail key={index} {...field} />
      }

      const FormField = fieldTypesMappings[field.fieldType]
      const { required, email } = field.validations ?? {}
      const validations = [
        required && Validation.REQUIRED,
        email && Validation.EMAIL,
      ].filter(Boolean) as Validation[]

      return <FormField {...field} key={index} validations={validations} />
    })}
  </>
)
