import { VFC, useState } from 'react'
import { Checkbox } from '@jifflenow/brightui/checkbox'
import { CopySlotsRoot, WeekLabel, WeekLabelRoot } from './styled-components'
import { CopyDaySlotsToProps } from './types'
import { getDateString, getDayFromDate } from './DateTimeConversions'

export const CopyDateSlotsTo: VFC<CopyDaySlotsToProps> = props => {
  const [checked, setChecked] = useState(false)
  const { item, dateFilter } = props

  return (
    <CopySlotsRoot>
      <Checkbox
        checked={checked}
        onChange={() => {
          setChecked(!checked)
          props.copyTo(item)
        }}
      >
        {dateFilter ? (
          <WeekLabelRoot>
            <WeekLabel>{getDayFromDate(item)}</WeekLabel>
            <WeekLabel> {getDateString(item)}</WeekLabel>
          </WeekLabelRoot>
        ) : (
          item
        )}
      </Checkbox>
    </CopySlotsRoot>
  )
}
