import { VFC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { Checkbox } from '@jifflenow/brightui/checkbox'
import { Field } from 'react-final-form'
import { useMe } from 'providers/MeProvider'
import { useProfileUser } from '../../providers/ProfileUserProvider'
import {
  CanManageSettingsFormControl,
  ToggleHelpText,
} from './styled-components'

const messages = defineMessages({
  label: {
    defaultMessage: 'Can manage settings',
    description: 'Field label',
  },
  canManageHelpText: {
    defaultMessage:
      'Applicable only if the role is Meeting Manager at any instance',
    description: 'Help text',
  },
})

export const CanManageSettings: VFC = () => {
  const { formatMessage } = useIntl()
  const {
    attributes: {
      role: loggedInUserRole,
      privileges: { canManageSettings },
    },
    id: loggedInUserId,
  } = useMe().data
  const {
    id: profileUserId,
    attributes: { role: profileUserRole },
  } = useProfileUser()

  const isOwnProfile = loggedInUserId === profileUserId

  return profileUserRole !== 'CSM' &&
    (loggedInUserRole === 'CSM' ||
      (canManageSettings && (isOwnProfile || loggedInUserRole === 'MM'))) ? (
    <Field<boolean>
      name="canManageSettings"
      render={({ input: { value, onChange } }) => (
        <>
          <CanManageSettingsFormControl
            label={formatMessage(messages.label)}
            data-pendo="profile/can-manage-settings"
          >
            <Checkbox
              checkmarkType="toggle_round"
              id="canManageSettings"
              checked={value}
              onChange={e => onChange(e.currentTarget.checked)}
              disabled={isOwnProfile}
            />
          </CanManageSettingsFormControl>
          <ToggleHelpText>
            {formatMessage(messages.canManageHelpText)}
          </ToggleHelpText>
        </>
      )}
    />
  ) : null
}
