import { components } from 'types/openapi'
import { FieldTypes } from 'components/CustomFormFields'
import { Rules, InputType, FormFieldProps } from 'components/CustomFormFields'
import { TransformFieldProps } from './types'

type UserAttributes = components['schemas']['UserAttributes']
type UserCustomFormFieldInfo = components['schemas']['CustomFormFieldInfo']
type UserStandardFormFieldInfo = components['schemas']['UserStandardFormFieldInfo']

type TransformField = (props: TransformFieldProps) => FormFieldProps

type StandardFields = Omit<UserAttributes, 'customFields' | 'active'>

const inputFieldTypes: { [key: string]: InputType } = {
  textfield: InputType.TEXT,
  numberfield: InputType.NUMBER,
  email: InputType.EMAIL,
}

const getCustomFieldName = (name: string) => `customFields.${name}`

export const transformField: TransformField = props => {
  const { user, field } = props
  const { type } = field
  const { fieldName, displayName, fieldInfo } = field.attributes
  const isACustomField =
    type === 'InternalUserCustomForm' || type === 'ExternalUserCustomForm'
  const {
    helpText,
    placeholder,
    show,
    mandatory,
  } = fieldInfo as UserStandardFormFieldInfo
  const {
    disabled,
    hidden,
    label,
    rules_with_values: rulesWithValues = [],
    values,
    widget = 'textfield',
    required,
  } = fieldInfo as UserCustomFormFieldInfo

  return {
    disabled: !isACustomField && fieldName === 'email' ? true : disabled,
    fieldType: widget as FieldTypes,
    helpText,
    hidden: hidden || show,
    name: isACustomField ? getCustomFieldName(fieldName) : fieldName,
    label: displayName || label,
    placeholder,
    rules: rulesWithValues.map(({ conditions = [], source, ...rest }) => ({
      ...rest,
      source: getCustomFieldName(source),
      conditions: conditions.map(
        ({
          values_to_display: valuesToDisplay,
          values_to_check: valuesToCompare,
          role_names_to_check: roleNamesToCheck,
          ...rest
        }) => ({ ...rest, valuesToCompare, valuesToDisplay, roleNamesToCheck })
      ),
    })) as Rules[],
    options: values as string[],
    type: inputFieldTypes[fieldName === 'email' ? 'email' : widget],
    validations: { required: Boolean(required || mandatory) },
    value:
      user[fieldName as keyof StandardFields] ||
      user?.customFields?.[fieldName],
    ...(widget === 'multiselect_dropdown' ? { multi: true } : null),
    ...(widget === 'checkbox' ? { checked: values === 'yes' } : null),
  }
}
