import { VFC, useState } from 'react'
import { useIntl } from 'react-intl'
import { weekDays } from 'utils/messages'
import { Checkbox } from '@jifflenow/brightui/checkbox'
import { Weekdays } from '../Common/weekDayMappings'
import { CopySlotsRoot } from './styled-components'
import { CopyDaySlotsToProps } from './types'

export const CopyDaySlotsTo: VFC<CopyDaySlotsToProps> = props => {
  const { formatMessage } = useIntl()
  const [checked, setChecked] = useState(false)
  const { weekDay } = props

  return (
    <CopySlotsRoot>
      <Checkbox
        checked={checked}
        onChange={() => {
          setChecked(!checked)
          props.addCopyDestinationDays(weekDay)
        }}
      >
        {formatMessage(weekDays[Weekdays[weekDay] as keyof typeof weekDays])}
      </Checkbox>
    </CopySlotsRoot>
  )
}
