import { useState, ChangeEvent, VFC, useEffect } from 'react'
import { Input } from '@jifflenow/brightui/input'
import { useHttpClient } from 'providers/HttpClientContext'
import { putUserEngagementsMapping } from 'rest'
import { defineMessages, useIntl } from 'react-intl'
import { showSuccessToast, showErrorToast } from 'utils/toast'
import { useBeforeUnload } from 'utils/useBeforeUnload'
import { ModalSpinner } from '@jifflenow/brightui/modal-spinner'
import { Search } from '@jifflenow/brightui/icon'
import { useProfileUser } from '../providers/ProfileUserProvider'
import { Root, Body } from '../Common/styled-components'
import { useDirtyCheckDialog } from '../Common/useDirtyCheckDialog'
import {
  Header,
  useCampaign,
  NoItemsRoot,
  NoItemsText,
  ListLoader,
  Footer,
  EventSelectWrapper,
  EventTypeAndLocationFilter,
} from '../Common'
import { EventSelectLabel } from '../Common/EventSelectLabel'
import { useEventType } from '../Common/useEventType'
import {
  InputControl,
  TitleSearchContainer,
  TitleContainer,
  Container,
  FooterContainer,
} from './styled-components'
import { useSelection } from './MappedTopicsSelectionsProvider'
import { useSelection as useUnMappedSelection } from './UnMappedTopicsSelectionProvider'
import { ItemsAgainstIDs, DefaultSelections, TopicsMappingProps } from './types'
import { TopicsList } from './TopicsList'

const messages = defineMessages({
  title: {
    defaultMessage: 'Topics for',
    description: 'Page title',
  },
  search: {
    defaultMessage: 'Topics Search',
    description: 'Placeholder text',
  },
  success: {
    defaultMessage: 'Saved Successfully.',
    description: 'Success message',
  },
  noCampaignsListed: {
    defaultMessage:
      'No topics to show as you are not mapped to any campaigns yet.',
    description: 'Empty Message',
  },
  genericError: {
    defaultMessage: 'Error Saving.',
    description: 'Error message',
  },
})

export const TopicsType: VFC<TopicsMappingProps> = ({ onNavigate }) => {
  const { campaign } = useCampaign()
  const unload = useBeforeUnload()
  const user = useProfileUser()
  const userId = user.id
  const [refresh, doRefresh] = useState(0)
  const [showLoader, setShowLoading] = useState(false)
  const [search, setSearch] = useState<string>('')
  const location = new URLSearchParams(window.location.search).get('location')
  const [defaultSelections, setDefaultSelections] = useState<DefaultSelections>(
    {
      mapped: {},
      unMapped: {},
    }
  )

  const eventType = useEventType()
  const client = useHttpClient()()

  const {
    setPageNumber: setMappedPageNumber,
    isAllSelected: mappedAllSelected,
    isMappedUuidListEmpty,
    mappedUuidList,
    selections: mappedTopicSelections,
    clearAll: clearAllMapped,
  } = useSelection()
  const {
    setPageNumber: setUnMappedPageNumber,
    isAllSelected: unmappedAllSelected,
    isUnMappedUuidListEmpty,
    unMappedUuidList,
    selections: unMappedTopicSelections,
    clearAll: clearAllUnMapped,
  } = useUnMappedSelection()

  const { formatMessage } = useIntl()

  const handleSubmit = async () => {
    setShowLoading(true)
    const mapped = {} as ItemsAgainstIDs
    const unMapped = {} as ItemsAgainstIDs

    mappedUuidList().forEach(key => {
      mapped[key] = {
        mapTo:
          mappedTopicSelections()[key]?.mapTo ??
          defaultSelections.mapped[key].mapTo,
      }
    })

    unMappedUuidList().forEach(key => {
      unMapped[key] = {
        mapTo:
          unMappedTopicSelections()[key]?.mapTo ??
          defaultSelections.unMapped[key].mapTo,
      }
    })

    const selectionMapper = (selection: ItemsAgainstIDs) =>
      Object.entries(selection).map(item => ({
        mappingEntityUuid: item[0],
        mappingAttributes: item[1],
      }))

    const mappedSelectionData = selectionMapper(mapped)
    const unMappedSelectionData = selectionMapper(unMapped)

    const payload = {
      ...(mappedAllSelected().mapTo !== undefined && {
        mapped: {
          selectAll: {
            mapTo: mappedAllSelected().mapTo,
          },
        },
      }),
      ...(unmappedAllSelected().mapTo !== undefined && {
        unmapped: {
          selectAll: {
            mapTo: unmappedAllSelected().mapTo,
          },
        },
      }),
      mappings: [...mappedSelectionData, ...unMappedSelectionData],
    }
    try {
      await putUserEngagementsMapping({
        client,
        id: userId,
        mappingType: 'TopicMapping',
        subSystem: campaign?.eventName as string,
        payload,
        locationId: campaign?.location as string,
        searchTerm: search,
      })
      clearAllMapped()
      clearAllUnMapped()
      setShowLoading(false)
      showSuccessToast(formatMessage(messages.success))
      setMappedPageNumber(1)
      setUnMappedPageNumber(1)
      doRefresh(prev => prev + 1)
      return undefined
    } catch (error) {
      setShowLoading(false)
      showErrorToast(formatMessage(messages.genericError))
      return error
    }
  }

  const handleSaveAndNext = async () => {
    const successFullSubmission = await handleSubmit()
    successFullSubmission === undefined && onNavigate?.()
  }

  const handleSave = async () => {
    await handleSubmit()
  }

  const isDirty =
    isUnMappedUuidListEmpty() &&
    isMappedUuidListEmpty() &&
    mappedAllSelected().mapTo === undefined &&
    unmappedAllSelected().mapTo === undefined

  useDirtyCheckDialog({
    isDirty: !isDirty,
    onSave: handleSubmit,
  })

  useEffect(() => {
    unload(!isDirty)
  }, [isDirty, unload])

  return (
    <>
      {showLoader && <ModalSpinner />}
      <Root>
        <Header />
        <Body>
          <Container>
            <TitleSearchContainer>
              <TitleContainer>
                <EventSelectLabel title={formatMessage(messages.title)} />
                <EventSelectWrapper $location={!!location}>
                  <EventTypeAndLocationFilter eventType={eventType} />
                </EventSelectWrapper>
              </TitleContainer>
              <InputControl>
                <Input
                  type="search"
                  value={search}
                  IconAfter={Search}
                  onChange={(e: ChangeEvent<HTMLInputElement>) =>
                    setSearch(e.target.value)
                  }
                  placeholder={formatMessage(messages.search)}
                />
              </InputControl>
            </TitleSearchContainer>
            {campaign?.location ? (
              <TopicsList
                search={search}
                userId={userId}
                refresh={refresh}
                setDefaultSelections={setDefaultSelections}
                currentLocationUuid={campaign.location}
              />
            ) : (
              <NoItemsRoot>
                <NoItemsText>
                  {campaign === null ? (
                    <ListLoader />
                  ) : (
                    formatMessage(messages.noCampaignsListed)
                  )}
                </NoItemsText>
              </NoItemsRoot>
            )}
          </Container>
        </Body>
        {(Object.keys(defaultSelections.mapped).length ||
          Object.keys(defaultSelections.unMapped).length) && (
          <FooterContainer>
            <Footer
              onSave={handleSave}
              onSaveAndNext={handleSaveAndNext}
              isDisabled={isDirty}
              onDiscard={() => {
                clearAllMapped()
                clearAllUnMapped()
              }}
              pendoPrefix="profile/campaign/topics"
            />
          </FooterContainer>
        )}
      </Root>
    </>
  )
}
