import { Dispatch, SetStateAction } from 'react'
import { components } from 'types/openapi'

type AvailabilityAttrs = components['schemas']['UserAvailability']['attributes']

type Day = AvailabilityAttrs['day']

type WeekDay = 1 | 2 | 3 | 4 | 5 | 6 | 7

export type TimeRangeInMinutes = AvailabilityAttrs['timeRanges']

export type TimeRangeInDates = { start: string; end: string; error?: string }[]

export type UTCMinutesAvailability = {
  day: Day
  timeRanges: TimeRangeInMinutes
}

export type UTCMinutesCombinedAvailability = {
  date: string
  timeRanges: TimeRangeInMinutes
}

export type Availability = {
  day: Day
  weekDay: WeekDay
  timeRanges: TimeRangeInDates
}

export type TimeZone = components['schemas']['PlacesTimeZone']
export type TimeZoneId = TimeZone['timeZoneId']

export type DaysToIds = { [key in Day]: string }

export type AvailabilityProviderProps = {
  userId: string
  timeZoneId: TimeZoneId
}

export type AvailabilityContext = {
  isDirty: boolean
  availability: Availability[]
  setAvailability: Dispatch<SetStateAction<Availability[]>>
  setDefaultAvailability: Dispatch<SetStateAction<Availability[]>>
  UTCAvailability: UTCMinutesAvailability[]
  setUTCAvailability: Dispatch<SetStateAction<UTCMinutesAvailability[]>>
  resetAvailability: () => void
  daysToIdMappings: DaysToIds
}

export enum AvailabilityTypes {
  Availability = 'Availability',
  Exceptions = 'Exceptions',
}

export type AvailabilityTabsProps = {
  timeZone: TimeZone
}
