import { AvatarOverrides } from '@jifflenow/brightui/avatar'
import { TableOverrides } from '@jifflenow/brightui/table-semantic'
import { TagOverrides } from '@jifflenow/brightui/tag'
import { ButtonOverrides } from '@jifflenow/brightui/button'

const Root: TableOverrides['Root'] = {
  style: ({ $theme: { mediaQuery, sizing } }) => ({
    borderStyle: 'none',
    paddingTop: sizing.scale750,
    width: '100%',
    [mediaQuery.medium]: {
      maxWidth: '420px',
    },
  }),
}

const TableHead: TableOverrides['TableHead'] = {
  style: {
    display: 'none',
  },
}

const TableBodyRow: TableOverrides['TableBodyRow'] = {
  style: { ':hover': { backgroundColor: 'transparent' } },
}

const TableBodyCell: TableOverrides['TableBodyCell'] = {
  style: ({ $colIndex, $theme: { typography, colors } }) => ({
    maxWidth: '300px',
    paddingLeft: 0,
    ...typography.Paragraph2,
    ...($colIndex === 0 ? { color: colors.mono700 } : null),
  }),
}

export const tableOverrides = {
  Root: Root as any,
  TableHead: TableHead as any,
  TableBodyRow: TableBodyRow as any,
  TableBodyCell: TableBodyCell as any,
}

export const tagOverrides: TagOverrides = {
  Root: {
    style: ({ $theme: { colors, sizing } }) => ({
      borderLeftColor: colors.mono600,
      borderRightColor: colors.mono600,
      borderTopColor: colors.mono600,
      borderBottomColor: colors.mono600,
      marginLeft: 0,
      marginRight: sizing.scale400,
    }),
  },
  Text: {
    style: ({ $theme: { colors } }) => ({
      color: colors.mono900,
      maxWidth: '160px',
    }),
  },
}

interface StyleProps {
  $didImageFailToLoad: boolean
  $size?: string
}

export const avatarOverrides: AvatarOverrides<StyleProps> = {
  Root: {
    style: ({ $theme: { sizing } }) => ({
      marginRight: sizing.scale600,
    }),
  },
  Initials: {
    style: ({ $theme: { typography } }) => ({
      ...typography.font1150,
    }),
  },
}

export const editButtonOverride: ButtonOverrides = {
  BaseButton: {
    style: {
      flex: 1,
    },
  },
}

export const copyButtonOverrides: ButtonOverrides = {
  BaseButton: {
    style: ({ $theme: { sizing } }) => ({
      backgroundColor: 'transparent',
      borderTopWidth: 0,
      borderRightWidth: 0,
      borderBottomWidth: 0,
      borderLeftWidth: 0,
      paddingLeft: sizing.scale100,
      paddingRight: sizing.scale100,
      paddingTop: sizing.scale100,
      paddingBottom: sizing.scale100,
    }),
  },
}
