import { ChangeEvent, VFC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { Checkbox } from '@jifflenow/brightui/checkbox'
import { CanRequestProps } from './types'

const aria = defineMessages({
  requestMeeting: {
    defaultMessage: 'Request meeting',
    description: 'Aria label',
  },
})

export const CanRequest: VFC<CanRequestProps> = ({
  checked,
  canEdit,

  onChange,
}) => {
  const { formatMessage } = useIntl()

  return (
    <Checkbox
      checked={checked}
      onChange={({ target: { checked } }: ChangeEvent<HTMLInputElement>) =>
        onChange(checked)
      }
      disabled={!canEdit}
      ariaLabel={formatMessage(aria.requestMeeting)}
    />
  )
}
