import { ChangeEvent, useMemo, useState, VFC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { Field } from 'react-final-form'
import { FieldValidator } from 'final-form'
import { Input } from '@jifflenow/brightui/input'
import { useMe } from 'providers/MeProvider'
import { getPrefixedInputOverride } from 'ui-components/common/overrides'
import { useValidatePersonalLinkFetcher } from 'rest'
import { asyncValidatorReducer } from 'utils/asyncValidatorReducer'
import { InputPrefix, Flex } from 'ui-components/common/styled-components'
import { FormControlForURL } from './styled-components'
import { PersonalLinkEditProps } from './types'

const messages = defineMessages({
  personalLinkLabel: {
    defaultMessage: 'Personal Meeting Link',
    description: 'Field label',
  },
  required: {
    defaultMessage: 'Required',
    description: 'Field validation error message',
  },
  userNameNotAvailableMessage: {
    defaultMessage: 'User name not available',
    description: 'Personal link validation error message',
  },
})

const splitAt = (index: number) => (str: string) => [
  str.slice(0, index),
  str.slice(index),
]
export const splitPersonalLink = (link: string | undefined) =>
  (str => splitAt(str.lastIndexOf('/') + 1)(str))(link ?? '')

const simpleMemoize = (fn: FieldValidator<string>): FieldValidator<string> => {
  let lastArg: string
  let lastResult: string

  return async (arg, ...rest) => {
    if (arg !== lastArg) {
      lastArg = arg
      lastResult = await fn(arg, ...rest)
    }

    return lastResult
  }
}

export const PersonalLinkEdit: VFC<PersonalLinkEditProps> = ({
  personalLink,
}) => {
  const { formatMessage } = useIntl()
  const { id: userUuid } = useMe().data
  const validatePersonalLink = useValidatePersonalLinkFetcher()
  const [prefix, previousTokenValue] = splitPersonalLink(personalLink)

  const [loading, setLoading] = useState(false)

  const required: FieldValidator<string> = async token =>
    token ? undefined : formatMessage(messages.required)

  const validateUrl: FieldValidator<string> = useMemo(
    () =>
      simpleMemoize(async token => {
        if (token === previousTokenValue) {
          return undefined
        }
        try {
          setLoading(true)
          const personalLinkValid = await validatePersonalLink({
            token,
            userUuid,
          })
          setLoading(false)
          return personalLinkValid
            ? undefined
            : formatMessage(messages.userNameNotAvailableMessage)
        } catch (error) {
          setLoading(false)
          return undefined
        }
      }),
    [formatMessage, previousTokenValue, userUuid, validatePersonalLink]
  )

  const validate = asyncValidatorReducer([required, validateUrl])

  return (
    <Field<string> name="personalUrlToken" type="text" validate={validate}>
      {({ input, meta }) => {
        const handleChange = (token: string) => {
          input.onChange(token.replace(/[^\dA-Za-z-]/gu, ''))
        }

        return (
          <FormControlForURL
            label={formatMessage(messages.personalLinkLabel)}
            error={(meta.dirty || meta.touched) && meta.error}
          >
            <Flex>
              <InputPrefix>{prefix}</InputPrefix>
              <Input
                {...input}
                onChange={(e: ChangeEvent<HTMLInputElement>) =>
                  handleChange(e.target.value)
                }
                id="personalUrlToken"
                autoComplete="off"
                overrides={getPrefixedInputOverride(loading)}
              />
            </Flex>
          </FormControlForURL>
        )
      }}
    </Field>
  )
}
