import { VFC } from 'react'
import { Button } from '@jifflenow/brightui/button'
import {
  EmptyMessageRoot,
  EmptyMessageText,
  ButtonWrapper,
} from './styled-components'

type EmptyMessageProps = {
  onAddNew?: () => void
  emptyMessage: string
  addNewButtonMessage: string
  showAddButton: boolean | undefined
}

export const EmptyMessage: VFC<EmptyMessageProps> = ({
  onAddNew,
  emptyMessage,
  addNewButtonMessage,
  showAddButton,
}) => (
  <EmptyMessageRoot>
    <EmptyMessageText>{emptyMessage}</EmptyMessageText>
    {showAddButton && (
      <ButtonWrapper>
        <Button onClick={onAddNew}>{addNewButtonMessage}</Button>
      </ButtonWrapper>
    )}
  </EmptyMessageRoot>
)
