import { DateTime } from 'luxon'
import {
  getDateTimeInTimeZone,
  getFullMinutes,
  getISOTimeFromDate,
  getEndDateOfDay,
  getPlusDate,
} from '../DateTimeConversions'
import {
  TimeZoneId,
  TimeRangeInDates,
  TimeRangeInMinutes,
  UTCMinutesCombinedAvailability,
} from '../types'
import { CombinedAvailability } from './types'

// Base empty availability to avoid missing any date after transformations.
const getBaseAvailabitlityHash = (dates: string[]) =>
  dates.reduce((acc, date) => ({ ...acc, [date]: [] }), {})

// Merges continuous time slots after UTC <=> TimeZone transformations
function mergeContinuousTimeRanges<T extends any[]>(timeRanges: T): T {
  return timeRanges
    .sort((a, b) => (a.start >= b.start ? 1 : -1))
    .reduce((acc, curr) => {
      const prevRange = acc.pop()

      return prevRange
        ? prevRange.end === curr.start
          ? [...acc, { start: prevRange.start, end: curr.end }]
          : [...acc, prevRange, curr]
        : [...acc, curr]
    }, [])
}

const transformMinutesInUTC = (data: UTCMinutesCombinedAvailability[]) =>
  data.map(({ date, timeRanges }) => ({
    date,
    timeRanges: timeRanges.map(({ start, end }) => ({
      start: DateTime.fromISO(`${date}T00:00:00Z`)
        .plus({ minutes: start })
        .toISO(),
      end: DateTime.fromISO(`${date}T00:00:00Z`).plus({ minutes: end }).toISO(),
    })),
  }))

export const getAvailabilityInTimeZone = (
  availability: UTCMinutesCombinedAvailability[],
  timeZone: TimeZoneId
) => {
  const availabilityInUTC = transformMinutesInUTC(availability)
  const baseAvailabilityHash = getBaseAvailabitlityHash(
    availability.map(({ date }) => date)
  )
  const reducedAvailability = availabilityInUTC
    .flatMap(({ timeRanges }) =>
      timeRanges.flatMap(range => {
        const startDateTime = getDateTimeInTimeZone(range.start, timeZone)
        const endDateTime = getDateTimeInTimeZone(range.end, timeZone)
        const start = startDateTime.toISO()
        const end = endDateTime.toISO()

        return startDateTime.toISODate() === endDateTime.toISODate()
          ? { start, end }
          : [
              { start, end: getEndDateOfDay(start) },
              { start: endDateTime.startOf('day').toISO(), end },
            ]
      })
    )
    .reduce((accRanges, range) => {
      const { start, end } = range
      const key = DateTime.fromISO(start).toISODate()

      return {
        ...accRanges,
        [key]: [
          ...(accRanges[key] ?? []),
          ...(start === end
            ? []
            : [
                {
                  start: getISOTimeFromDate(start),
                  end: getISOTimeFromDate(end),
                },
              ]),
        ],
      }
    }, baseAvailabilityHash as { [key: string]: TimeRangeInDates })

  return Object.keys(reducedAvailability)
    .sort((next: string, prev: string) => Number(next) - Number(prev))
    .map(date => ({
      date,
      timeRanges: mergeContinuousTimeRanges<TimeRangeInDates>(
        reducedAvailability[date]
      ),
    }))
}

const transformISOTimeToDates = (data: CombinedAvailability[]) =>
  data.map(({ timeRanges, ...rest }) => ({
    ...rest,
    timeRanges: timeRanges.map(({ start, end }) => ({
      start: `${DateTime.fromISO(rest.date).toISODate()}T${start}`,
      end: `${DateTime.fromISO(rest.date).toISODate()}T${end}`,
    })),
  }))

export const getAvailability = (availability: CombinedAvailability[]) => {
  const availabilityWithDates = transformISOTimeToDates(availability)

  const baseAvailabilityHash = getBaseAvailabitlityHash(
    availability.map(({ date }) => date)
  )
  const reducedAvailability = availabilityWithDates
    .flatMap(({ timeRanges }) =>
      timeRanges.flatMap(range => {
        const endDate =
          getFullMinutes(range.end) === 0
            ? getPlusDate(range.end, { day: 1 })
            : range.end

        return { start: range.start, end: endDate }
      })
    )
    .reduce((accRanges, range) => {
      const { start, end } = range
      const key = DateTime.fromISO(start).toISODate()

      const startInMinutes = getFullMinutes(start)
      const endInMinutes = getFullMinutes(end)

      return {
        ...accRanges,
        [key]: [
          ...(accRanges[key] ?? []),
          ...(startInMinutes === endInMinutes && endInMinutes !== 0
            ? []
            : [
                {
                  start: startInMinutes,
                  end: endInMinutes || 1440,
                },
              ]),
        ],
      }
    }, baseAvailabilityHash as { [key: string]: TimeRangeInMinutes })

  return Object.keys(reducedAvailability)
    .sort((next: string, prev: string) => Number(next) - Number(prev))
    .map(date => ({
      date,
      timeRanges: mergeContinuousTimeRanges<TimeRangeInMinutes>(
        reducedAvailability[date]
      ),
    }))
}
