import { VFC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { Checkbox } from '@jifflenow/brightui/checkbox'
import { Field } from 'react-final-form'
import { CheckboxControl } from './styled-components'

const messages = defineMessages({
  label: {
    defaultMessage: 'Can request a new instance',
    description: 'Field label',
  },
})

export const CanCreateInstance: VFC = () => {
  const { formatMessage } = useIntl()

  return (
    <Field<boolean>
      name="canCreateEvent"
      render={({ input: { value, onChange } }) => (
        <CheckboxControl
          label={formatMessage(messages.label)}
          data-pendo="profile/can-request-new-instance"
        >
          <Checkbox
            checkmarkType="toggle_round"
            id="can_create_instance"
            checked={Boolean(value)}
            onChange={e => onChange(e.currentTarget.checked)}
          />
        </CheckboxControl>
      )}
    />
  )
}
