import { VFC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { Button } from '@jifflenow/brightui/button'
import { DownArrow, Settings } from '@jifflenow/brightui/icon'
import { StatefulPopover, PLACEMENT } from '@jifflenow/brightui/popover'
import { ModalSpinner } from '@jifflenow/brightui/modal-spinner'
import { useMe } from 'providers/MeProvider'
import {
  useUserMappingsSettingsMutation,
  useUserMappingsSettingsQuery,
} from 'rest'
import { showErrorToast, showSuccessToast } from 'utils/toast'
import { DefaultMappingsProps, Role, Setting } from './types'
import { ToggleSettings } from './ToggleSettings'
import {
  SettingsCount,
  ArrowContainer,
  ContentContainer,
  SettingsHeader,
} from './styled-components'

const messages = defineMessages({
  title: {
    defaultMessage: 'Map future engagement types',
    description: 'Settings placeholder',
  },
  success: {
    defaultMessage: 'Updated Successfully.',
    description: 'Success message',
  },
  genericError: {
    defaultMessage: 'Error Updating.',
    description: 'Error message',
  },
})

const adminRoles: Role[] = ['CSM', 'MM', 'JMM']
const csmAndMmRoles: Role[] = ['CSM', 'MM']

export const DefaultMappings: VFC<DefaultMappingsProps> = ({
  selectedLocation,
  user,
}) => {
  const { formatMessage } = useIntl()
  const {
    id: loggedInUserId,
    attributes: { role: companyRole, eventRole },
  } = useMe().data
  const {
    id: selectedUserId,
    attributes: { role: profileUserRole },
  } = user

  const {
    id: location_uuid,
    attributes: { systemName: eventName },
  } = selectedLocation

  const { data, refetch } = useUserMappingsSettingsQuery({
    query: { location_uuid },
    path: {
      userId: selectedUserId,
      mappingType: 'EngagementMapping',
      eventName,
    },
  })

  const setting = data?.data

  const {
    mutateAsync,
    isLoading: isMutating,
  } = useUserMappingsSettingsMutation({
    path: {
      eventName,
      userId: selectedUserId,
      mappingType: 'EngagementMapping',
    },
    query: { location_uuid },
  })

  const handleChange = async (name: Setting, value: boolean) => {
    try {
      await mutateAsync({ data: { ...setting!, [name]: value } })
      refetch()
      showSuccessToast(formatMessage(messages.success))
    } catch (error) {
      showErrorToast(formatMessage(messages.genericError))
    }
  }

  const canEdit =
    (adminRoles.includes(companyRole) ||
      (eventRole && adminRoles.includes(eventRole))) &&
    loggedInUserId !== selectedUserId

  const isCsmOrMm = csmAndMmRoles.includes(profileUserRole)

  const Content = () =>
    setting ? (
      <ContentContainer>
        <ToggleSettings
          name="defaultCanRequest"
          value={setting.defaultCanRequest}
          onChange={handleChange}
          disabled={!canEdit || isCsmOrMm}
        />
        <ToggleSettings
          name="defaultCanParticipate"
          value={setting.defaultCanParticipate}
          onChange={handleChange}
          disabled={!canEdit}
        />
      </ContentContainer>
    ) : null

  return (
    <>
      {isMutating && <ModalSpinner />}
      <StatefulPopover
        content={<Content />}
        placement={PLACEMENT.bottomRight}
        returnFocus
        autoFocus
      >
        <Button kind="tertiary">
          {formatMessage(messages.title)}
          <SettingsHeader>
            <SettingsCount>2</SettingsCount>
            <Settings size={15} />
            <ArrowContainer>
              <DownArrow size={10} />
            </ArrowContainer>
          </SettingsHeader>
        </Button>
      </StatefulPopover>
    </>
  )
}
