import { VFC } from 'react'
import { useMe } from 'providers/MeProvider'
import { Button, KIND } from '@jifflenow/brightui/button'
import { ThreeDots } from '@jifflenow/brightui/icon'
import { StatefulPopover, PLACEMENT } from '@jifflenow/brightui/popover'
import { DrawerCloseButton, useDrawer } from 'ui-components/Drawer'
import { useAppSource, AppSource } from 'providers/AppSourceProvider'
import { useProfileUser } from '../providers/ProfileUserProvider'
import UserStatus from './UserStatus'
import { popoverOverrides } from './overrides'
import {
  HeaderTitle,
  PopoverMenuItem,
  MobileViewAction,
  DesktopViewAction,
  HeaderContainer,
} from './styled-components'

export const Header: VFC = () => {
  const { closeDrawer } = useDrawer()
  const { app } = useAppSource()
  const {
    id: loggedInUserId,
    attributes: {
      privileges: { canMarkUsersActive },
    },
  } = useMe().data
  const {
    id: profileUserId,
    attributes: { firstName = '', lastName = '' },
  } = useProfileUser()

  const myProfile = loggedInUserId === profileUserId

  return (
    <HeaderContainer $hasMenu>
      {app === AppSource.WideScale && (
        <DrawerCloseButton onClick={() => closeDrawer()} />
      )}

      {(firstName || lastName) && (
        <HeaderTitle>{`${firstName} ${lastName}`}</HeaderTitle>
      )}

      {canMarkUsersActive && !myProfile && (
        <>
          <DesktopViewAction>
            <UserStatus />
          </DesktopViewAction>
          <MobileViewAction>
            <StatefulPopover
              overrides={popoverOverrides}
              placement={PLACEMENT.bottomRight}
              content={() => (
                <PopoverMenuItem>
                  <UserStatus />
                </PopoverMenuItem>
              )}
              returnFocus
              autoFocus
            >
              <Button kind={KIND.minimal}>
                <ThreeDots color="mono700" size={24} />
              </Button>
            </StatefulPopover>
          </MobileViewAction>
        </>
      )}
    </HeaderContainer>
  )
}
