import { VFC } from 'react'
import { formatDateRange, formatToStartOfTheDay } from 'utils/dates'
import { useEventInfo } from '../providers/EventInfoProvider'
import { EventDatesLabelWrapper, Date } from './styled-components'
import { EventSelectLabelProps } from './types'

export const EventDatesLabel: VFC<EventSelectLabelProps> = ({ title }) => {
  const { startDate, endDate } = useEventInfo().location || {}

  return (
    <EventDatesLabelWrapper>
      {title}&nbsp;
      <Date>
        {startDate &&
          endDate &&
          formatDateRange(
            formatToStartOfTheDay(startDate),
            formatToStartOfTheDay(endDate)
          )}
      </Date>
    </EventDatesLabelWrapper>
  )
}
