import { ChangeEvent, VFC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { Checkbox } from '@jifflenow/brightui/checkbox'
import { ParticipateAllCheckboxProps, ParticipateAllProps } from './types'
import { EmptyHeaderLabel } from './styled-components'

const messages = defineMessages({
  participateMeetingLabel: {
    defaultMessage: 'Participate in meeting',
    description: 'Column header',
  },
})

const ParticipateAllCheckbox: VFC<ParticipateAllCheckboxProps> = ({
  canEdit,
  participateAll,
  modifiedParticipateMappingCount,
  onChange,
}) => {
  const { formatMessage } = useIntl()

  return (
    <Checkbox
      checked={participateAll}
      isIndeterminate={modifiedParticipateMappingCount > 0}
      onChange={({ target: { checked } }: ChangeEvent<HTMLInputElement>) =>
        onChange(checked)
      }
      disabled={!canEdit}
    >
      {formatMessage(messages.participateMeetingLabel)}
    </Checkbox>
  )
}

const EmptyParticipateAll = () => {
  const { formatMessage } = useIntl()

  return (
    <EmptyHeaderLabel>
      {formatMessage(messages.participateMeetingLabel)}
    </EmptyHeaderLabel>
  )
}

export const ParticipateAll: VFC<ParticipateAllProps> = ({
  isEmpty,
  ...rest
}) => (isEmpty ? <EmptyParticipateAll /> : <ParticipateAllCheckbox {...rest} />)
