import { useState, VFC } from 'react'
import { HttpError, patchUserProfile, usePortalV2Client } from 'rest'
import { defineMessages, useIntl } from 'react-intl'
import { Checkbox } from '@jifflenow/brightui/checkbox'
import { useQueryClient } from 'react-query'
import { showErrorToast, showSuccessToast } from 'utils/toast'
import { ModalSpinner } from '@jifflenow/brightui/modal-spinner'
import { components } from 'types/openapi'
import { ConfirmationDialog } from 'ui-components/ConfirmationDialog'
import { useProfileUser } from '../providers/ProfileUserProvider'
import { CheckboxControl } from './styled-components'

type GeneralError = components['schemas']['GeneralError']

const messages = defineMessages({
  label: {
    defaultMessage: 'Active',
    description: 'Button label',
  },
  success: {
    defaultMessage: 'User status updated successfully.',
    description: 'Success message',
  },
  genericError: {
    defaultMessage: 'Something is not right. Please retry.',
    description: 'Error message',
  },
  modalHeader: {
    defaultMessage: 'Are you sure?',
    description: 'Header text',
  },
  modalMessage: {
    defaultMessage: 'Inactive user cannot access Jifflenow.',
    description: 'Message text',
  },
  cancelButtonLabel: {
    defaultMessage: 'Discard',
    description: 'Button label',
  },
  successButtonLabel: {
    defaultMessage: 'Save',
    description: 'Button label',
  },
})

const UserStatus: VFC = () => {
  const { formatMessage } = useIntl()
  const portalClient = usePortalV2Client()
  const queryClient = useQueryClient()
  const {
    id: userId,
    attributes: { active: isActive },
  } = useProfileUser()
  const [loading, setLoading] = useState(false)
  const [showWarning, setShowWarning] = useState<boolean>(false)

  const handleToggleChange = () => {
    if (isActive) {
      setShowWarning(true)
    } else {
      toggleActive()
    }
  }

  const handleClose = () => {
    setShowWarning(false)
  }

  const toggleActive = async () => {
    setLoading(true)
    try {
      await patchUserProfile(portalClient, { active: !isActive }, userId)
      queryClient.invalidateQueries('fetchUser')
      setLoading(false)
      setShowWarning(false)
      showSuccessToast(formatMessage(messages.success))
    } catch (e) {
      setLoading(false)
      if (e instanceof HttpError && e.response.status === 403) {
        const errorMessage =
          (e as GeneralError).errors[0].title ??
          formatMessage(messages.genericError)
        showErrorToast(errorMessage)
        return
      }
      showErrorToast(formatMessage(messages.genericError))
    }
  }

  return (
    <>
      {loading && <ModalSpinner />}
      {showWarning && (
        <ConfirmationDialog
          heading={formatMessage(messages.modalHeader)}
          message={formatMessage(messages.modalMessage)}
          cancelLabel={formatMessage(messages.cancelButtonLabel)}
          successLabel={formatMessage(messages.successButtonLabel)}
          onClose={handleClose}
          onCancel={handleClose}
          onSuccess={toggleActive}
        />
      )}
      <CheckboxControl
        label={formatMessage(messages.label)}
        data-pendo="profile/is-active-toggle"
      >
        <Checkbox
          checkmarkType="toggle_round"
          id="user_active_status"
          checked={isActive}
          onChange={handleToggleChange}
          disabled={loading}
          ariaLabel={formatMessage(messages.label)}
        />
      </CheckboxControl>
    </>
  )
}

export default UserStatus
