import { createContext, FC, useContext, useState, useMemo } from 'react'
import { Campaign } from './types'

type ContextT = {
  campaign: Campaign | null
  setCampaign: (campaign: Campaign | null) => void
}
const Context = createContext<ContextT | null>(null)

export const CampaignProvider: FC = ({ children }) => {
  const [campaign, setCampaign] = useState<Campaign | null>(null)

  const value = useMemo<ContextT>(() => ({ campaign, setCampaign }), [
    campaign,
    setCampaign,
  ])

  return <Context.Provider value={value}>{children}</Context.Provider>
}

export const useCampaign = () => {
  const context = useContext(Context) as ContextT
  if (context === undefined && process.env.NODE_ENV !== 'production') {
    throw new Error('useCampaign must be used within a CampaignProvider')
  }

  return context
}
