import { VFC, ChangeEvent } from 'react'
import { Checkbox } from '@jifflenow/brightui/checkbox'
import { RowCheckboxProps } from './types'

export const RowCheckbox: VFC<RowCheckboxProps> = ({
  rowId,
  rowName,
  rowChecked,
  handleSelectItem,
  disabled,
  ariaLabel,
}) => {
  const handleChange = ({ target }: ChangeEvent<HTMLInputElement>) => {
    if (target.checked) {
      handleSelectItem({ uuid: rowId, [rowName]: true })
    } else {
      handleSelectItem({ uuid: rowId, [rowName]: false })
    }
  }

  return (
    <Checkbox
      checked={rowChecked}
      onChange={handleChange}
      disabled={disabled}
      ariaLabel={ariaLabel}
    />
  )
}
