import { VFC, FC, useState, useEffect, useMemo } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { Config, FORM_ERROR } from 'final-form'
import { useQuery } from 'react-query'
import { Form as FinalForm, FormSpy } from 'react-final-form'
import createFocusDecorator from 'final-form-focus'
import createDecorator from 'final-form-calculate'
import { ModalSpinner } from '@jifflenow/brightui/modal-spinner'
import { useHttpClient } from 'providers/HttpClientContext'
import { useMe } from 'providers/MeProvider'
import { AppSource, useAppSource } from 'providers/AppSourceProvider'
import { showErrorToast, showSuccessToast } from 'utils/toast'
import { useBeforeUnload } from 'utils/useBeforeUnload'
import { useDrawer } from 'ui-components/Drawer'
import {
  useUsersSettings,
  putUserSettings,
  fetchFreeBusyCISConfig,
  getTagCloudList,
} from 'rest'
import { useEventConfigQuery, useRolesQuery } from 'gql'
import { RolesDisplayNames } from 'gql/types/globalTypes'
import { fetchRoles_fetchRoles } from 'gql/types/fetchRoles'
import { bulkFetchLocationConfigsByName_bulkFetchLocationConfigsByName_configValue_CustomNameValue as CustomNameValue } from 'gql/types/bulkFetchLocationConfigsByName'
import { useDirtyCheckDialog } from '../Common/useDirtyCheckDialog'
import { Footer } from '../Common'
import { Role } from '../types'
import { RoleField } from './RoleField'
import { MappedUserField } from './MappedUserField'
import { ToggleField } from './ToggleField'
import { ModeOfAttendance } from './ModeOfAttendance'
import { TagCloud } from './TagCloud'
import { ActivityManager } from './ActivityManager'
import { SettingsLoader } from './SettingsLoader'
import {
  EventConfigs,
  FormValues,
  UserSettings,
  SettingsFormProps,
  FormProps,
} from './types'
import { Container } from './styled-components'
import { MappedEngagementType } from './MappedEngagementType'
import { MappedTopic } from './MappedTopic'
import { CanManageSettings } from './CanManageSettings'
import { CisSyncSetting } from './CisSyncSetting'

const messages = defineMessages({
  title: {
    defaultMessage: 'Settings for',
    description: 'Page title',
  },
  genericError: {
    defaultMessage: 'Something is not right. Please retry.',
    description: 'Error message',
  },
  success: {
    defaultMessage: 'User settings updated successfully.',
    description: 'Success message',
  },
  fetchFreeBusySuccess: {
    defaultMessage:
      'Synchronization with external calendar has been initiated. This might a take few minutes.',
    description: 'Sync Success Message',
  },
})

type Roles = (keyof typeof RolesDisplayNames)[]

const adminRoles: Set<Role> = new Set(['CSM', 'MM', 'JMM'])

const internalEditableRoles: Roles = ['MM', 'JMM', 'EX', 'EA', 'AM', 'Partner']

const externalEditableRoles: Roles = ['ET']

const jmmEditableRoles: Roles = ['EX', 'EA', 'AM', 'Partner']

const filterRoles = (roles: Roles) => ({
  clientDisplayName,
}: fetchRoles_fetchRoles) => roles.includes(clientDisplayName)

const focusDecorator = createFocusDecorator<FormValues>()

const Form: FC<FormProps> = ({
  handleSubmit,
  dirty,
  submitSucceeded,
  children,
  onSave,
  isLastItem,
  values,
}) => {
  const unload = useBeforeUnload()

  useDirtyCheckDialog({
    isDirty: dirty,
    onSave: () => onSave(values as FormValues),
  })

  useEffect(() => {
    unload(
      dirty &&
        // When this page is the last navigation in the menu, block the prompt on a successful "Save and Done"
        // so that the user can navigate away from the page
        !(isLastItem && submitSucceeded)
    )
  }, [dirty, isLastItem, submitSucceeded, unload])

  return (
    <form onSubmit={handleSubmit} noValidate>
      <Container>{children}</Container>

      <FormSpy
        subscription={{
          submitting: true,
          validating: true,
          dirty: true,
          values: true,
          valid: true,
        }}
      >
        {({ submitting, validating, dirty, values, form, valid }) => (
          <Footer
            hasForm
            isLastItem={isLastItem}
            onDiscard={() => form.reset()}
            onSave={() => valid && onSave(values as FormValues)}
            isDisabled={!dirty || submitting || validating}
            isLoading={submitting}
            pendoPrefix="profile/campaign/settings"
          />
        )}
      </FormSpy>
    </form>
  )
}

const getFormInitialValues = (arg: UserSettings) => {
  const {
    settings,
    mappedTo,
    eventRole,
    cisDetails,
    locationSpecificDetails,
  } = arg

  return {
    eventRole: eventRole.name,
    mappedTo:
      mappedTo?.users.map(({ name, uuid }) => ({
        label: name,
        id: uuid,
      })) ?? [],
    ...settings.reduce(
      (acc, { name, value }) => ({ ...acc, [name]: value }),
      {}
    ),
    cisEmail: cisDetails?.cisEmail,
    modeOfAttendance: locationSpecificDetails?.modeOfAttendance,
    taskManager: locationSpecificDetails?.taskManager,
    ...locationSpecificDetails?.tagClouds?.reduce(
      (
        acc,
        { tagCloudUuid, tags }: { tagCloudUuid: string; tags: string[] }
      ) => ({
        ...acc,
        ...(tagCloudUuid ? { [tagCloudUuid]: { tagCloudUuid, tags } } : {}),
      }),
      {}
    ),
    canManageSelfServeSettings:
      locationSpecificDetails?.canManageSelfServeSettings,
  }
}

export const SettingsForm: VFC<SettingsFormProps> = props => {
  const { formatMessage } = useIntl()
  const client = useHttpClient()()
  const { closeDrawer } = useDrawer()
  const isSourceWeb = useAppSource().app === AppSource.WideScale

  const {
    id: userId,
    attributes: { userType },
  } = props.user
  const {
    attributes: { role: loggedInCompanyRole },
    id: loggedInUserId,
  } = useMe().data
  const { eventName, location, eventType } = props.campaign
  const [userSettings, setUserSettings] = useState<UserSettings | null>(null)

  const [showSpinner, setShowSpinner] = useState<boolean>(false)
  const [eventConfig, setEventConfigs] = useState<EventConfigs>()

  const formInitialValues = useMemo(
    () => (userSettings ? getFormInitialValues(userSettings) : {}),
    [userSettings]
  )

  const roleFiledDecorator = useMemo(
    () =>
      createDecorator({
        field: 'eventRole',
        updates: {
          mappedTo: (_, allValues, prevValues) => {
            const { eventRole, mappedTo } = allValues as FormValues

            if (!Object.keys(prevValues as FormValues).length) {
              return mappedTo
            }

            const formInitialValues =
              userSettings && getFormInitialValues(userSettings)

            if (formInitialValues) {
              if (
                (formInitialValues.eventRole === 'EA' && eventRole === 'EA') ||
                (formInitialValues.eventRole === 'EX' && eventRole === 'EX')
              ) {
                return formInitialValues.mappedTo
              }
            }

            return mappedTo
          },
        },
      }),
    [userSettings]
  )

  const { data: tagCloudList } = useQuery(
    [
      'tagCloudList',
      {
        path: { eventName },
        query: {
          current_location_uuid: location,
        },
      },
    ],
    getTagCloudList
  )

  const { roles = [] } = useRolesQuery({
    onError: () => showErrorToast(formatMessage(messages.genericError)),
  })

  const {
    data: { data: loggedInUserEventSettings } = {},
    isLoading: isLoggedInUserSettingsLoading,
    run: fetchLoggedInUserSettings,
  } = useUsersSettings({
    userId: loggedInUserId,
    onReject: () => showErrorToast(formatMessage(messages.genericError)),
  })

  const { eventRole: { name: loggedInEventRole = '' } = {} } =
    loggedInUserEventSettings ?? {}

  const isEventRoleAdmin =
    loggedInEventRole && adminRoles.has(loggedInEventRole)

  const isRoleFieldDisabled =
    !isEventRoleAdmin ||
    !userSettings?.eventRole.canEdit ||
    (loggedInCompanyRole === 'JMM' &&
      !jmmEditableRoles.includes(userSettings.eventRole.name))

  const editableRoles = roles
    .filter(
      filterRoles(
        userType === 'internal' ? internalEditableRoles : externalEditableRoles
      )
    )
    .filter(
      ({ clientDisplayName: role }) =>
        loggedInCompanyRole !== 'JMM' ||
        isRoleFieldDisabled ||
        jmmEditableRoles.includes(role)
    )

  const {
    isLoading: isSettingsLoading,
    run: fetchSettings,
    reload,
  } = useUsersSettings({
    userId,
    onResolve: ({ data }) => setUserSettings(data),
    onReject: () => showErrorToast(formatMessage(messages.genericError)),
  })

  const {
    loading: isConfigLoading,
    refetch: refetchEventConfig,
  } = useEventConfigQuery({
    variables: {
      names: [
        'in_person_attendance_custom_label',
        'virtual_attendance_custom_label',
        'enable_mode_of_attendance',
      ],
    },
    context: {
      headers: { 'X-Location-Uuid': location },
    },
    notifyOnNetworkStatusChange: true,
    onCompleted: data =>
      setEventConfigs(
        data.bulkFetchLocationConfigsByName.reduce(
          (acc, { configName, configValue }) => ({
            ...acc,
            [configName]: (configValue as CustomNameValue).value,
          }),
          {}
        ) as EventConfigs
      ),
  })

  useEffect(() => {
    refetchEventConfig()
  }, [refetchEventConfig, location])

  useEffect(() => {
    fetchSettings({ locationId: location, subSystem: eventName })
    fetchLoggedInUserSettings({ locationId: location, subSystem: eventName })
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [eventName, location, props.eventType, loggedInCompanyRole])

  const handleSubmit: (values: FormValues) => void = async values => {
    try {
      setShowSpinner(true)

      const asArray = Object.entries(values)

      const filteredValue = asArray
        .filter(
          ar => !tagCloudList?.data.tag_cloud_list.find(rm => rm.uuid === ar[0])
        )
        .reduce(
          (acc, item) => ({ ...acc, [item[0]]: item[1] }),
          {}
        ) as FormValues

      const tagClouds = asArray
        .filter(ar =>
          tagCloudList?.data.tag_cloud_list.find(rm => rm.uuid === ar[0])
        )
        .reduce(
          (acc: { tagCloudUuid: string; tags: string[] }[], item) => [
            ...acc,
            item[1],
          ],
          []
        )

      const {
        cisEmail,
        modeOfAttendance,
        taskManager,
        mappedTo,
        canManageSelfServeSettings,
        ...rest
      } = filteredValue
      const userPayload = {
        ...rest,
        ...(mappedTo
          ? { mappedTo: mappedTo.map(({ id }) => id as string) }
          : null),
        ...(cisEmail && { cisDetails: { cisEmail } }),
        ...(modeOfAttendance || taskManager || tagClouds
          ? {
              locationSpecificDetails: {
                ...(modeOfAttendance ? { modeOfAttendance } : null),
                ...(taskManager ? { taskManager } : null),
                ...(tagClouds ? { tagClouds } : null),
                ...(rest.eventRole === 'MM'
                  ? { canManageSelfServeSettings }
                  : null),
              },
            }
          : null),
      }

      await putUserSettings({
        client,
        userId,
        subSystem: eventName,
        payload: { data: userPayload },
      })
      setShowSpinner(false)
      showSuccessToast(formatMessage(messages.success))
      return undefined
    } catch (error) {
      setShowSpinner(false)
      showErrorToast(formatMessage(messages.genericError))
      return { [FORM_ERROR]: { code: 'GenericError' } }
    }
  }

  const handleFetchFreeBusy = async () => {
    setShowSpinner(true)
    try {
      await fetchFreeBusyCISConfig(client, eventName, userId, location)
      showSuccessToast(formatMessage(messages.fetchFreeBusySuccess))
      setShowSpinner(false)
    } catch (error) {
      setShowSpinner(false)
      showErrorToast(formatMessage(messages.genericError))
    }
  }

  const isLoading =
    isSettingsLoading || isLoggedInUserSettingsLoading || isConfigLoading
  const handleDone = () => isSourceWeb && closeDrawer()

  const handleSaveAndNext: Config<FormValues>['onSubmit'] = async args => {
    const result = await handleSubmit(args)
    if (result === undefined) {
      props.isLastItem && handleDone()
      !props.isLastItem && props.onNavigate()
    }

    return result
  }

  const handleSave: (arg: FormValues) => void = async args => {
    const result = await handleSubmit(args)
    if (result === undefined) {
      reload()
    }

    return result
  }

  const canEditSsFields =
    loggedInCompanyRole === 'CSM' || loggedInCompanyRole === 'MM'

  return (
    <>
      {showSpinner && <ModalSpinner />}
      {isLoading && <SettingsLoader />}
      {!isLoading && userSettings && (
        <FinalForm<FormValues>
          decorators={[
            focusDecorator,
            // TODO: Awaiting fix for this [bug](https://github.com/final-form/final-form-calculate/issues/38)
            // to remove `ts-expect-error`.
            // @ts-expect-error
            roleFiledDecorator,
          ]}
          onSubmit={handleSaveAndNext}
          initialValues={formInitialValues}
          // eslint-disable-next-line complexity
          render={renderProps => {
            const {
              values: { eventRole, cisEnabled },
            } = renderProps

            return (
              <Form
                {...renderProps}
                onSave={handleSave}
                isLastItem={props.isLastItem}
              >
                <RoleField
                  roles={editableRoles}
                  isDisabled={
                    loggedInUserId === userId ||
                    (isRoleFieldDisabled && loggedInCompanyRole !== 'CSM')
                  }
                />
                {userSettings.mappedTo &&
                  (eventRole === 'EA' || eventRole === 'EX') && (
                    <MappedUserField
                      campaign={props.campaign}
                      name="mappedTo"
                      role={eventRole}
                      isDisabled={!userSettings.mappedTo.canEdit}
                    />
                  )}
                {eventType !== 'StaffScheduling' &&
                  eventRole === 'MM' &&
                  (loggedInUserEventSettings ||
                    loggedInCompanyRole === 'CSM') && (
                    <CanManageSettings settings={loggedInUserEventSettings} />
                  )}
                {isEventRoleAdmin &&
                  userSettings.settings.map(({ canEdit, name }) => (
                    <ToggleField key={name} name={name} isDisabled={!canEdit} />
                  ))}
                {cisEnabled && (
                  <CisSyncSetting
                    cisDetails={userSettings.cisDetails}
                    onHandleFetchFreeBusy={handleFetchFreeBusy}
                  />
                )}
                {eventConfig?.enable_mode_of_attendance === 'true' && (
                  <ModeOfAttendance {...eventConfig} />
                )}
                {eventType === 'StaffScheduling' && (
                  <>
                    <ActivityManager
                      name="taskManager"
                      isDisabled={!canEditSsFields}
                    />
                    {userSettings?.locationSpecificDetails?.tagClouds && (
                      <TagCloud
                        eventName={eventName}
                        location={location}
                        isDisabled={!canEditSsFields}
                      />
                    )}
                  </>
                )}
                {!isEventRoleAdmin && (
                  <>
                    <MappedEngagementType
                      eventName={eventName}
                      userId={userId}
                      location={location}
                    />
                    <MappedTopic
                      eventName={eventName}
                      userId={userId}
                      location={location}
                    />
                  </>
                )}
              </Form>
            )
          }}
        />
      )}
    </>
  )
}
