import { VFC, useState } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { FormattedMessage } from 'react-intl'
import { ConfirmationDialog } from 'ui-components/ConfirmationDialog'
import { useDirtyCheckDialog } from 'components/ProfileDrawer/Common/useDirtyCheckDialog'
import { SaveButtonProps } from './types'
import { FooterButton } from './styled-components'

const messages = defineMessages({
  confirmationHeader: {
    defaultMessage: 'Confirmation',
    description: 'Modal heading',
  },
  confirmationMessage: {
    defaultMessage:
      'Changes in availability will only reflect for future meetings and not for already scheduled meetings.',
    description: 'Confirmation message',
  },
  cancelButtonLabel: {
    defaultMessage: 'Cancel',
    description: 'Button label',
  },
  successButtonLabel: {
    defaultMessage: 'OK',
    description: 'Button label',
  },
})

export const SaveButton: VFC<SaveButtonProps> = ({
  isDirty,
  isDisabled,
  pendoPrefix,
  onSave,
  clearTempActiveKey,
}) => {
  const { formatMessage } = useIntl()
  const [showConfirmation, setShowConfirmation] = useState(false)

  const handleSave = () => {
    setShowConfirmation(true)
    clearTempActiveKey()
  }

  const onClose = () => setShowConfirmation(false)

  const onSuccess = () => {
    setShowConfirmation(false)
    return onSave()
  }

  useDirtyCheckDialog({
    isDirty,
    onSave: () => {
      setShowConfirmation(true)
      return true
    },
  })

  return (
    <>
      {showConfirmation && (
        <ConfirmationDialog
          onClose={onClose}
          onCancel={onClose}
          onSuccess={onSuccess}
          heading={formatMessage(messages.confirmationHeader)}
          message={formatMessage(messages.confirmationMessage)}
          cancelLabel={formatMessage(messages.cancelButtonLabel)}
          successLabel={formatMessage(messages.successButtonLabel)}
        />
      )}

      <FooterButton
        type="submit"
        onClick={handleSave}
        disabled={isDisabled}
        data-pendo={`${pendoPrefix}/save`}
      >
        <FormattedMessage defaultMessage="Save" description="Button label" />
      </FooterButton>
    </>
  )
}
