import { VFC, useState, useEffect, ReactNode } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { DateTime } from 'luxon'
import { IconButton, Button, KIND } from '@jifflenow/brightui/button'
import { Trash, Pencil } from '@jifflenow/brightui/icon'
import { useEventInfo } from 'components/ProfileDrawer/providers/EventInfoProvider'
import { EventDatesLabel } from 'components/ProfileDrawer/Common/EventDatesLabel'
import { CampaignFilter, useCampaign } from '../../../Common'
import { getGroupedExceptionDates } from '../groupExceptionDates'
import { AddEditException as EditException } from '../AddEditException'
import { AddExceptionButton } from '../AddExceptionButton'
import { ListExceptionsProps, Exception } from '../types'
import {
  NoException,
  SelectEventLabel,
  SelectEventRoot,
  SelectEventWrapper,
  Text,
} from '../styled-components'
import { clearButtonOverrides } from '../overrides'
import { ListItem } from './ListItem'
import { iconButtonOverrides } from './overrides'
import {
  Root,
  ListHeader,
  ExceptionList,
  ExceptionListItem,
  ExceptionActions,
  DateHeader,
  HourHeader,
  ActionHeader,
  AddExceptionButtonRoot,
  ListHeadingDesc,
  ListHeaderRoot,
  FilterWrapper,
  EventDatesRoot,
} from './styled-components'

const messages = defineMessages({
  filterBy: {
    defaultMessage: 'Filter by',
    description: 'Label text',
  },
  noException: {
    defaultMessage: 'No exceptions for the selected campaign',
    description: 'Button text',
  },
  clear: {
    defaultMessage: 'Clear',
    description: 'Button label',
  },
  dateHeader: {
    defaultMessage: 'Date(s)',
    description: 'Column Header',
  },
  hourHeader: {
    defaultMessage: 'Available Hours',
    description: 'Column Header',
  },
  actionHeader: {
    defaultMessage: 'Actions',
    description: 'Column Header',
  },
  exceptionsFor: {
    defaultMessage: 'Exceptions for',
    description: 'Label text',
  },
  exceptionsNote: {
    defaultMessage:
      "<b>Note:</b> Exceptions are your 'non-availability' time slots against the 'Availability' mentioned. <b>These exceptions are applicable to all the Jifflenow events falling on below dates.</b>",
    description: 'Label text',
  },
})

export const ListExceptions: VFC<ListExceptionsProps> = ({
  timeZone,
  exceptions,
  setExceptions,
}) => {
  const { formatMessage } = useIntl()
  const { campaign, setCampaign } = useCampaign()
  const [isOpen, setIsOpen] = useState(false)
  const [exceptionEditIndex, setExceptionEditIndex] = useState(-1)
  const [filteredExceptions, setFilteredExceptions] = useState<Exception[]>(
    exceptions
  )
  const { location } = useEventInfo()

  const { timeZoneId } = timeZone
  const groupedExceptions = getGroupedExceptionDates(exceptions)

  const groupedFilteredExceptions = getGroupedExceptionDates(filteredExceptions)

  useEffect(() => {
    setFilteredExceptions(
      exceptions.filter(({ date }) => {
        if (!campaign) {
          return true
        }

        const startDateInChosenTimezone = DateTime.fromISO(campaign.startDate, {
          zone: campaign.timeZone,
        })
          .setZone(timeZoneId)
          .toISODate()

        const endDateInChosenTimezone = DateTime.fromISO(campaign.endDate, {
          zone: campaign.timeZone,
        })
          .endOf('day')
          .setZone(timeZoneId)
          .toISODate()

        return (
          startDateInChosenTimezone <= date && endDateInChosenTimezone >= date
        )
      })
    )
  }, [campaign, exceptions, timeZoneId])

  const handleEdit = (indexForEdit: number) => {
    setExceptionEditIndex(indexForEdit)
    setIsOpen(true)
  }

  const handleDelete = (indexToDelete: number) => {
    const datesToDelete = groupedExceptions.find(
      (_, index) => indexToDelete === index
    )?.date!
    setExceptions(
      exceptions.filter(({ date }) => !datesToDelete.includes(date))
    )
  }

  const onClose = () => {
    setExceptionEditIndex(-1)
    setIsOpen(false)
  }

  return (
    <Root>
      <ListHeader>
        <SelectEventRoot>
          <ListHeaderRoot>
            {!location ? (
              <FilterWrapper>
                <SelectEventLabel>
                  {formatMessage(messages.filterBy)}
                </SelectEventLabel>
                <SelectEventWrapper>
                  <CampaignFilter setDefaultValue={false} />
                  <Button
                    type="button"
                    onClick={() => setCampaign(null)}
                    overrides={clearButtonOverrides}
                    kind={KIND.minimal}
                  >
                    {formatMessage(messages.clear)}
                  </Button>
                </SelectEventWrapper>
              </FilterWrapper>
            ) : (
              <EventDatesRoot>
                <EventDatesLabel
                  title={formatMessage(messages.exceptionsFor)}
                />
              </EventDatesRoot>
            )}
            <ListHeadingDesc>
              {formatMessage(messages.exceptionsNote, {
                b: (chunks: ReactNode) => <strong>{chunks}</strong>,
              })}
            </ListHeadingDesc>
          </ListHeaderRoot>
        </SelectEventRoot>
        <AddExceptionButtonRoot>
          <AddExceptionButton
            exceptions={exceptions}
            setExceptions={setExceptions}
            timeZone={timeZone}
          />
        </AddExceptionButtonRoot>
      </ListHeader>
      {isOpen && (
        <EditException
          isOpen={isOpen}
          onClose={onClose}
          exceptions={exceptions}
          timeZone={timeZone}
          setExceptions={setExceptions}
          {...(exceptionEditIndex !== -1
            ? {
                listedExceptions: groupedExceptions,
                listedExceptionEditIndex: exceptionEditIndex,
                timeSlots: groupedExceptions[exceptionEditIndex].available,
                calendarDates: groupedExceptions[exceptionEditIndex].date,
              }
            : null)}
        />
      )}
      {groupedFilteredExceptions.length ? (
        <ExceptionList>
          <ExceptionListItem>
            <DateHeader>{formatMessage(messages.dateHeader)}</DateHeader>
            <HourHeader>{formatMessage(messages.hourHeader)}</HourHeader>
            <ActionHeader>{formatMessage(messages.actionHeader)}</ActionHeader>
          </ExceptionListItem>
          {groupedFilteredExceptions.map((exception, exceptionIndex) => (
            <ExceptionListItem key={exceptionIndex}>
              <ListItem exception={exception} />
              <ExceptionActions>
                <IconButton
                  size={18}
                  overrides={iconButtonOverrides}
                  onClick={() => handleEdit(exceptionIndex)}
                  data-pendo="profile/availability/edit-exception"
                >
                  <Pencil />
                </IconButton>
                <IconButton
                  size={18}
                  overrides={iconButtonOverrides}
                  onClick={() => handleDelete(exceptionIndex)}
                  data-pendo="profile/availability/delete-exception"
                >
                  <Trash />
                </IconButton>
              </ExceptionActions>
            </ExceptionListItem>
          ))}
        </ExceptionList>
      ) : (
        <NoException>
          <AddExceptionButton
            exceptions={exceptions}
            setExceptions={setExceptions}
            timeZone={timeZone}
          />
          <Text>{formatMessage(messages.noException)}</Text>
        </NoException>
      )}
    </Root>
  )
}
