import { VFC } from 'react'
import { Modal } from '@jifflenow/brightui/modal'
import { ModalSpinner } from '@jifflenow/brightui/modal-spinner'
import { ErrorSection } from 'ui-components/ErrorPage'
import { usePrivacyMessages } from 'rest'
import { EditAvatarProps } from './types'
import { ModalBody } from './styled-components'
import { UploadAvatar } from './UploadAvatar'

export const EditAvatar: VFC<EditAvatarProps> = props => {
  const { onClose } = props
  const {
    isLoading,
    error,
    refetch,
    data: { data } = { data: undefined },
  } = usePrivacyMessages({ config: 'user_management_privacy_message' })

  if (isLoading) {
    return <ModalSpinner />
  }

  if (error) {
    return (
      <Modal onClose={onClose} isOpen>
        <ModalBody>
          <ErrorSection onButtonClick={() => refetch()} />
        </ModalBody>
      </Modal>
    )
  }

  if (data) {
    return <UploadAvatar data={data} {...props} />
  }

  return null
}
