import { VFC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import {
  TableBuilder,
  TableBuilderColumn,
} from '@jifflenow/brightui/table-semantic'
import { useMe } from 'providers/MeProvider'
import { Role } from '../../types'
import { useProfileUser } from '../../providers/ProfileUserProvider'
import {
  ColumnCheckbox,
  RowCheckbox,
  EmptyMessage,
  useCampaign,
} from '../../Common'
import { RowLabel } from './styled-components'
import { MappingsListProps } from './types'
import { MappingTableOverrides } from './overrides'

const messages = defineMessages({
  IsAnExpert: {
    defaultMessage: 'Expert',
    description: 'Column header',
  },
  EmptyMessage: {
    defaultMessage: 'No Topics added',
    description: 'Empty Message',
  },
  EmptyMessageMapped: {
    defaultMessage: 'User is currently not mapped to any Topic.',
    description: 'Empty Message',
  },
  EmptyMessageUnMapped: {
    defaultMessage: 'User is currently mapped to all Topics.',
    description: 'Empty Message',
  },
  AddNewButtonMessage: {
    defaultMessage: 'Add Topic',
    description: 'Button Text',
  },
})

const aria = defineMessages({
  mapToExpert: {
    defaultMessage: 'Map to expert',
    description: 'Aria label',
  },
})

const adminRoles: Role[] = ['CSM', 'MM', 'JMM']

export const MappingsList: VFC<MappingsListProps> = ({
  items,
  isSelectAll,
  isItemSelected,
  toggleSelectAll,
  toggleSelectItem,
  hasSomeItemSelected,
  mappingsCount,
  listType,
}) => {
  const { formatMessage } = useIntl()
  const { eventName, location } = useCampaign().campaign!
  const { id: selectedUserId } = useProfileUser()
  const {
    id: loggedInUserId,
    attributes: { role: companyRole, eventRole },
  } = useMe().data

  const allSelectedIsAnExpert = isSelectAll

  const isListEmpty = !items.length

  const handleAddNewTopic = () =>
    (window.location.href = `/${eventName}/topics/topic_listing?current_location_uuid=${location}`)

  const noMappingsInSystem = !mappingsCount.mapped && !mappingsCount.unMapped

  const isAdmin =
    adminRoles.includes(companyRole) ||
    (!!eventRole && adminRoles.includes(eventRole))
  const canEdit = isAdmin && loggedInUserId !== selectedUserId

  return (
    <TableBuilder
      data={items}
      overrides={MappingTableOverrides}
      emptyMessage={
        <EmptyMessage
          onAddNew={handleAddNewTopic}
          emptyMessage={
            noMappingsInSystem
              ? formatMessage(messages.EmptyMessage)
              : listType === 'mapped'
              ? formatMessage(messages.EmptyMessageMapped)
              : formatMessage(messages.EmptyMessageUnMapped)
          }
          addNewButtonMessage={formatMessage(messages.AddNewButtonMessage)}
          showAddButton={noMappingsInSystem && isAdmin}
        />
      }
    >
      <TableBuilderColumn>
        {row => <RowLabel>{row.displayName}</RowLabel>}
      </TableBuilderColumn>
      <TableBuilderColumn
        header={
          <ColumnCheckbox
            columnName="mapTo"
            label={formatMessage(messages.IsAnExpert)}
            checked={allSelectedIsAnExpert}
            intermediate={hasSomeItemSelected}
            handleSelectAll={toggleSelectAll}
            handleDeselectAll={toggleSelectAll}
            showCheckbox={isListEmpty}
            disabled={!canEdit}
          />
        }
      >
        {row => (
          <RowCheckbox
            rowId={row.id}
            rowName="mapTo"
            rowChecked={
              isItemSelected(row.id)?.mapTo ??
              allSelectedIsAnExpert ??
              row.mapTo
            }
            handleSelectItem={toggleSelectItem}
            disabled={!canEdit}
            ariaLabel={formatMessage(aria.mapToExpert)}
          />
        )}
      </TableBuilderColumn>
    </TableBuilder>
  )
}
