import { FC } from 'react'
import { useEventTypes } from 'rest'
import { defineMessages, useIntl } from 'react-intl'
import { TextFilter, Option } from '@jifflenow/brightui/filter'
import { filterOverrides, typeOptions } from 'components/Events'
import { EventType } from './types'

const messages = defineMessages({
  eventType: {
    defaultMessage: 'Instance Type',
    description: 'Filter Label for status of the event type',
  },
})

const toFilterTypeFormat = (option: string) => ({
  id: option,
  label: typeOptions[option],
})

export const EventTypeFilter: FC<{
  types: EventType[]
  onChange: (val: EventType[]) => void
}> = ({ types = [], onChange }) => {
  const { formatMessage } = useIntl()
  const { isLoading, data } = useEventTypes()

  return (
    <TextFilter
      label={formatMessage(messages.eventType)}
      onChange={newTypes => onChange(newTypes.map(({ id }) => id as EventType))}
      options={data?.data.map(toFilterTypeFormat) || []}
      overrides={filterOverrides}
      value={types.map(toFilterTypeFormat)}
      isLoading={isLoading}
      filterOptions={(options: Option) =>
        options.filter((i: { id: string }) => i.id !== 'Emeet')
      }
    />
  )
}
