import { VFC } from 'react'
import { useMutation } from 'react-query'
import { topicsPreferencesMutationFn } from 'rest'
import { KIND, Pagination } from '@jifflenow/brightui/pagination'
import { usePageSizes } from 'utils/usePageSizes'
import { useCampaign } from '../Common'
import { PaginationContainerProps } from './types'
import { PaginationWrapper } from './styled-components'

export const PAGE_SIZE = 10

export const PaginationContainer: VFC<PaginationContainerProps> = props => {
  const { eventName, location } = useCampaign().campaign!

  const pageSizes = usePageSizes(props.totalItems)

  const { mutate: patch } = useMutation(
    topicsPreferencesMutationFn({
      path: { eventName },
      query: { location_uuid: location },
    }),
    {}
  )

  return (
    <PaginationWrapper>
      <Pagination
        kind={KIND.light}
        totalItems={props.totalItems}
        pageSizes={pageSizes}
        currentPageSize={props.pageSize}
        currentPageNumber={props.pageNumber}
        onPageNumberChange={props.onPageNumberChange}
        onPageSizeChange={pageSize => {
          props.onPageSizeChange(pageSize)
          props.onPageNumberChange(1)

          patch({ data: { [props.eventType]: { pageSize } } })
        }}
      />
    </PaginationWrapper>
  )
}
