import { FC, useState, useEffect } from 'react'
import { FormattedMessage } from 'react-intl'
import { useField } from 'react-final-form'
import { Select, OnChangeParams, Option } from '@jifflenow/brightui/select'
import { showErrorToast } from 'utils/toast'
import { Field, Validation } from 'ui-components/Field'
import { FormFieldProps } from 'components/CustomFormFields'
import { useValidateDomain } from 'rest'
import { useProfileUser } from '../../providers/ProfileUserProvider'
import { FormControl } from './styled-components'

export const SecondaryEmail: FC<FormFieldProps> = props => {
  const { name, label, disabled, validations } = props
  const [newEmail, setNewEmail] = useState<string>('')
  const {
    attributes: { userType },
  } = useProfileUser()

  const {
    meta: { error },
    input: { value, onChange },
  } = useField<string[]>('secondaryEmails')

  const { run: validate } = useValidateDomain({
    onResolve: ({ errors }) => {
      if (errors?.email) {
        showErrorToast(
          <span
            className="anchor-primary"
            dangerouslySetInnerHTML={{ __html: errors.email }}
          />
        )

        onChange(value.filter(email => email !== newEmail))
      }
    },
  })

  useEffect(() => {
    newEmail &&
      !error &&
      userType === 'internal' &&
      validate({ email: newEmail, isAliasEmail: true })
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [newEmail, error, userType])

  return (
    <Field<string[]>
      name={name}
      multiple
      parse={val => val.map(({ id }: Option) => id)}
      format={val => (val ? val.map(val => ({ id: val, label: val })) : [])}
      validations={[
        Validation.EMAIL,
        ...(validations?.required ? [Validation.REQUIRED] : []),
      ]}
      render={({ input, meta }) => (
        <FormControl label={label} error={meta.error}>
          <Select
            {...input}
            multi
            id={name}
            searchable
            creatable
            clearable
            disabled={disabled}
            backspaceClearsInputValue
            onCloseResetsInput={false}
            onBlurResetsInput={false}
            onChange={({ value }: OnChangeParams) => {
              const newValue = value.find(val => val.isCreatable)?.id as string

              if (newValue && /^\S+@\S+$/u.test(newValue)) {
                setNewEmail(newValue)
              }
              input.onChange(value)
            }}
            noResultsMsg={
              <FormattedMessage
                defaultMessage="Start typing to add an email address"
                description="Help text"
              />
            }
          />
        </FormControl>
      )}
    />
  )
}
