import { useEffect, useState, VFC } from 'react'
import { useQuery } from 'react-query'
import { getEngagementPreferencesQueryFn, useUserMappingsQuery } from 'rest'
import { ErrorPage } from 'ui-components/ErrorPage'
import { ListLoader } from '../Common'
import { useEventName } from '../providers/EventNameProvider'
import { MappingTable } from './MappingTable'
import { PaginationContainer, PAGE_SIZE } from './PaginationContainer'
import { MappingsPageRoot } from './styled-components'
import {
  EngagementRelationship,
  EngagementTypeMappings,
  MappingsPageProps,
  MappingValue,
  UserMappingResponse,
} from './types'

const transformOption = ({
  id,
  attributes,
  relationships,
}: UserMappingResponse['data'][0]): MappingValue => {
  const { canParticipate, canRequest } = attributes as EngagementTypeMappings
  const {
    displayName: name,
  } = (relationships as EngagementRelationship).engagement.data.attributes

  return {
    name,
    id,
    canRequest: canRequest!,
    canParticipate: canParticipate!,
  }
}

const mapItem = (item: MappingValue): [string, MappingValue] => [item.id, item]

export const MappingsPage: VFC<MappingsPageProps> = props => {
  const {
    search: search_term,
    selectedLocation,
    activeTab,
    dispatch,
    pageNumber,
    setPageNumber,
    user: { id: userId },
  } = props
  const {
    id: location_uuid,
    attributes: { systemName: eventName },
  } = selectedLocation

  const [pageSize, setPageSize] = useState(0)

  useEffect(() => setPageNumber(1), [search_term, location_uuid, setPageNumber])

  const eventType =
    useEventName().eventType === 'Emeet' ? 'emeetInstances' : 'eventInstances'

  useQuery(
    [
      'engagementMappingPreferences',
      { path: { eventName }, query: { location_uuid } },
    ],
    getEngagementPreferencesQueryFn,
    {
      onSuccess: ({ data }) => {
        setPageSize(data[eventType]?.pageSize ?? PAGE_SIZE)
      },
      onError: () => {
        setPageSize(PAGE_SIZE)
      },
    }
  )

  const { data, isLoading, error, refetch } = useUserMappingsQuery(
    {
      path: { eventName, userId, mappingType: 'EngagementMapping' },
      query: {
        page: { number: pageNumber, size: pageSize },
        search_term,
        location_uuid,
        mapped: activeTab === 'mapped',
      },
    },
    {
      enabled: pageSize > 0,
      onSuccess: ({ data, meta }) => {
        const { mapped, unmapped } = meta!

        dispatch({
          type: 'updateMapping',
          payload: {
            activeTab,
            mappedCount: mapped.number,
            unMappedCount: unmapped.number,
            initialItems: new Map(data.map(transformOption).map(mapItem)),
          },
        })
      },
    }
  )

  if (isLoading || pageSize === 0) {
    return <ListLoader />
  }

  if (error || !data) {
    return <ErrorPage onButtonClick={() => refetch()} />
  }

  const items = data.data.map(transformOption)
  const size = data.meta!.page.size!

  return (
    <MappingsPageRoot>
      <MappingTable {...props} items={items} />
      {!!items.length && size > PAGE_SIZE && (
        <PaginationContainer
          eventType={eventType}
          location={selectedLocation}
          onPageNumberChange={setPageNumber}
          onPageSizeChange={setPageSize}
          pageNumber={pageNumber}
          pageSize={pageSize}
          totalItems={size}
        />
      )}
    </MappingsPageRoot>
  )
}
