import { VFC } from 'react'
import { EventStatus } from 'components/Events'
import { AutoTooltip } from 'ui-components/AutoTooltip'
import { formatDateRange, formatToStartOfTheDay } from 'utils/dates'
import { EventStatusFilter as EventStatusType } from 'components/Events/EventStatus/types'
import { useEventInfo } from '../providers/EventInfoProvider'
import {
  EventSelectLabelWrapper,
  StatusWrapper,
  EventName,
  EventDates,
  Option,
} from './styled-components'
import { EventSelectLabelProps } from './types'

export const EventSelectLabel: VFC<EventSelectLabelProps> = ({ title }) => {
  const { filterStatus, mode, displayName, startDate, endDate } =
    useEventInfo().location || {}

  return (
    <EventSelectLabelWrapper>
      <Option>{title}</Option>
      {filterStatus && (
        <StatusWrapper>
          <EventStatus mode={mode} status={filterStatus as EventStatusType} />
        </StatusWrapper>
      )}
      {displayName && (
        <AutoTooltip content={displayName} showArrow>
          <EventName>{displayName}</EventName>
        </AutoTooltip>
      )}
      {startDate && endDate && (
        <EventDates>
          (
          {formatDateRange(
            formatToStartOfTheDay(startDate),
            formatToStartOfTheDay(endDate)
          )}
          )
        </EventDates>
      )}
    </EventSelectLabelWrapper>
  )
}
