import { useState, VFC } from 'react'
import {
  addUserToLocation,
  removeUserFromLocation,
  usePortalV2Client,
} from 'rest'
import { useMe } from 'providers/MeProvider'
import { showErrorToast, showSuccessToast } from 'utils/toast'
import { defineMessages, useIntl } from 'react-intl'
import { Button, KIND } from '@jifflenow/brightui/button'
import { fullWidthButtonOverrides } from 'components/Events'
import {
  useProfileUser,
  useRefetchUser,
} from '../../providers/ProfileUserProvider'
import { UserActionProps } from './types'

const messages = defineMessages({
  add: {
    defaultMessage: 'Add yourself',
    description: 'User map status for campaign',
  },
  remove: {
    defaultMessage: 'Remove yourself',
    description: 'User map status for campaign',
  },
  addOnBehalf: {
    defaultMessage: 'Add',
    description: 'User map status for campaign',
  },
  removeOnBehalf: {
    defaultMessage: 'Remove',
    description: 'User map status for campaign',
  },
  genericError: {
    defaultMessage: 'Something is not right. Please retry.',
    description: 'Error message',
  },
  successAdded: {
    defaultMessage: 'User added to instance.',
    description: 'Success message',
  },
  successRemoved: {
    defaultMessage: 'User removed from instance.',
    description: 'Success message',
  },
})

export const UserActions: VFC<UserActionProps> = props => {
  const { formatMessage } = useIntl()
  const [isUserMapped, setIsUserMapped] = useState<boolean>(props.isUserMapped)
  const [showLoader, setShowLoader] = useState<boolean>(false)
  const userId = useProfileUser().id
  const client = usePortalV2Client()
  const refetch = useRefetchUser()
  const { locationId } = props

  const handleClick = async () => {
    setShowLoader(true)
    try {
      await (!isUserMapped
        ? removeUserFromLocation(client, userId, locationId)
        : addUserToLocation(client, userId, [locationId]))
      setShowLoader(false)
      showSuccessToast(
        isUserMapped
          ? formatMessage(messages.successAdded)
          : formatMessage(messages.successRemoved)
      )
      await refetch()
      setIsUserMapped(!isUserMapped)
    } catch (error) {
      setShowLoader(false)
      showErrorToast(formatMessage(messages.genericError))
    }
  }
  const selectedUserId = useProfileUser().id
  const loggedInUserId = useMe().data.id
  const isViewingMyOwnOrAssumedProfile = loggedInUserId === selectedUserId

  return (
    <Button
      overrides={fullWidthButtonOverrides}
      kind={KIND.secondary}
      onClick={handleClick}
      isLoading={showLoader}
    >
      {isUserMapped
        ? isViewingMyOwnOrAssumedProfile
          ? formatMessage(messages.add)
          : formatMessage(messages.addOnBehalf)
        : isViewingMyOwnOrAssumedProfile
        ? formatMessage(messages.remove)
        : formatMessage(messages.removeOnBehalf)}
    </Button>
  )
}
