import { VFC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { useHistory } from 'react-router-dom'
import { Button } from '@jifflenow/brightui/button'
import { useMe } from 'providers/MeProvider'
import { EmptyMessageRoot } from './styled-components'
import { addButtonOverrides } from './overrides'
import { EmptyMessageProps } from './types'

const messages = defineMessages({
  emptyMessage: {
    defaultMessage: 'No engagement types added',
    description: 'Empty Message',
  },
  emptyMessageWithoutAddMapped: {
    defaultMessage: 'User is currently not mapped to any Engagement Type.',
    description: 'Empty Message',
  },
  emptyMessageWithoutAddUnmapped: {
    defaultMessage: 'User is currently mapped to all Engagement Types.',
    description: 'Empty Message',
  },
  addEngagementTypeLabel: {
    defaultMessage: 'Add Engagement type',
    description: 'Button Text',
  },
})

export const EmptyMessage: VFC<EmptyMessageProps> = ({
  selectedLocation,
  isNoEngagementType,
  activeTab,
}) => {
  const { formatMessage } = useIntl()
  const history = useHistory()
  const loggedInRole = useMe().data.attributes.role

  const {
    id: location,
    attributes: { systemName: eventName },
  } = selectedLocation

  const handleOnClick = () =>
    history.push(`/events/${eventName}/engagement-types?location=${location}`)

  return (
    <EmptyMessageRoot>
      {formatMessage(
        messages[
          isNoEngagementType
            ? 'emptyMessage'
            : activeTab === 'mapped'
            ? 'emptyMessageWithoutAddMapped'
            : 'emptyMessageWithoutAddUnmapped'
        ]
      )}

      {isNoEngagementType && loggedInRole === 'CSM' && (
        <Button overrides={addButtonOverrides} onClick={handleOnClick}>
          {formatMessage(messages.addEngagementTypeLabel)}
        </Button>
      )}
    </EmptyMessageRoot>
  )
}
