import { useState, VFC } from 'react'
import { defineMessages, useIntl, MessageDescriptor } from 'react-intl'
import {
  Tabs,
  Tab,
  TabTitle,
  getTabOverrides,
} from '@jifflenow/brightui/tabs-motion'
import { getEventLocation_getEventLocation } from 'gql/types/getEventLocation'
import { EventConfigs } from 'providers/LocationProvider'
import { bulkFetchLocationConfigsByName_bulkFetchLocationConfigsByName_configValue_CustomNameValue as CustomNameValue } from 'gql/types/bulkFetchLocationConfigsByName'
import { useEventConfigQuery, useEventLocationQuery } from 'gql'
import { showErrorToast } from 'utils/toast'
import { EventSubTypes } from 'gql/types/globalTypes'
import { ModalSpinner } from '@jifflenow/brightui/modal-spinner'
import { useMe } from 'providers/MeProvider'
import { Role } from '../types'
import { Mapped } from './Mapped'
import { UnMapped } from './UnMapped'
import { tabOverrides, tabsOverrides } from './overrides'
import {
  TopicDisableMessageRoot,
  DisableMessageText,
} from './styled-components'
import { MappedUnmappedCount, TopicsListProps } from './types'

const messages = defineMessages({
  Mapped: {
    defaultMessage: 'Mapped',
    description: 'Tab title',
  },
  Unmapped: {
    defaultMessage: 'Unmapped',
    description: 'Tab title',
  },

  genericError: {
    defaultMessage: 'Error Saving.',
    description: 'Error message',
  },
  disableMessageError: {
    defaultMessage:
      'Topics are not enabled for this {eventSubType}. For details contact {supportEmail}',
    description: 'Disable message',
  },
})

const adminRoles: Set<Role> = new Set(['CSM', 'MM', 'JMM'])

export const TopicsList: VFC<TopicsListProps> = ({
  search,
  userId,
  refresh,
  setDefaultSelections,
  currentLocationUuid,
}) => {
  const { formatMessage } = useIntl()
  const [activeTab, setActiveTab] = useState<string>('0')
  const [itemsCount, setItemsCount] = useState<MappedUnmappedCount>({
    mapped: 0,
    unMapped: 0,
  })
  const [
    location,
    setLocation,
  ] = useState<getEventLocation_getEventLocation | null>(null)

  const [configs, setConfigs] = useState<EventConfigs | undefined>()
  const { role: companyRole, eventRole } = useMe().data.attributes

  const { loading: eventConfigLoader } = useEventConfigQuery({
    variables: { names: ['enable_topic'] },
    context: {
      headers: { 'X-Location-Uuid': currentLocationUuid },
    },
    notifyOnNetworkStatusChange: true,
    fetchPolicy: 'cache-and-network',
    onCompleted: data =>
      setConfigs(
        data.bulkFetchLocationConfigsByName.reduce(
          (acc, { configName, configValue }) => ({
            ...acc,
            [configName]: (configValue as CustomNameValue).value === 'true',
          }),
          {}
        ) as EventConfigs
      ),
  })

  const { loading: locationLoader } = useEventLocationQuery({
    variables: {
      uuid: currentLocationUuid,
    },
    notifyOnNetworkStatusChange: true,
    fetchPolicy: 'cache-and-network',
    onError: () => {
      showErrorToast(formatMessage(messages.genericError))
    },
    onCompleted: ({ getEventLocation }) => setLocation(getEventLocation),
  })

  const TabsTitle = (message: MessageDescriptor, count?: number) => (
    <TabTitle>
      {formatMessage(message)} &middot; {count}
    </TabTitle>
  )

  return (
    <>
      {(eventConfigLoader || locationLoader) && <ModalSpinner />}
      {configs?.enable_topic ? (
        <Tabs
          overrides={tabsOverrides}
          onChange={({ activeKey }) => {
            setActiveTab(activeKey as string)
          }}
          activeKey={activeTab}
        >
          <Tab
            title={TabsTitle(messages.Mapped, itemsCount.mapped)}
            overrides={getTabOverrides(tabOverrides)}
          >
            <Mapped
              id={userId}
              refresh={refresh}
              setTotalCount={setItemsCount}
              setDefaultSelections={setDefaultSelections}
              searchTerm={search}
              mappingsCount={itemsCount}
            />
          </Tab>
          {(adminRoles.has(companyRole) ||
            (eventRole && adminRoles.has(eventRole))) && (
            <Tab
              title={TabsTitle(messages.Unmapped, itemsCount.unMapped)}
              overrides={getTabOverrides(tabOverrides)}
            >
              <UnMapped
                id={userId}
                refresh={refresh}
                setTotalCount={setItemsCount}
                setDefaultSelections={setDefaultSelections}
                searchTerm={search}
                mappingsCount={itemsCount}
              />
            </Tab>
          )}
        </Tabs>
      ) : (
        !locationLoader && (
          <TopicDisableMessageRoot>
            <DisableMessageText>
              {formatMessage(messages.disableMessageError, {
                eventSubType:
                  location?.subType === EventSubTypes.Emeet
                    ? 'instance'
                    : 'event',
                supportEmail: location?.supportEmail,
              })}
            </DisableMessageText>
          </TopicDisableMessageRoot>
        )
      )}
    </>
  )
}
