import { useState } from 'react'
import { DateTime } from 'luxon'
import { useEventCityTimeZone } from 'rest'
import { TimeZone } from './types'

export const useTimeZoneFromCity = () => {
  const [cityTimeZone, setCityTimeZone] = useState<TimeZone | null>(null)

  const { isPending, run: fetchCityTimeZone } = useEventCityTimeZone({
    onResolve: ({ data: { timeZoneId } }) => {
      const { offsetNameShort, offsetNameLong } = DateTime.local().setZone(
        timeZoneId
      )
      setCityTimeZone({
        timeZoneId,
        timeZoneName: `${offsetNameShort} | ${offsetNameLong}`,
      })
    },
  })

  const onCityChange = (placeId: string) => {
    placeId ? fetchCityTimeZone({ placeId }) : setCityTimeZone(null)
  }

  return {
    isPending,
    cityTimeZone,
    onCityChange,
  }
}
