import { VFC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { TextFilter, Option } from '@jifflenow/brightui/filter'
import { Value } from '@jifflenow/brightui/select'
import { filterOverrides } from 'components/Events'
import {
  EventType,
  EventTypeDisplayNames,
  EventTypesFilterV2Props,
} from './types'

const messages = defineMessages({
  eventType: {
    defaultMessage: 'Instance Type',
    description: 'Filter Label for status of the event type',
  },
})

const optionsMapping: Record<EventType, EventTypeDisplayNames> = {
  TradeShow: 'eEvent',
  Ebc: 'Ebc',
  OngoingSales: 'eCampaign',
  StaffScheduling: 'Staff Scheduling',
  Emeet: 'Emeet',
}

const formatOption = (name: EventType): Option => ({
  id: name,
  label: optionsMapping[name],
})

export const EventTypesFilterV2: VFC<EventTypesFilterV2Props> = ({
  eventTypes,
  selectedEventTypes,
  onChange,
}) => {
  const { formatMessage } = useIntl()

  const options = eventTypes.map(formatOption)
  const value = selectedEventTypes.map(formatOption)

  const handleChange = (values: Value) =>
    onChange(values.map(({ id }) => id as EventType))

  return (
    <TextFilter
      label={formatMessage(messages.eventType)}
      onChange={handleChange}
      options={options}
      overrides={filterOverrides}
      value={value}
    />
  )
}
