import { FC } from 'react'
import { useHistory } from 'react-router-dom'
import { Drawer } from 'ui-components/Drawer'
import { useMediaQuery } from 'utils/useMediaQuery'
import { useAppSource, AppSource } from 'providers/AppSourceProvider'
import { drawerOverrides } from './overrides'

export const ProfileDrawerWrapper: FC = ({ children }) => {
  const history = useHistory()
  const { app } = useAppSource()

  const isWideScale = app === AppSource.WideScale

  return (
    <Drawer
      size={useMediaQuery('medium') && isWideScale ? '90%' : '100%'}
      isOpen
      closeable={false}
      onClose={() => {
        history.length === 1 ? history.replace('/') : history.goBack()
      }}
      animate={isWideScale}
      overrides={isWideScale ? undefined : drawerOverrides}
    >
      {children}
    </Drawer>
  )
}
