import { VFC, useImperativeHandle, forwardRef } from 'react'
import ContentLoader from 'react-content-loader'
import { useStyletron } from '@jifflenow/brightui'
import { FlexGrid, FlexGridItem } from '@jifflenow/brightui/flex-grid'
import { Container } from './styled-components'

const LIST_CARD_HEIGHT = 40

const LoadingDropdownRow: VFC<{ height: number }> = ({ height }) => {
  const [, theme] = useStyletron()
  const contentLoaderOptions = {
    style: {
      width: '40%',
      height: `${height}`,
      marginTop: theme.sizing.scale600,
      marginBottom: theme.sizing.scale600,
    },
    width: '1000',
    height: '100',
    viewBox: '0 0 1000 100',
    speed: 2,
    primaryColor: theme.colors.mono200,
    secondaryColor: theme.colors.mono300,
  }

  return (
    <ContentLoader {...contentLoaderOptions}>
      <rect y="0" rx={1} ry={20} width="1000" height="100" />
    </ContentLoader>
  )
}

const LoadingToggleRow: VFC<{ height: number }> = ({ height }) => {
  const [, theme] = useStyletron()
  const contentLoaderOptions = {
    style: {
      width: '100%',
      height: `${height}`,
      marginTop: theme.sizing.scale600,
      marginBottom: theme.sizing.scale600,
    },
    width: '1000',
    height: '100',
    viewBox: '0 0 1000 100',
    speed: 2,
    primaryColor: theme.colors.mono200,
    secondaryColor: theme.colors.mono300,
  }

  return (
    <ContentLoader {...contentLoaderOptions}>
      <rect y="0" rx={1} ry={20} width="1000" height="100" />
    </ContentLoader>
  )
}

export const SettingsLoader = forwardRef((_, ref) => {
  const [, theme] = useStyletron()
  useImperativeHandle(ref, () => ({
    getReactContainerStyle: () => ({
      alignSelf: 'flex-start',
      marginTop: theme.sizing.scale600,
    }),
  }))

  return (
    <Container>
      <FlexGrid
        flexGridColumnCount={1}
        overrides={{
          Block: {
            style: ({ $theme: { sizing } }) => ({
              marginTop: sizing.scale100,
              marginBottom: sizing.scale100,
            }),
          },
        }}
      >
        {Array.from({ length: 2 }).map((_, i) => (
          <FlexGridItem key={i}>
            <LoadingDropdownRow height={LIST_CARD_HEIGHT} />
          </FlexGridItem>
        ))}
        {Array.from({ length: 8 }).map((_, i) => (
          <FlexGridItem key={i}>
            <LoadingToggleRow height={LIST_CARD_HEIGHT} />
          </FlexGridItem>
        ))}
      </FlexGrid>
    </Container>
  )
})
