import { useEffect, useRef, VFC } from 'react'
import { FormattedMessage } from 'react-intl'
import { Button, KIND } from '@jifflenow/brightui/button'
import { ModalSpinner } from '@jifflenow/brightui/modal-spinner'
import { useDeAuthorize, useOAuthMeta } from 'rest'
import { showErrorToast } from 'utils/toast'
import { useProfileUser } from '../providers/ProfileUserProvider'
import { ActionButtonProps } from './types'

const childWindowParams = 'width=1000,height=600,left=200,top=100'

const getCurrentDomain = () => {
  const urlParts = window.origin.split('.')
  urlParts.shift()
  return urlParts.join('.')
}

export const ConnectButton: VFC<ActionButtonProps> = ({
  integrationName,
  onSuccess,
  onFailure,
}) => {
  const childWindow = useRef<Window | null>(null)

  const { refetch, isLoading } = useOAuthMeta(
    { conferenceName: integrationName },
    {
      enabled: false,
      onSuccess: data => {
        data?.data &&
          (childWindow.current = window.open(
            data.data.redirectionUrl,
            undefined,
            childWindowParams
          ))
      },
      onError: () => {
        showErrorToast(
          <FormattedMessage
            defaultMessage="Something went wrong. Please try again."
            description="Generic error"
          />
        )
      },
    }
  )

  useEffect(() => {
    const interval = setInterval(() => {
      if (childWindow.current?.closed) {
        childWindow.current = null
        onSuccess?.()
      }
    }, 1000)

    return () => clearInterval(interval)
  }, [onSuccess])

  useEffect(() => {
    const handler = (event: MessageEvent) => {
      const { origin, data } = event
      if (
        childWindow.current &&
        (origin === window.origin || origin.includes(getCurrentDomain()))
      ) {
        onFailure?.(data.type)
      }
    }
    window.addEventListener('message', handler)
    return () => window.removeEventListener('message', handler)
  }, [onFailure])

  return (
    <>
      {isLoading && <ModalSpinner />}
      <Button
        kind={KIND.primary}
        onClick={() => refetch()}
        data-pendo={`profile/integration-${integrationName}/connect`}
      >
        <FormattedMessage defaultMessage="Connect" description="Button label" />
      </Button>
    </>
  )
}

export const DisconnectButton: VFC<ActionButtonProps> = ({
  integrationName,
  onSuccess,
}) => {
  const { id: userUuid } = useProfileUser()
  const { isLoading, mutateAsync } = useDeAuthorize()

  const handleDisconnect = async () => {
    try {
      await mutateAsync({ conferenceName: integrationName, userUuid })
      onSuccess?.()
    } catch (error) {
      showErrorToast(
        <FormattedMessage
          defaultMessage="Something went wrong. Please try again."
          description="Generic error"
        />
      )
    }
  }

  return (
    <>
      {isLoading && <ModalSpinner />}
      <Button
        kind={KIND.primary}
        onClick={handleDisconnect}
        data-pendo={`profile/integration-${integrationName}/disconnect`}
      >
        <FormattedMessage
          defaultMessage="Disconnect"
          description="Button label"
        />
      </Button>
    </>
  )
}
