import {
  FC,
  createContext,
  useContext,
  useMemo,
  useState,
  Dispatch,
  SetStateAction,
} from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { ConfirmationDialog } from 'ui-components/ConfirmationDialog'
import { useNavProvider } from './NavProvider'

type DirtyCheck = {
  isDirty: boolean
  setIsDirty: Dispatch<SetStateAction<boolean>>
  setShowConfirmation: Dispatch<SetStateAction<boolean>>
  onCloseConfirmation: Dispatch<SetStateAction<(arg?: () => void) => void>>
  onSuccessConfirmation: Dispatch<SetStateAction<(arg?: () => void) => any>>
}

const messages = defineMessages({
  confirmationHeader: {
    defaultMessage: 'Unsaved Changes',
    description: 'Modal heading',
  },
  confirmationMessage: {
    defaultMessage:
      'You have unsaved changes, would you like to save and continue?',
    description: 'Confirmation message',
  },
  cancelButtonLabel: {
    defaultMessage: 'Discard',
    description: 'Button label',
  },
  successButtonLabel: {
    defaultMessage: 'Save',
    description: 'Button label',
  },
})

const Context = createContext<DirtyCheck>({
  isDirty: false,
  setIsDirty: () => {},
  setShowConfirmation: () => {},
  onCloseConfirmation: () => {},
  onSuccessConfirmation: () => {},
})

export const ProfileSectionDirtyCheckProvider: FC = ({ children }) => {
  const { formatMessage } = useIntl()
  const [isDirty, setIsDirty] = useState<boolean>(false)
  const [showConfirmation, setShowConfirmation] = useState<boolean>(false)
  const [onCancel, setOnCancel] = useState<(arg?: () => void) => void>(() => {})
  const [onSuccess, setOnSuccess] = useState<(arg?: () => void) => any>(
    () => {}
  )

  const {
    state: { tempSelectedKey },
    dispatch,
  } = useNavProvider()

  const handleClose = () => {
    setShowConfirmation(false)
    if (tempSelectedKey) {
      dispatch({
        type: 'setTempSelectedKey',
        payload: undefined,
      })
    }
  }

  const handleSuccess = async () => {
    setShowConfirmation(false)
    const result = await onSuccess()
    if (result === undefined) {
      setIsDirty(false)
      onCancel()
    }
  }

  const handleCancel = () => {
    setShowConfirmation(false)
    onCancel()
  }

  const value = useMemo(
    () => ({
      isDirty,
      setIsDirty,
      setShowConfirmation,
      onSuccessConfirmation: setOnSuccess,
      onCloseConfirmation: setOnCancel,
    }),
    [isDirty]
  )

  return (
    <Context.Provider value={value}>
      {showConfirmation && (
        <ConfirmationDialog
          closeable
          onClose={handleClose}
          onCancel={handleCancel}
          onSuccess={handleSuccess}
          heading={formatMessage(messages.confirmationHeader)}
          message={formatMessage(messages.confirmationMessage)}
          cancelLabel={formatMessage(messages.cancelButtonLabel)}
          successLabel={formatMessage(messages.successButtonLabel)}
        />
      )}
      {children}
    </Context.Provider>
  )
}

export const useDirtyCheck = () => {
  const context = useContext(Context)
  /* istanbul ignore next */
  if (context === undefined) {
    throw new Error(
      'useDirtyCheck must be used within a ProfileSectionDirtyCheckProvider'
    )
  }

  return context
}
