import { VFC, useState, useEffect } from 'react'
import { defineMessages, FormattedMessage, useIntl } from 'react-intl'
import { Info } from 'luxon'
import { KIND } from '@jifflenow/brightui/button'
import {
  Modal,
  ModalBody,
  ModalFooter,
  ModalHeader,
  ModalButton,
} from '@jifflenow/brightui/modal'
import { Select, Option } from '@jifflenow/brightui/select'
import { CopySlotsModalBodyWrapper } from '../styled-components'
import { modalButtonOverrides } from '../overrides'
import { CopyAvailabilityProps } from './types'
import { FilterWrapper, Filter, CopyToDateFilter } from './styled-components'
import { CopyDateSlotsTo } from './CopyDateSlotsTo'
import { DateRangeFilter } from './DateRangeFilter'
import { getDayFromDate } from './DateTimeConversions'

const messages = defineMessages({
  specificDays: {
    defaultMessage: 'Specific Days',
    description: 'Field label',
  },
  specificDates: {
    defaultMessage: 'Specific Dates',
    description: 'Field label',
  },
})

enum FilterType {
  DATES = 'dates',
  DAYS = 'days',
}

export const CopyCombinedAvailability: VFC<CopyAvailabilityProps> = props => {
  const { formatMessage } = useIntl()
  const {
    sourceDate,
    availabilityInTZ,
    setAvailabilityInTZ,
    dateRange: initialDateRange,
    ...modalProps
  } = props!

  const options = [
    {
      id: FilterType.DAYS,
      label: formatMessage(messages.specificDays),
    },
    {
      id: FilterType.DATES,
      label: formatMessage(messages.specificDates),
    },
  ] as Option[]

  const { id: initialValue } = options.find(({ id }) => id === FilterType.DAYS)!
  const [filterValue, setFilterValue] = useState(initialValue)
  const [dateRange, setDateRange] = useState(initialDateRange)

  const [target, setTarget] = useState<string[]>([] as string[])

  useEffect(() => {
    setTarget([])
    setDateRange(initialDateRange)
  }, [initialDateRange, filterValue])

  const addCopyDestinationDays = (item: string) => {
    setTarget([...target, item])
  }

  const handleCopySlots = () => {
    const { timeRanges } = availabilityInTZ.find(
      ({ date }) => date === sourceDate
    )!
    if (filterValue) {
      setAvailabilityInTZ(
        availabilityInTZ.map(availability => {
          const match =
            filterValue === FilterType.DATES
              ? availability.date
              : getDayFromDate(availability.date)

          return target.includes(match)
            ? { ...availability, timeRanges }
            : availability
        })
      )
      modalProps.onClose()
      setTarget([])
    }
  }

  return (
    <Modal {...modalProps}>
      <ModalHeader>
        <FilterWrapper>
          <div>
            <FormattedMessage
              defaultMessage="Copy availability of {date} to"
              description="Header title"
              values={{
                date: sourceDate,
              }}
            />
          </div>

          <Filter>
            <Select
              id="copyFilter"
              options={options}
              required
              clearable={false}
              value={options.filter(({ id }) => filterValue === id)}
              onChange={({ value }: { value: Option[] }) =>
                setFilterValue(value[0].id)
              }
            />
          </Filter>
        </FilterWrapper>
      </ModalHeader>
      <ModalBody>
        <CopySlotsModalBodyWrapper>
          {filterValue === FilterType.DATES ? (
            <>
              <CopyToDateFilter>
                <DateRangeFilter
                  dateRange={dateRange}
                  onChange={(dateRange: string[]) => setDateRange(dateRange)}
                  minDate={initialDateRange[0]}
                  maxDate={initialDateRange[1]}
                />
              </CopyToDateFilter>
              {availabilityInTZ
                ?.filter(
                  ({ date }) =>
                    date !== sourceDate &&
                    date >= dateRange[0] &&
                    date <= dateRange[1]
                )
                .map(({ date }) => (
                  <CopyDateSlotsTo
                    key={date}
                    item={date}
                    dateFilter
                    copyTo={addCopyDestinationDays}
                  />
                ))}
            </>
          ) : (
            Info.weekdays('long').map(day => (
              <CopyDateSlotsTo
                key={day}
                item={day}
                copyTo={addCopyDestinationDays}
              />
            ))
          )}
        </CopySlotsModalBodyWrapper>
      </ModalBody>
      <ModalFooter>
        <ModalButton
          kind={KIND.tertiary}
          overrides={modalButtonOverrides}
          onClick={modalProps.onClose}
          data-pendo="profile/availability/copy-cancel"
        >
          <FormattedMessage
            defaultMessage="Cancel"
            description="Button label"
          />
        </ModalButton>
        <ModalButton
          kind={KIND.primary}
          onClick={handleCopySlots}
          overrides={modalButtonOverrides}
          data-pendo="profile/availability/copy-ok"
        >
          <FormattedMessage defaultMessage="OK" description="Button label" />
        </ModalButton>
      </ModalFooter>
    </Modal>
  )
}
