import { createContext, FC, useContext, useEffect, useState } from 'react'
import { ErrorPage } from 'ui-components/ErrorPage'
import { useLocation } from 'react-router-dom'
import { useEventLocationQuery } from 'gql'
import { ModalSpinner } from '@jifflenow/brightui/modal-spinner'
import { useEventTypes } from 'rest'
import { LocationData } from '../Common/types'

const Context = createContext<LocationData | undefined>(undefined)

const LocationFetcher: FC<{
  onCompleted: (data: LocationData) => void
}> = ({ onCompleted }) => {
  const { isLoading: loading, data, error, refetch } = useEventTypes()
  const locationId = new URLSearchParams(useLocation().search).get('location')

  const {
    loading: locationLoading,
    roles: location,
    error: locationError,
    refetch: locationRefetch,
  } = useEventLocationQuery({
    variables: {
      uuid: new URLSearchParams(useLocation().search).get('location')!,
    },
    skip: !locationId,
  })

  useEffect(() => {
    locationId && data && location
      ? onCompleted({
          eventTypes: data,
          location,
        })
      : data &&
        !locationId &&
        onCompleted({
          eventTypes: data,
          location: undefined,
        })
  }, [data, location, locationId, onCompleted])

  if (error || locationError) {
    return (
      <ErrorPage
        onButtonClick={() => (error ? refetch() : locationRefetch())}
      />
    )
  }

  if (loading || locationLoading) {
    return <ModalSpinner noBackground />
  }

  return null
}

export const EventInfoProvider: FC = ({ children }) => {
  const [location, setLocation] = useState<LocationData | undefined>(undefined)

  return !location ? (
    <LocationFetcher onCompleted={data => setLocation(data)} />
  ) : (
    <Context.Provider value={location}>{children}</Context.Provider>
  )
}

export const useEventInfo = () => {
  const context = useContext(Context) as LocationData
  if (context === undefined && process.env.NODE_ENV !== 'production') {
    throw new Error('useEventInfo must be used within a EventInfoProvider')
  }

  return context
}
