import { useReducer } from 'react'
import { Action, Mapping, State } from './types'

const DEFAULT_VALUE: Mapping = {
  mappedCount: 0,
  unMappedCount: 0,
  requestAll: undefined,
  participateAll: undefined,
  mappedItems: new Map(),
  initialItems: new Map(),
}

const initialState: State = {
  mapped: DEFAULT_VALUE,
  unmapped: DEFAULT_VALUE,
}

// eslint-disable-next-line complexity
const reducer = (state: State, action: Action): State => {
  const { type } = action

  switch (type) {
    case 'requestAll': {
      const { activeTab, requestAll } = action.payload
      const activeTabState = state[activeTab]
      const { initialItems, mappedItems } = activeTabState

      const mappedItemsCopy = new Map([...mappedItems])
      for (const [key, value] of mappedItems) {
        const initialValue = initialItems.get(key)
        initialValue?.canParticipate === value.canParticipate
          ? mappedItemsCopy.delete(key)
          : mappedItemsCopy.set(key, {
              ...value,
              canRequest: requestAll,
            })
      }

      return {
        ...state,
        [activeTab]: {
          ...state[activeTab],
          requestAll,
          mappedItems: mappedItemsCopy,
        },
      }
    }
    case 'participateAll': {
      const { activeTab, participateAll } = action.payload
      const activeTabState = state[activeTab]
      const { initialItems, mappedItems } = activeTabState

      const mappedItemsCopy = new Map([...mappedItems])
      for (const [key, value] of mappedItems) {
        const initialValue = initialItems.get(key)
        initialValue?.canRequest === value.canRequest
          ? mappedItemsCopy.delete(key)
          : mappedItemsCopy.set(key, {
              ...value,
              canParticipate: participateAll,
            })
      }

      return {
        ...state,
        [activeTab]: {
          ...state[activeTab],
          participateAll,
          mappedItems: mappedItemsCopy,
        },
      }
    }
    case 'updateMapping': {
      const {
        activeTab,
        mappedCount,
        unMappedCount,
        initialItems,
      } = action.payload
      const activeTabState = state[activeTab]

      return {
        ...state,
        [activeTab]: {
          ...activeTabState,
          mappedCount: mappedCount ?? activeTabState.mappedCount,
          unMappedCount: unMappedCount ?? activeTabState.unMappedCount,
          initialItems: initialItems
            ? new Map([...activeTabState.initialItems, ...(initialItems ?? [])])
            : activeTabState.initialItems,
        },
      }
    }
    case 'setCanRequest': {
      const { activeTab, item } = action.payload
      const activeTabState = state[activeTab]
      const { initialItems, mappedItems, requestAll } = activeTabState

      const mappedItemsCopy = new Map([...mappedItems])
      const initialValue = initialItems.get(item.id)

      initialValue?.canParticipate === item.canParticipate
        ? requestAll === undefined
          ? initialValue?.canRequest === item.canRequest
            ? mappedItemsCopy.delete(item.id)
            : mappedItemsCopy.set(item.id, item)
          : requestAll === item.canRequest
          ? mappedItemsCopy.delete(item.id)
          : mappedItemsCopy.set(item.id, item)
        : mappedItemsCopy.set(item.id, item)

      return {
        ...state,
        [activeTab]: {
          ...activeTabState,
          mappedItems: mappedItemsCopy,
        },
      }
    }
    case 'setCanParticipate': {
      const { activeTab, item } = action.payload
      const activeTabState = state[activeTab]
      const { initialItems, mappedItems, participateAll } = activeTabState

      const mappedItemsCopy = new Map([...mappedItems])
      const initialValue = initialItems.get(item.id)

      initialValue?.canRequest === item.canRequest
        ? participateAll === undefined
          ? initialValue?.canParticipate === item.canParticipate
            ? mappedItemsCopy.delete(item.id)
            : mappedItemsCopy.set(item.id, item)
          : participateAll === item.canParticipate
          ? mappedItemsCopy.delete(item.id)
          : mappedItemsCopy.set(item.id, item)
        : mappedItemsCopy.set(item.id, item)

      return {
        ...state,
        [activeTab]: {
          ...activeTabState,
          mappedItems: mappedItemsCopy,
        },
      }
    }
    case 'resetValues': {
      return initialState
    }
    case 'discardChanges': {
      const { mapped, unmapped } = state

      return {
        mapped: {
          ...mapped,
          requestAll: undefined,
          participateAll: undefined,
          mappedItems: new Map(),
        },
        unmapped: {
          ...unmapped,
          requestAll: undefined,
          participateAll: undefined,
          mappedItems: new Map(),
        },
      }
    }

    default:
      return state
  }
}

export const useEngagementTypes = () => useReducer(reducer, initialState)
