import { FC, createContext, useContext, useMemo } from 'react'

type EventNameType = { eventType?: string }

const EventName = createContext<EventNameType | null>(null)

export const EventNameProvider: FC<EventNameType> = ({
  eventType,
  children,
}) => {
  const value = useMemo(() => ({ eventType: eventType ?? '' }), [eventType])

  return <EventName.Provider value={value}>{children}</EventName.Provider>
}

export const useEventName = () => {
  const context = useContext(EventName)
  /* istanbul ignore next */
  if (context === null && process.env.NODE_ENV !== 'production') {
    throw new Error('useEventName must be used within a EventNameProvider')
  }

  return context!
}
