import { FC } from 'react'
import { components } from 'types/openapi'
import { FlexGrid, FlexGridItem } from '@jifflenow/brightui/flex-grid'
import { flexGridOverrides } from 'components/Events'
import { useWindowSize } from 'utils/useWindowSize'
import { BlockProps } from '@jifflenow/brightui/block'
import { NoItem } from './NoItem'
import { EventCard } from './EventCard'

type Locations = components['schemas']['Locations'] & { eventType?: string }

export const ItemsGrid: FC<Locations> = ({
  meta: { page },
  data: locations,
  eventType,
}) => {
  const { windowWidth } = useWindowSize()
  const isFullMode = page.total < 4 && windowWidth > 1365
  const getColumnCount = () => [
    isFullMode
      ? windowWidth > 1365
        ? 3
        : 1
      : windowWidth <= 1040
      ? 1
      : windowWidth <= 1365
      ? 2
      : page.total < 4 || windowWidth <= 1660
      ? 3
      : 4,
  ]

  const itemProps: BlockProps = {
    justifyContent: 'center',
    alignContent: 'center',
    alignItems: 'center',
    paddingBottom: '24px',
  }

  return locations.length ? (
    <FlexGrid
      flexGridColumnCount={getColumnCount()}
      flexGridColumnGap="24px"
      overrides={flexGridOverrides}
    >
      {locations.map(location => (
        <FlexGridItem {...itemProps} key={location.id}>
          <EventCard location={location} eventCount={page.total} />
        </FlexGridItem>
      ))}
    </FlexGrid>
  ) : (
    // TODO: get confirmation on empty screen for campaign listing
    <NoItem eventType={eventType} />
  )
}
