import { VFC, ChangeEvent } from 'react'
import { Checkbox } from '@jifflenow/brightui/checkbox'
import { TableHeaderRoot, ColumnTitle } from './styled-components'
import { ColumnCheckboxProps } from './types'

export const ColumnCheckbox: VFC<ColumnCheckboxProps> = ({
  label,
  checked,
  intermediate,
  handleSelectAll,
  handleDeselectAll,
  showCheckbox,
  columnName,
  disabled,
}) => {
  const handleChange = ({ target }: ChangeEvent<HTMLInputElement>) => {
    if (target.checked) {
      handleSelectAll({ [columnName]: true })
    } else {
      handleDeselectAll({ [columnName]: false })
    }
  }

  return (
    <TableHeaderRoot>
      {showCheckbox ? (
        <ColumnTitle>{label}</ColumnTitle>
      ) : (
        <Checkbox
          checked={checked}
          isIndeterminate={intermediate}
          onChange={handleChange}
          disabled={disabled}
        >
          {label}
        </Checkbox>
      )}
    </TableHeaderRoot>
  )
}
