import { FC } from 'react'
import { Select, OnChangeParams, Option } from '@jifflenow/brightui/select'
import { Field, Validation } from 'ui-components/Field'
import { MultiSelectFieldProps } from '../types'
import { FormControl } from './styled-components'

export const MultiSelectField: FC<MultiSelectFieldProps> = props => {
  const {
    name,
    label,
    options,
    creatable,
    clearable,
    validations,
    disabled,
  } = props

  return (
    <Field<(string | number | undefined)[] | undefined>
      name={name}
      validations={validations}
      parse={(value: Option[]) => value.map(({ id }: Option) => id)}
      format={value =>
        value ? value.map(val => ({ id: val, label: val })) : []
      }
      render={({ input, meta }) => (
        <FormControl label={label} error={meta.touched && meta.error}>
          <Select
            {...input}
            multi
            id={name}
            required={validations.includes(Validation.REQUIRED)}
            searchable
            disabled={disabled}
            creatable={creatable}
            clearable={clearable}
            backspaceClearsInputValue
            onCloseResetsInput={false}
            onBlurResetsInput={false}
            options={options?.map(option => ({ id: option, label: option }))}
            onChange={({ value }: OnChangeParams) => {
              input.onChange(value)
            }}
          />
        </FormControl>
      )}
    />
  )
}
