import { CustomHeaderProps } from '../types'
import { StyledHeader, StyledPageBreak, Hr } from './styled-components'

export const Header = (props: CustomHeaderProps) => (
  <StyledHeader
    $isHidden={props.hidden}
    dangerouslySetInnerHTML={{ __html: props.label ?? '' }}
  />
)

export const PageBreak = (props: CustomHeaderProps) => (
  <>
    <Hr $isHidden={props.hidden} />
    <StyledPageBreak $isHidden={props.hidden}>{props.label}</StyledPageBreak>
  </>
)
