import { FC } from 'react'
import { useIntl, defineMessages } from 'react-intl'
import { DateTime } from 'luxon'
import { Add, Trash, CopyOutlined } from '@jifflenow/brightui/icon'
import { IconButton } from '@jifflenow/brightui/button'
import { weekDays } from 'utils/messages'
import { RangeTimeSlot } from 'ui-components/RangeTimeSlot'
import { useAvailability } from '../AvailabilityProvider'
import {
  getISODateFromWeekDayAndTime,
  getISOTimeFromDate,
  getStartOfDateFromWeekDay,
  getISOTimeFromJSDate,
  getFullMinutes,
  getEndDateOfDay,
} from '../DateTimeConversions'
import { Availability } from '../types'
import {
  checkForOverlaps as checkForOverlapsUtility,
  deleteAllSlots as deleteAllSlotsUtility,
  addNewSlot as addNewSlotUtitility,
  deleteSlot as deleteSlotUtility,
} from '../utilities'
import { iconButtonOverrides } from './overrides'
import { AvailabilityDayProps } from './types'
import {
  WeekDayRoot,
  WeekDayTitle,
  SlotsRoot,
  ActionsContainer,
  TitleActionsContainer,
  TimeSlotWrapper,
  DaySlotWrapper,
  ActionBar,
  WeekDayTitleMobile,
} from './styled-components'

const messages = defineMessages({
  overlapError: {
    defaultMessage: 'Time overlap',
    description: 'Error message',
  },
  endTimeLessThanStartTime: {
    defaultMessage: 'Choose an end time later than start time.',
    description: 'Error message',
  },
})

export const AvailabilityDay: FC<AvailabilityDayProps> = ({
  dayAvailability,
  onCopySlots,
}) => {
  const { formatMessage } = useIntl()
  const { availability, setAvailability } = useAvailability()
  const { day, weekDay, timeRanges } = dayAvailability

  const lastSlotEndsAt12AM = timeRanges.length
    ? DateTime.fromISO(timeRanges[timeRanges.length - 1].end).hour === 0
    : false

  const check = (availability: Availability) => availability.weekDay === weekDay
  const getStartOfDay = () => getStartOfDateFromWeekDay(weekDay)
  const getNextSlot = (prevEndTime: string) =>
    getISODateFromWeekDayAndTime(weekDay, prevEndTime)

  const deleteAllSlots = () => {
    deleteAllSlotsUtility<Availability>({
      availability,
      setAvailability,
      check,
    })
  }

  const addNewSlot = () => {
    addNewSlotUtitility<Availability>({
      availability,
      setAvailability,
      check,
      timeRanges,
      getNextSlot,
      getStartOfDay,
    })
  }

  const deleteSlot = (indexToDelete: number) => {
    deleteSlotUtility({
      availability,
      setAvailability,
      check,
      formatMessage,
      indexToDelete,
    })
  }

  const checkForOverlaps = () => {
    checkForOverlapsUtility({ setAvailability, formatMessage, check })
  }

  const editSlotStartTime = (date: Date, indexToUpdate: number) => {
    const startDateTime = DateTime.fromJSDate(date)
    /**
     * FinalizeEndTime decides to update the end time based on the newly
     * selected start time, there are 3 cases -
     * 1. If end time is already 12:00am, end time should result to no change
     * 2. If start time is already greater than 23:00pm, end time will be 12:00am
     * 3. If start time is greater/equal, end time will be start time + 1 hour
     */
    const finalizeEndTime = (end: string, index: number) =>
      getFullMinutes(startDateTime.toISO()) === 0
        ? end
        : startDateTime.toMillis() >=
          DateTime.fromISO(
            getISODateFromWeekDayAndTime(
              weekDay,
              timeRanges.find((_, idx) => idx === index)?.end!
            )
          ).toMillis()
        ? getISOTimeFromJSDate(
            startDateTime.hour === 23
              ? DateTime.fromISO(
                  `${startDateTime.toISODate()}T24:00:00`
                ).toJSDate()
              : startDateTime.plus({ hour: 1 }).toJSDate()
          )
        : end

    setAvailability(
      availability.map(availability =>
        availability.weekDay === weekDay
          ? {
              ...availability,
              timeRanges: availability.timeRanges.map((range, index) =>
                index === indexToUpdate
                  ? {
                      start: getISOTimeFromJSDate(date),
                      end: finalizeEndTime(range.end, index),
                    }
                  : range
              ),
            }
          : availability
      )
    )

    checkForOverlaps()
  }

  const editSlotEndTime = (date: Date, indexToUpdate: number) => {
    setAvailability(
      availability.map(availability =>
        availability.weekDay === weekDay
          ? {
              ...availability,
              timeRanges: availability.timeRanges.map((range, index) => {
                if (index !== indexToUpdate) {
                  return range
                }

                const is12AM = getFullMinutes(date.toISOString()) === 0

                const end = is12AM
                  ? getISOTimeFromDate(getEndDateOfDay(date.toISOString()))
                  : getISOTimeFromJSDate(date)

                return {
                  start: range.start,
                  ...(!is12AM && end <= range.start
                    ? {
                        error: formatMessage(messages.endTimeLessThanStartTime),
                      }
                    : null),
                  end,
                }
              }),
            }
          : availability
      )
    )

    checkForOverlaps()
  }

  const isSlotsEmpty = !timeRanges.length

  return (
    <WeekDayRoot key={day} $isEmpty={isSlotsEmpty}>
      <TitleActionsContainer>
        <DaySlotWrapper>
          <WeekDayTitle>
            {formatMessage(weekDays[day as keyof typeof weekDays])}
          </WeekDayTitle>
          <SlotsRoot>
            {timeRanges.map(({ start, end, error }, index) => (
              <TimeSlotWrapper key={`${index}-${start}-${end}`}>
                <RangeTimeSlot
                  start={getISODateFromWeekDayAndTime(weekDay, start)}
                  end={getISODateFromWeekDayAndTime(weekDay, end)}
                  error={error}
                  onDelete={() => deleteSlot(index)}
                  onStartChange={date => editSlotStartTime(date, index)}
                  onEndChange={date => editSlotEndTime(date, index)}
                />
              </TimeSlotWrapper>
            ))}
          </SlotsRoot>
        </DaySlotWrapper>
        <ActionBar $isEmpty={isSlotsEmpty}>
          <WeekDayTitleMobile>
            {formatMessage(weekDays[day as keyof typeof weekDays])}
          </WeekDayTitleMobile>
          <ActionsContainer
            data-pendo={`profile/availability/${day}-action-container`}
          >
            <IconButton
              size={20}
              overrides={iconButtonOverrides}
              disabled={lastSlotEndsAt12AM}
              onClick={addNewSlot}
              data-pendo="profile/availability/add"
            >
              <Add size={20} />
            </IconButton>
            <IconButton
              size={20}
              overrides={iconButtonOverrides}
              disabled={!timeRanges.length}
              onClick={() => onCopySlots(weekDay)}
              data-pendo="profile/availability/copy"
            >
              <CopyOutlined />
            </IconButton>
            <IconButton
              size={20}
              disabled={!timeRanges.length}
              overrides={iconButtonOverrides}
              onClick={deleteAllSlots}
              data-pendo="profile/availability/delete"
            >
              <Trash />
            </IconButton>
          </ActionsContainer>
        </ActionBar>
      </TitleActionsContainer>
    </WeekDayRoot>
  )
}
