import { VFC, useState, useMemo } from 'react'
import { FormattedMessage } from 'react-intl'
import { Form } from 'react-final-form'
import { Config } from 'final-form'
import { useQueryClient } from 'react-query'
import { Modal, ModalFooter, ModalHeader } from '@jifflenow/brightui/modal'
import { KIND } from '@jifflenow/brightui/button'
import { ModalSpinner } from '@jifflenow/brightui/modal-spinner'
import { Cropper } from 'ui-components/ImageCropper/Cropper'
import { MinWidthButton } from 'ui-components/common/styled-components'
import { getCroppedImg, MimeType, OutputType } from 'ui-components/ImageCropper'
import { useMe } from 'providers/MeProvider'
import { useProfileUser } from 'components/ProfileDrawer/providers/ProfileUserProvider'
import { useAvatarUpload } from 'rest'
import { showErrorToast, showSuccessToast } from 'utils/toast'
import { sendMessage, Message } from 'utils/messageDispatcher'
import { modalButtonOverrides } from '../overrides'
import {
  AvatarConsent,
  AvatarConsentProps,
  FormValues,
  UploadAvatarProps,
} from './types'
import { CropperContainer, ModalBody } from './styled-components'
import { AvatarConsentField, transformConsent } from './AvatarConsentField'

export const UploadAvatar: VFC<UploadAvatarProps> = ({
  data,
  image,
  onCancel,
  onClose,
  onSuccess,
  onError,
}) => {
  const queryClient = useQueryClient()
  const [croppedAreaPixels, setCroppedAreaPixels] = useState({
    x: 0,
    y: 0,
    width: 0,
    height: 0,
  })

  const userId = useProfileUser().id
  const loggedInUserId = useMe().data.id
  const isUploadingForSelf = userId === loggedInUserId

  const { isLoading: avatarUploadInProgress, mutateAsync } = useAvatarUpload()

  const consents = data.custom_consents.avatar_upload_consent_message.data.map(
    transformConsent
  )

  const getConsent = (consentName: AvatarConsent): AvatarConsentProps =>
    consents.find(({ name }) => name === consentName)!

  const consent = getConsent(
    isUploadingForSelf
      ? 'avatar_upload_self_consent_message'
      : 'avatar_upload_behalf_consent_message'
  )

  const initialValue = useMemo(() => ({ [consent.name]: false }), [
    consent.name,
  ])

  const handleSubmit: Config<FormValues>['onSubmit'] = async values => {
    try {
      const avatar = (await getCroppedImg(
        URL.createObjectURL(image),
        croppedAreaPixels,
        'round',
        image.type as MimeType,
        OutputType.DATA_URL
      )) as string
      await mutateAsync({
        avatar: {
          content: avatar,
          contentType: image.type,
          fileName: 'avatar',
        },
        userId,
        consentField: consent.name as AvatarConsent,
        consent: values[consent.name as AvatarConsent],
      })
      onSuccess(avatar)
      showSuccessToast(
        <FormattedMessage
          defaultMessage="Image uploaded successfully"
          description="Toast message"
        />
      )
      if (isUploadingForSelf) {
        queryClient.invalidateQueries('userMe')
        sendMessage(Message.RELOAD_ME)
      }
    } catch (error) {
      console.error(error)
      showErrorToast(
        <FormattedMessage
          defaultMessage="Error uploading image"
          description="Toast message"
        />
      )
    }

    return undefined
  }

  const imageUrl = useMemo(() => URL.createObjectURL(image), [image])

  return (
    <>
      {avatarUploadInProgress && <ModalSpinner />}
      <Modal onClose={onClose} isOpen>
        <Form<FormValues>
          onSubmit={handleSubmit}
          initialValues={initialValue}
          render={({ handleSubmit }) => (
            <form onSubmit={handleSubmit}>
              <ModalHeader>
                <FormattedMessage
                  defaultMessage="Profile Photo"
                  description="Modal heading"
                />
              </ModalHeader>
              <ModalBody>
                <CropperContainer>
                  <Cropper
                    image={imageUrl}
                    cropShape="round"
                    onCropChange={setCroppedAreaPixels}
                    onError={onError}
                  />
                </CropperContainer>
                <AvatarConsentField {...consent} />
              </ModalBody>
              <ModalFooter>
                <MinWidthButton
                  type="button"
                  kind={KIND.secondary}
                  onClick={onCancel}
                >
                  <FormattedMessage
                    defaultMessage="Cancel"
                    description="Button label"
                  />
                </MinWidthButton>
                <MinWidthButton type="submit" overrides={modalButtonOverrides}>
                  <FormattedMessage
                    defaultMessage="Save"
                    description="Button label"
                  />
                </MinWidthButton>
              </ModalFooter>
            </form>
          )}
        />
      </Modal>
    </>
  )
}
