import { VFC } from 'react'
import { DateTime } from 'luxon'
import { defineMessages, useIntl } from 'react-intl'
import {
  CityFilter,
  FilterOptionFormat,
  filterOverrides,
  dateFilterOverrides,
  statusOptions,
  modeOptions,
  FiltersContainer,
  FilterItem,
} from 'components/Events'
import { DateRangeFilter, TextFilter } from '@jifflenow/brightui/filter'
import { filterOptionLabel } from 'ui-components/common/messages'
import { EventTypeFilter } from '../../Common/CampaignFilter'
import { FilterProps, EventStatusFilter, EventMode } from './types'
import { ActionText } from './styled-components'

const messages = defineMessages({
  eventStatus: {
    defaultMessage: 'Status',
    description: 'Filter Label for status of the event instance',
  },
  eventMode: {
    defaultMessage: 'Mode',
    description: 'Filter Label for mode of the event instance',
  },
  dateRange: {
    defaultMessage: 'Date Range',
    description: 'Filter Label',
  },
  hostingType: {
    defaultMessage: 'Hosting Type',
    description: 'Filter Label',
  },
  actionText: {
    defaultMessage: 'Filter by',
    description: 'Filter Label',
  },
})

const toFilterFormat = (option: string) => ({ id: option, label: option })

export const Filters: VFC<FilterProps> = ({ filters, onChange, eventType }) => {
  const { formatMessage } = useIntl()

  const toFilterOptionFormat = (option: EventMode | EventStatusFilter) => ({
    id: option,
    label: formatMessage(filterOptionLabel[option]),
  })

  const {
    types = [],
    statuses = [],
    modes = [],
    cities = [],
    dateInterval = '',
  } = filters ?? {}

  const handleDateChange = (dateRange: Date[]) => {
    dateRange =
      dateRange?.length === 1 ? [dateRange[0], dateRange[0]] : dateRange
    onChange({
      dateInterval:
        dateRange
          ?.map((date: Date) => DateTime.fromJSDate(date).toISODate())
          .join('--') ?? '',
    })
  }

  return (
    <FiltersContainer>
      <ActionText>{formatMessage(messages.actionText)}</ActionText>
      {eventType !== 'Emeet' && (
        <FilterItem>
          <EventTypeFilter
            types={types}
            onChange={eventTypes =>
              onChange({
                types: eventTypes,
              })
            }
          />
        </FilterItem>
      )}
      <FilterItem>
        <TextFilter
          label={formatMessage(messages.eventStatus)}
          onChange={newStatuses => {
            onChange({
              statuses: newStatuses.map(({ id }) => id as EventStatusFilter),
            })
          }}
          options={statusOptions.map(toFilterOptionFormat)}
          overrides={filterOverrides}
          value={statuses.map(toFilterOptionFormat)}
        />
      </FilterItem>
      <FilterItem>
        <TextFilter
          label={formatMessage(messages.eventMode)}
          onChange={newModes => {
            onChange({
              modes: newModes.map(({ id }) => id as EventMode),
            })
          }}
          options={modeOptions.map(toFilterOptionFormat)}
          overrides={filterOverrides}
          value={modes.map(toFilterOptionFormat)}
        />
      </FilterItem>
      <FilterItem>
        <DateRangeFilter
          label={formatMessage(messages.dateRange)}
          onChange={handleDateChange}
          value={
            dateInterval
              ? dateInterval.split('--').map(date => new Date(date))
              : []
          }
          overrides={dateFilterOverrides}
        />
      </FilterItem>
      {/* <FilterItem>
        <TextFilter
          label={formatMessage(messages.hostingType)}
          onChange={newHostingType =>
            setFilters(prevFilters => ({
              ...prevFilters,
              categories: newHostingType.map(({ id }) => id as EventCategory),
            }))
          }
          options={categoryOptions.map(toFilterFormat)}
          overrides={dateFilterOverrides}
          value={categories.map(toFilterFormat)}
        />
      </FilterItem> */}
      <FilterItem>
        <CityFilter
          onChange={(newCities: [FilterOptionFormat]) => {
            onChange({
              cities: newCities.map(({ id }) => id!),
            })
          }}
          value={cities.map(toFilterFormat)}
        />
      </FilterItem>
    </FiltersContainer>
  )
}
