import { FC, useState, ChangeEvent } from 'react'
import { Checkbox } from '@jifflenow/brightui/checkbox'
import { Paragraph1 } from '@jifflenow/brightui/typography'
import { Field } from 'ui-components/Field'
import { CheckboxFieldProps } from '../types'
import { FormControl } from './styled-components'

export const CheckboxField: FC<CheckboxFieldProps> = props => {
  const { name, type, label, validations, checked } = props
  const [isChecked, setIsChecked] = useState(checked)

  return (
    <Field<string>
      name={name}
      validations={validations}
      render={({ input, meta }) => (
        <FormControl error={meta.touched && meta.error}>
          <Checkbox
            {...input}
            id={name}
            type={type}
            checked={isChecked}
            onChange={({ target }: ChangeEvent<HTMLInputElement>) => {
              setIsChecked(target.checked)
              input.onChange(target.checked)
            }}
          >
            <Paragraph1
              color="inherit"
              className="anchor-primary"
              // @ts-expect-error
              dangerouslySetInnerHTML={{ __html: label }}
            />
          </Checkbox>
        </FormControl>
      )}
    />
  )
}
