import { VFC, useState } from 'react'
import { FormattedMessage } from 'react-intl'
import { useQueryClient } from 'react-query'
import { KIND } from '@jifflenow/brightui/button'
import {
  Modal,
  ModalBody,
  ModalFooter,
  ModalHeader,
  SIZE,
} from '@jifflenow/brightui/modal'
import { showSuccessToast, showErrorToast } from 'utils/toast'
import { ModalSpinner } from '@jifflenow/brightui/modal-spinner'
import { MinWidthButton } from 'ui-components/common/styled-components'
import { useMe } from 'providers/MeProvider'
import { useProfileUser } from 'components/ProfileDrawer/providers/ProfileUserProvider'
import { useAvatarDelete } from 'rest'
import { sendMessage, Message } from 'utils/messageDispatcher'
import { ImageDeleteButton } from './ImageDeleteButton'
import { DeleteAvatarProps } from './types'
import { modalButtonOverrides } from './overrides'

export const DeleteAvatar: VFC<DeleteAvatarProps> = ({ revertAvatar }) => {
  const queryClient = useQueryClient()
  const { mutateAsync: deleteUserAvatar } = useAvatarDelete()
  const loggedInUserId = useMe().data.id
  const userId = useProfileUser().id

  const [isOpen, setIsOpen] = useState(false)
  const [loading, setLoading] = useState(false)
  const close = () => setIsOpen(false)

  const handleDelete = async () => {
    close()
    setLoading(true)
    try {
      await deleteUserAvatar({ userId })
      setLoading(false)
      revertAvatar()
      showSuccessToast(
        <FormattedMessage
          defaultMessage="Image deleted successfully"
          description="Toast message"
        />
      )
      if (userId === loggedInUserId) {
        queryClient.invalidateQueries('userMe')
        sendMessage(Message.RELOAD_ME)
      }
    } catch (error) {
      setLoading(false)
      showErrorToast(
        <FormattedMessage
          defaultMessage="Error deleting image"
          description="Toast message"
        />
      )
    }
  }

  return (
    <>
      {loading && <ModalSpinner />}
      <ImageDeleteButton type="button" onClick={() => setIsOpen(true)} />
      <Modal size={SIZE.small} onClose={close} isOpen={isOpen}>
        <ModalHeader>
          <FormattedMessage
            defaultMessage="Delete Photo"
            description="Header title"
          />
        </ModalHeader>
        <ModalBody>
          <FormattedMessage
            defaultMessage="Are you sure you want to delete the profile image?"
            description="Confirmation message"
          />
        </ModalBody>
        <ModalFooter>
          <MinWidthButton type="button" kind={KIND.secondary} onClick={close}>
            <FormattedMessage defaultMessage="Cancel" description="Cancel" />
          </MinWidthButton>
          <MinWidthButton
            type="button"
            overrides={modalButtonOverrides}
            kind={KIND.primary}
            onClick={handleDelete}
          >
            <FormattedMessage defaultMessage="Delete" description="Delete" />
          </MinWidthButton>
        </ModalFooter>
      </Modal>
    </>
  )
}
