import { FC } from 'react'
import { Checkbox } from '@jifflenow/brightui/checkbox'
import { Paragraph1 } from '@jifflenow/brightui/typography'
import { Field, Validation } from 'ui-components/Field'
import { components } from 'types/openapi'
import { AvatarConsentFieldProps, AvatarConsentProps } from './types'
import { FormControl } from './styled-components'

type CustomConsent = components['schemas']['CustomConsent']

type TransformField = (props: CustomConsent) => AvatarConsentProps

export const transformConsent: TransformField = ({
  field_name,
  optionLabel,
  helpText,
  required,
}: CustomConsent) => ({
  name: field_name,
  label: optionLabel,
  helpText,
  validations: required ? [Validation.REQUIRED] : [],
})

export const AvatarConsentField: FC<AvatarConsentFieldProps> = props => {
  const { name, label, helpText, validations } = props

  return (
    <Field<string>
      type="checkbox"
      name={name}
      validations={validations}
      render={({ input, meta }) => (
        <FormControl caption={helpText} error={meta.touched && meta.error}>
          <Checkbox {...input}>
            <Paragraph1
              color="inherit"
              className="anchor-primary"
              // @ts-expect-error
              dangerouslySetInnerHTML={{ __html: label }}
            />
          </Checkbox>
        </FormControl>
      )}
    />
  )
}
