import { useCallback, useState, VFC } from 'react'
import { defineMessages, useIntl, MessageDescriptor } from 'react-intl'
import {
  Tabs,
  Tab,
  TabTitle,
  getTabOverrides,
} from '@jifflenow/brightui/tabs-motion'
import { CampaignProvider } from '../Common'
import { useDirtyCheck } from '../providers/ProfileSectionDirtyCheckProvider'
import { useNavProvider } from '../providers/NavProvider'
import { useAvailability } from './AvailabilityProvider'
import { Availabilities } from './Availabilities'
import { Exceptions } from './Exceptions'
import { AvailabilityTabsProps, AvailabilityTypes } from './types'
import { tabOverrides, tabsOverrides } from './overrides'
// import { ListLoader } from './Availabilities/Loader'

const messages = defineMessages({
  availability: {
    defaultMessage: 'Availability',
    description: 'Tab title',
  },
  exceptions: {
    defaultMessage: 'Exceptions',
    description: 'Tab title',
  },
})

export const AvailabilityTabs: VFC<AvailabilityTabsProps> = ({ timeZone }) => {
  const { formatMessage } = useIntl()
  const { Availability, Exceptions: ExceptionsTabKey } = AvailabilityTypes
  const { isDirty, setShowConfirmation, onCloseConfirmation } = useDirtyCheck()
  const [activeKey, setActiveKey] = useState<AvailabilityTypes>(Availability)
  const [tempActiveKey, setTempActiveKey] = useState<
    AvailabilityTypes | undefined
  >(undefined)
  const { isDirty: isAvailabilityDirty, resetAvailability } = useAvailability()

  const tabTitle = (message: MessageDescriptor) => (
    <TabTitle>{formatMessage(message)}</TabTitle>
  )

  const {
    state: { tempSelectedKey },
    dispatch,
  } = useNavProvider()

  const clearTempActiveKey = () => {
    if (tempActiveKey) {
      setTempActiveKey(undefined)
    }
  }

  const handleSuccess = useCallback(() => {
    if (tempSelectedKey) {
      dispatch({
        type: 'setSelectedKey',
        payload: tempSelectedKey,
      })
      dispatch({
        type: 'setTempSelectedKey',
        payload: undefined,
      })
    } else if (tempActiveKey) {
      setActiveKey(tempActiveKey)
      setTempActiveKey(undefined)
    }
  }, [dispatch, tempActiveKey, tempSelectedKey])

  return (
    <Tabs
      activeKey={activeKey}
      onChange={({ activeKey }) => {
        if (isDirty) {
          onCloseConfirmation(() => () => {
            setActiveKey(activeKey as AvailabilityTypes)
            isAvailabilityDirty && resetAvailability()
          })
          setShowConfirmation(true)
          setTempActiveKey(activeKey as AvailabilityTypes)
        } else {
          setActiveKey(activeKey as AvailabilityTypes)
        }
      }}
      overrides={tabsOverrides}
    >
      <Tab
        key={Availability}
        title={tabTitle(messages.availability)}
        overrides={getTabOverrides(tabOverrides)}
      >
        <Availabilities
          onSuccess={handleSuccess}
          clearTempActiveKey={clearTempActiveKey}
          timeZone={timeZone}
        />
      </Tab>
      <Tab
        key={ExceptionsTabKey}
        title={tabTitle(messages.exceptions)}
        overrides={getTabOverrides(tabOverrides)}
      >
        <CampaignProvider>
          <Exceptions
            onSuccess={handleSuccess}
            clearTempActiveKey={clearTempActiveKey}
            timeZone={timeZone}
          />
        </CampaignProvider>
      </Tab>
    </Tabs>
  )
}
