import { FC, VFC } from 'react'
import { FormattedMessage } from 'react-intl'
import { ErrorPage } from 'ui-components/ErrorPage'
import {
  Body,
  Header,
  ListLoader,
  Root as StyledRoot,
  useEventTypeAndLocation,
} from '../Common'
import { useEventName } from '../providers/EventNameProvider'
import { EngagementTypesProps } from './types'
import {
  Container,
  NoLocationsMessage,
  NoLocationsMessageRoot,
  PageContainer,
} from './styled-components'
import { EngagementTypesLocationView } from './EngagementTypesLocationView'

const NoEngagements = () => {
  const eventType = useEventName().eventType

  return (
    <PageContainer>
      <NoLocationsMessageRoot>
        <NoLocationsMessage>
          {eventType === 'Emeet' ? (
            <FormattedMessage
              defaultMessage="No engagement types to show as you are not mapped to any eMeet yet."
              description="Empty Message"
            />
          ) : (
            <FormattedMessage
              defaultMessage="No engagement types to show as you are not mapped to any campaigns yet."
              description="Empty Message"
            />
          )}
        </NoLocationsMessage>
      </NoLocationsMessageRoot>
    </PageContainer>
  )
}

const Root: FC = ({ children }) => (
  <StyledRoot>
    <Header />
    <Body>{children}</Body>
  </StyledRoot>
)

export const EngagementTypes: VFC<EngagementTypesProps> = props => {
  const { isError, refetch, ...restProps } = useEventTypeAndLocation()

  if (restProps.isLoading && !restProps.locations.length) {
    return (
      <Root>
        <Container>
          <ListLoader />
        </Container>
      </Root>
    )
  }

  if (isError) {
    return (
      <Root>
        <PageContainer>
          <ErrorPage onButtonClick={() => refetch()} />
        </PageContainer>
      </Root>
    )
  }

  return (
    <Root>
      {restProps.locations.length ? (
        <EngagementTypesLocationView {...restProps} {...props} />
      ) : (
        <NoEngagements />
      )}
    </Root>
  )
}
