import { Exception, AvailabilityException, CreateException } from '../types'

export const fromAPI = (data: AvailabilityException) =>
  Object.values(
    data.reduce((acc, curr) => {
      const { date, timeRanges } = curr.attributes
      const type =
        curr.type === 'UserAvailabilityException' ? 'available' : 'unavailable'

      acc[date] = acc[date]
        ? { ...acc[date], [type]: [...acc[date][type], ...timeRanges] }
        : { date, available: [], unavailable: [], [type]: timeRanges }

      return acc
    }, {} as { [date: string]: Exception })
  )

export const toAPI = (data: Exception[]) =>
  data.reduce((acc, curr) => {
    const { date, available, unavailable, inverted } = curr

    available.length &&
      acc.push({
        date,
        type: 'UserAvailabilityException',
        timeRanges: available,
      })

    unavailable.length &&
      acc.push({
        date,
        type: 'UserUnavailabilityException',
        timeRanges: unavailable,
      })

    inverted?.length &&
      acc.push({
        date,
        type: 'InvertedAvailabilityException',
        timeRanges: inverted,
      })

    return acc
  }, [] as CreateException)
