import { useEffect, useMemo, useState, VFC } from 'react'
import { defineMessages, FormattedMessage, useIntl } from 'react-intl'
import { FieldRenderProps } from 'react-final-form'
import { Select, Value } from '@jifflenow/brightui/select'
import { Field } from 'ui-components/Field'
import {
  MOAFieldHelpText,
  MOAFieldLabel,
  MOAFieldRoot,
} from './styled-components'
import { moaOverrides } from './overrides'
import { ModeOfAttendanceProps } from './types'

const messages = defineMessages({
  moaPlaceholder: {
    defaultMessage: 'Select one',
    description: 'Placeholder text',
  },
})

const MOASelect: VFC<
  FieldRenderProps<string, HTMLElement> & ModeOfAttendanceProps
> = ({
  input: { value: fieldValue, onChange, ...restInput },
  in_person_attendance_custom_label,
  virtual_attendance_custom_label,
  isDisabled,
}) => {
  const { formatMessage } = useIntl()
  const [value, setValue] = useState<Value>()

  const options = useMemo(
    () => [
      { id: 'in_person', label: in_person_attendance_custom_label },
      { id: 'virtual', label: virtual_attendance_custom_label },
    ],
    [in_person_attendance_custom_label, virtual_attendance_custom_label]
  )

  useEffect(() => {
    setValue(options.filter(({ id }) => id === fieldValue))
  }, [fieldValue, options])

  const handleOnChange = (value: Value) => {
    setValue(value)
    onChange(value[0].id)
  }

  return (
    <Select
      {...restInput}
      disabled={isDisabled}
      clearable={false}
      placeholder={formatMessage(messages.moaPlaceholder)}
      value={value}
      options={options}
      onChange={({ value }: { value: Value }) => handleOnChange(value)}
      overrides={moaOverrides}
    />
  )
}

export const ModeOfAttendance: VFC<ModeOfAttendanceProps> = rest => (
  <MOAFieldRoot>
    <div>
      <MOAFieldLabel>
        <FormattedMessage
          defaultMessage="Mode of attendance"
          description="Field label"
        />
      </MOAFieldLabel>
      <MOAFieldHelpText>
        <FormattedMessage
          defaultMessage="Change in mode of attendance applies to future meetings only and not to the existing meeting"
          description="Help text"
        />
      </MOAFieldHelpText>
    </div>
    <Field<string>
      name="modeOfAttendance"
      render={props => <MOASelect {...props} {...rest} />}
    />
  </MOAFieldRoot>
)
