import {
  FC,
  useRef,
  createContext,
  useContext,
  useState,
  useEffect,
  useMemo,
} from 'react'
import { ModalSpinner } from '@jifflenow/brightui/modal-spinner'
import { ErrorPage } from 'ui-components/ErrorPage'
import { useUserAvailability } from 'rest'
import { Weekdays } from './Common/weekDayMappings'
import {
  getAvailabilityInTimeZone,
  getAvailabilityInUTC,
} from './Availabilities/transform'
import {
  DaysToIds,
  Availability,
  UTCMinutesAvailability,
  AvailabilityContext,
  AvailabilityProviderProps,
} from './types'

const Context = createContext<AvailabilityContext | undefined>(undefined)

const useAvailabilities = (timeZone: string) => {
  const [isDirty, setIsDirty] = useState(false)
  const [daysToIdMappings, setDaysToIdMappings] = useState<DaysToIds | {}>({})

  const [UTCAvailability, setUTCAvailability] = useState<
    UTCMinutesAvailability[]
  >([])
  const [availability, setAvailability] = useState<Availability[]>([])
  const [defaultAvailability, setDefaultAvailability] = useState<
    Availability[]
  >([])

  const shouldUpdateUTCAvailability = useRef(true)

  const resetAvailability = () => {
    setAvailability(defaultAvailability)
    setIsDirty(false)
    shouldUpdateUTCAvailability.current = false
  }

  useEffect(() => {
    if (shouldUpdateUTCAvailability.current && availability.length) {
      setUTCAvailability(getAvailabilityInUTC(availability, timeZone))
    }

    shouldUpdateUTCAvailability.current = true
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [availability])

  useEffect(() => {
    setIsDirty(
      JSON.stringify(availability) !== JSON.stringify(defaultAvailability)
    )
  }, [availability, defaultAvailability])

  useEffect(() => {
    if (UTCAvailability.length) {
      shouldUpdateUTCAvailability.current = false
      setAvailability(getAvailabilityInTimeZone(UTCAvailability, timeZone))
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [timeZone])

  return {
    isDirty,
    daysToIdMappings,
    setDaysToIdMappings,
    UTCAvailability,
    setUTCAvailability,
    availability,
    setAvailability,
    resetAvailability,
    setDefaultAvailability,
  }
}

export const AvailabilityProvider: FC<AvailabilityProviderProps> = props => {
  const { children, timeZoneId, userId } = props
  const {
    isDirty,
    availability,
    setAvailability,
    UTCAvailability,
    setUTCAvailability,
    daysToIdMappings,
    setDaysToIdMappings,
    resetAvailability,
    setDefaultAvailability,
  } = useAvailabilities(timeZoneId)

  const { isPending, error, reload } = useUserAvailability({
    id: userId,
    onResolve: ({ data }) => {
      const availability = data
        .map(({ attributes }) => ({ ...attributes }))
        .sort((next, prev) => Weekdays[next.day] - Weekdays[prev.day])
      const getAvailabilityInTZ = getAvailabilityInTimeZone(
        availability,
        timeZoneId
      )

      setAvailability(getAvailabilityInTZ)
      setDefaultAvailability(getAvailabilityInTZ)
      setUTCAvailability(availability)

      setDaysToIdMappings(
        data.reduce(
          (acc, curr) => ({ ...acc, [curr.attributes.day]: curr.id }),
          {}
        )
      )
    },
  })

  const value = useMemo<AvailabilityContext>(
    () => ({
      isDirty,
      availability,
      setAvailability,
      UTCAvailability,
      setUTCAvailability,
      daysToIdMappings: daysToIdMappings as DaysToIds,
      resetAvailability,
      setDefaultAvailability,
    }),
    [
      isDirty,
      availability,
      setAvailability,
      UTCAvailability,
      setUTCAvailability,
      daysToIdMappings,
      resetAvailability,
      setDefaultAvailability,
    ]
  )

  if (error) {
    return <ErrorPage onButtonClick={reload} />
  }

  return (
    <>
      {!availability.length && isPending && <ModalSpinner />}
      {availability.length !== 0 && (
        <Context.Provider value={value}>{children}</Context.Provider>
      )}
    </>
  )
}

export const useAvailability = () => {
  const context = useContext(Context) as AvailabilityContext
  if (context === undefined && process.env.NODE_ENV !== 'production') {
    throw new Error(
      'useAvailability must be used within a AvailabilityProvider'
    )
  }

  return context
}
