import { DateTime, DurationObject } from 'luxon'

type ISODateToDateTime = (date: string, zone: string) => DateTime
type ISODateToISODate = (date: string, zone: string) => string

const referenceISODateString = '2024-01-01'

const referenceISODate = `${referenceISODateString}T00:00:00Z`

const referenceDate = DateTime.fromISO(referenceISODate)

export const getISODateString = (date: string) =>
  DateTime.fromISO(date).toISODate()

export const getJSDate = (date: string) => DateTime.fromISO(date).toJSDate()

export const getUTCDateFromMinutes = (plus: DurationObject) =>
  referenceDate.toUTC().plus(plus).toISO()

export const getFullMinutes = (date: string) => {
  const { minute, hour } = DateTime.fromISO(date)

  return hour * 60 + minute
}

export const getUTCFullMinutes = (date: string) => {
  const { minute, hour } = DateTime.fromISO(date).toUTC()

  return hour * 60 + minute
}

export const getDateTimeInTimeZone: ISODateToDateTime = (date, zone) =>
  DateTime.fromISO(date).setZone(zone).setZone('local', { keepLocalTime: true })

export const getDateInTimeZone: ISODateToISODate = (date, zone) =>
  getDateTimeInTimeZone(date, zone).toISO()

export const getUTCDateTimeFromTimeZone: ISODateToDateTime = (date, zone) =>
  DateTime.fromISO(date).setZone(zone, { keepLocalTime: true }).toUTC()

export const getUTCStartDate = (date: string) =>
  DateTime.fromISO(date).toUTC(0, { keepLocalTime: true }).toISO()

export const getPlusDate = (date: string, plus: DurationObject) =>
  DateTime.fromISO(date).plus(plus).toISO()

export const getPlusISOStringDate = (date: string, plus: DurationObject) =>
  DateTime.fromISO(date).plus(plus).toISODate()

export const getStartOfDate = (date: string) =>
  DateTime.fromISO(date).startOf('day').toISO()

export const getDateFromJSDate = (date: Date) =>
  DateTime.fromJSDate(date).toISO()

export const getISODateStringFromJSDate = (date: Date) =>
  DateTime.fromJSDate(date).toISODate()

export const getFormattedTimeFromDate = (date: string) =>
  DateTime.fromISO(date).toFormat('hh:mm a')

export const diffInISODates = (prev: string, next: string) =>
  DateTime.fromISO(next).diff(DateTime.fromISO(prev), 'day')

export const areDatesAdjacent = (prev: string, next: string) =>
  diffInISODates(prev, next).days === 1

export const getISOTimeFromDate = (date: string) =>
  DateTime.fromISO(date).toISOTime()

export const getISOTimeFromJSDate = (date: Date) =>
  DateTime.fromJSDate(date).toISOTime()

export const getISODateStringFromUTCDate = (date: string) =>
  DateTime.fromISO(date).toUTC().toISODate()

export const getStartOfDateFromWeekDay = (weekDay: number) =>
  DateTime.fromISO(referenceISODateString)
    .plus({ day: weekDay - 1 })
    .startOf('day')
    .toISO()

export const getISODateFromWeekDayAndTime = (weekDay: number, time: string) =>
  `${DateTime.fromISO(referenceISODateString)
    .plus({ day: weekDay - 1 })
    .toISODate()}T${time}`

export const getEndDateOfDay = (date: string) =>
  DateTime.fromISO(`${DateTime.fromISO(date).toISODate()}T24:00:00`).toISO()

export const getEndDateOfDayInUTC = (date: string) =>
  `${DateTime.fromISO(date).toISODate()}T24:00:00Z`

export const getRefISODateWithTime = (time: string) =>
  DateTime.fromISO(`${referenceISODateString}T${time}`).toISO()
