import { useState, useMemo } from 'react'
import { useForm, useFormState } from 'react-final-form'
import { Value } from '@jifflenow/brightui/select'
import { useMe } from 'providers/MeProvider'
import { components } from 'types/openapi'
import {
  FormFieldProps,
  SelectFieldProps,
  Validations,
  SourceBasedRules,
  FieldCondition,
} from './types'
import { processRulesOnFieldsMount, resolveFieldRules } from './utils'

type HandleRules = { name: string; value: Value }
type Role = components['schemas']['Role']

const filterFieldsWithRules = (fields: FormFieldProps[]) =>
  fields
    .filter(
      field =>
        field.rules?.length &&
        field.rules[0].source &&
        field.rules[0].conditions.length
    )
    .map(({ name, rules }) => ({
      name,
      source: rules![0].source,
      conditions: rules![0].conditions,
      type: rules![0].type,
    }))

const getSourceBasedRulesObject = (filteredFields: SourceBasedRules[]) =>
  filteredFields.reduce((prev, curr) => {
    prev[curr.source] = prev[curr.source] || []
    prev[curr.source].push(curr)
    return prev
  }, {} as { [key: string]: SourceBasedRules[] })

const getRulesActionObject = ({
  value,
  rulesList,
  sourceBasedRulesObject,
  role,
}: {
  value: string
  rulesList: SourceBasedRules[]
  sourceBasedRulesObject: { [key: string]: SourceBasedRules[] }
  role: Role
}): { [key: string]: FieldCondition } => {
  const hideAllDependentFields = (
    fields: { [key: string]: FieldCondition },
    { name }: SourceBasedRules
  ) => {
    fields[name] = { name, show: false, optionsToShow: [] }
    return fields
  }
  const getNestedDependentFields = (
    fields: SourceBasedRules[],
    field: SourceBasedRules
  ) => {
    let allFields = [...fields, field]
    if (sourceBasedRulesObject[field.name]) {
      allFields = sourceBasedRulesObject[field.name].reduce(
        getNestedDependentFields,
        allFields
      )
    }

    return allFields
  }

  const targetFieldsActionHash = rulesList
    .map(rules => resolveFieldRules({ rules, value, role }))
    .reduce((prev, curr) => ({ ...prev, [curr.name]: curr }), {})

  return Object.keys(targetFieldsActionHash)
    .filter(name => sourceBasedRulesObject[name])
    .flatMap(name => sourceBasedRulesObject[name])
    .reduce(getNestedDependentFields, [])
    .reduce(hideAllDependentFields, targetFieldsActionHash)
}

export const useRules = (formFields: FormFieldProps[]) => {
  // TODO: Set values.customFields type via FormValues generics
  const { values } = useFormState()
  const {
    attributes: { role },
  } = useMe().data

  const initialFields = processRulesOnFieldsMount(
    formFields,
    values.customFields,
    role
  )
  const [fields, setFields] = useState(initialFields)
  const { change, getFieldState } = useForm()

  const sourceBasedRulesObject = useMemo(
    () => getSourceBasedRulesObject(filterFieldsWithRules(formFields)),
    [formFields]
  )

  const handleRules = ({ value, name: sourceName }: HandleRules) => {
    const rulesList = sourceBasedRulesObject[sourceName] || []

    // Return, if there are no rules based-on the current dropdown source
    if (!rulesList.length) {
      return
    }
    // Collect rules action on all the nested dependent fields of the current source
    const rulesActionObject = getRulesActionObject({
      rulesList,
      sourceBasedRulesObject,
      value: value[0]?.id as string,
      role,
    })

    // Filter the previous rendered rule based fields and reset their form values
    formFields
      .filter(
        ({ name }) =>
          rulesActionObject[name] &&
          !rulesActionObject[name].show &&
          getFieldState(name)
      )
      .forEach(({ name }) => change(name, ''))

    // Filter fields to render based on selected value of current source
    const fieldsToShow = formFields
      .filter(({ name }) => {
        if (name === sourceName) {
          return true
        } else if (rulesActionObject[name]) {
          return rulesActionObject[name].show
        } else {
          return fields.findIndex(({ name: fName }) => fName === name) >= 0
        }
      })
      .map(
        (
          field: Omit<SelectFieldProps, 'validations'> & {
            validations?: Validations
          }
        ) => {
          const { options, name } = field
          field.options = rulesActionObject[name]?.optionsToShow || options
          return field
        }
      )

    setFields(fieldsToShow)
  }

  return { fields, handleRules }
}
