import { ChangeEvent, VFC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { Checkbox } from '@jifflenow/brightui/checkbox'
import { CanParticipateProps } from './types'

const aria = defineMessages({
  participateInMeeting: {
    defaultMessage: 'Participate in meeting',
    description: 'Aria label',
  },
})

export const CanParticipate: VFC<CanParticipateProps> = ({
  checked,
  canEdit,
  onChange,
}) => {
  const { formatMessage } = useIntl()

  return (
    <Checkbox
      checked={checked}
      onChange={({ target: { checked } }: ChangeEvent<HTMLInputElement>) =>
        onChange(checked)
      }
      disabled={!canEdit}
      ariaLabel={formatMessage(aria.participateInMeeting)}
    />
  )
}
